// main: ../main.scss
/*
* Component: cheminfo modal
* ----------------------
*/

//chem info    
.cheminfo-modal{
  width: $cheminfo-modal-content-width;
  >.modal{
    .cheminfo-modal-header > .close{
      margin-top: $cheminfo-modal-close-top;
      margin-right: $cheminfo-modal-close-right;
      font-size: $cheminfo-modal-header-btnsize;
    }
    .modal-dialog{
      width: ($content-width - 356px);
    }
    .modal-content{
      padding: 0;
    }
  }
}

//  header 
.cheminfo-modal-header{
  height: 93px;
  background-color: $cheminfo-modal-lightgray;
  border-bottom: 1px solid $cheminfo-modal-bordercolor-b;
  margin: 0;
  border-top-left-radius: $cheminfo-modal-header-radius;
  border-top-right-radius: $cheminfo-modal-header-radius;
  // modal header title
  .cheminfo-add-modal-h1{
    font-size: $cheminfo-modal-header-title;
    color: $brand-gray-dark;
    line-height: ($line-height-base + 3.8);
  }
}

//  body 
.cheminfo-modal-body{
  padding: 15px 15px 6px 25px;
  .cheminfo-scroll-area{
    height: 560px;
    padding: 11px 28px 0 25px;
    text-align: left;
    overflow-y: auto;
    box-sizing:border-box;
  }
  .cheminfo-data-area {
    width:712px;
  }
}

//   (input, select)
// form 
.input-add-box{
  input{
    border:1px solid $list-group-border;
    border-radius: $border-radius-base;
    padding: 7px 13px;
    width:198px;
    font-size: ($font-size-base + 2);
  }
  .cheminfo-add-value{
    width: 284px;
    height:38px;
  }
  .cheminfo-add-info{
    width:100%;
  }
  .cheminfo-add-direct,
  .input-select .input-base,
  .cheminfo-select-input{
    padding: 7px 13px;
    font-size: ($font-size-base + 2);
  }
  .input-select .aList li a{
    font-size: ($font-size-base + 2);
  }
  .input-select{
    width:194px;
    .input-search{
      height:37px\9;
    }
  }
  //.input-select,
  .sco-type2,
  .cheminfo-add-value,
  .fileinput.upload-re{
    margin-right:8px;
  }
  .sco-type2,
  .cheminfo-add-value,
  .fileinput.upload-re{
    float:left;
  }
  .fileinput.upload-re,
  .fileinput-new,
  .fileinput.upload-re2 {
    padding: 3px 3px;
  }
  .fileinput.upload-re{
    width:440px;
  }
  //type  select box  : NMR Spectra
  //.type-select-box,
  .sco-type2,
  .cheminfo-nmr-input{
    width: $cheminfo-modal-type-width;
    height:38px;
  }
  .select-choose-only {
    height:38px;
  }
  .sco-type2 input,
  .cheminfo-nmr-input {
    font-size:14px;
  }
}
// IE10 and IE11
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	.input-add-box .input-select .input-search{
    height:37px;
  }
}
.cheminfo-add-info/*,
.cheminfo-text-area*/{
  width:100%;
}

.result-convert {
  width:100%;
  margin-top:9px;
  padding:10px 14px;
  border-top:1px solid $brand-pattensblue;
  border-bottom:1px solid $brand-pattensblue;
  color:$brand-normaltext;
  @extend .clearfix-block;

  .convert-title {
    float:left;
    margin-right:30px;
    padding-right:30px;
    border-right:1px solid $brand-pattensblue;
  }
  .convert-value {
    float:left;
    max-width:480px;
    overflow:hidden;
  }
}

//unit box(converter) article-date
.unitbox-area{
  margin-top: 11px;
}

//  -    footer 
.cheminfo-modal-footer{
  height: 102px;
  padding: 26px 50px 0 50px;
  border: none;
  .btn-set-area{
    margin-top: 0;
    text-align: right;
    button{
      margin-left: 6px;
    }
  }
}
