// main: ../main.scss
/*
 * Component: Main Header
 * ----------------------
 */
.main-header-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: $navbar-height;
  z-index:$zindex-navbar;
  transition: background 0.3s ease-in-out;
  /// gnb white mode  -- font style
  &.light{
      // background-color: $normal-color;
      background: rgba(256, 256, 256, 1);
      //border-style: solid;
      border-bottom: 1px solid $brand-nav-bordercolor;

    .gm-wrap {
      border-bottom:1px solid $brand-nav-bordercolor;
      .gm-link{
      //  color:#fff;
      color:$brand-normaltext;
        text-decoration: none;
            &:hover,
            &:focus,
            &.focus,
            &:active,
            &.active
            {
              color: $brand-lightblue;
            }
        }
          ul li {
            //border:1px solid red;
            text-decoration: none;
            color:$normal-color;
              &:hover,
              &:focus,
              &.focus,
              &:active,
              &.active
              {
                color: $brand-lightblue;
              }
          }
        }

    .gnb-wrap {
          ul li a {
            text-decoration: none;
            color:$brand-normaltext;
              &:hover,
              &:focus,
              &.focus,
              &:active,
              &.active
              {
                color: $brand-lightblue;
              }
              &.gnb-mail-link {
                color:$brand-lightgray-text;
                  &:hover,
                  &:focus
                  {
                    color: $brand-lightblue;
                  }
              }
              &.btn-brand-orange {
                color: #fff;
                  &:hover,
                  &.focus,
                  &:active
                  {
                    color: #fff;
                  }

              }
          }
      }

    .logo{
      float:left;
      line-height: $gnb-height;
      > a{
            display: block;
            width:170px;
            background-image: url('#{$img-path}/common/main-logo-light-beta.png');
            background-repeat: no-repeat;
            background-position: left center;
            text-indent: -9999px;
            overflow: hidden;
              &,
              &:active,
              &:hover,
              &:focus
              {
                  @include tab-focus();
                  outline: 0;
              }
       }
      //end a
    }//end logo
  }

  /// gnb white mode  -- font style
  &.dark{
      // background-color : transparent;
      background: rgba(256, 256, 256, 0);
    .gm-wrap{
      border-bottom:1px solid $main-top-dark-border-color;
      .gm-link{
        color:$normal-color;
        text-decoration: none;
            &:hover,
            &:focus,
            &.focus,
            &:active,
            &.active
            {
              color: $brand-lightblue;
            }
        }
          ul li  {
            text-decoration: none;
            color:$normal-color;
              &:hover,
              &:focus,
              &.focus,
              &:active,
              &.active
              {
                color: $brand-lightblue;
              }
          }
    }

  .gnb-wrap {
      ul li a {
        text-decoration: none;
        color:$normal-color;
          &:hover,
          &:focus,
          &.focus,
          &:active,
          &.active {
            color: $brand-lightblue;
          }
          &.gnb-mail-link {
            color:$brand-lightgray-text;
            &:hover,
            &:focus{
              color: $brand-lightblue;
            }
          }
          &.btn-brand-orange {
            color: #fff;
              &:hover,
              &.focus,
              &:active{
                color: #fff;
              }
          }
      }

      .user-menu {
        >.user-name > a {
          color:$brand-normaltext;
          &:hover,
          &:focus,
          &:active {
            color: $brand-lightblue;
          }
        }
      }

        .snb-dropmenu {
          > li a {
            text-decoration: none;
            color:$brand-normaltext;
          //border:1px solid red;
              &:hover,
              &:focus,
              &.active{
                color: $brand-lightblue;
              }
              &.gnb-mail-link {
                color:$brand-lightgray-text;
                &:hover,
                &:focus{
                  color: $brand-lightblue;
                }
              }
            }//END A
        }


    }



      .logo{
        float:left;
        line-height: $gnb-height;
        > a{
              display: block;
              width:170px;
              background-image: url('#{$img-path}/common/main-logo-dark-beta.png');
              background-repeat: no-repeat;
              background-position: left center;
              text-indent: -9999px;
              overflow: hidden;
                &,
                &:active,
                &:hover,
                &:focus
                {
                    @include tab-focus();
                    outline: 0;
                }
        }
        //end a
      }//end logo


  }

}


.header-wrap {
  width:$content-width;
  margin:0 auto;
//overflow: hidden;
}


.gm-wrap {
  position: absolute;
  top:0;
  left:0;
  right:0;
}
.gm {
  position:relative;
  margin: 0 auto;
  height:$gm-height;
  line-height: $gm-height;
  width:$content-width;

  a {
    font-size:($font-size-value - 1) * 1px;
  }

  > .text-what {
    position:absolute;
    top:0;
    left:2px;
    width:142px;
    height:41px;
    border-bottom:1px solid $brand-lightblue;
    text-align:center;
  }
  .dropdown{
    z-index:$zindex-dropdown;
  }
    >ul {
      float:right;
      list-style:none;
      //margin-right: 12px;
      >li{
        display: block;
        float: left;
        margin-left: 25px;
        ul {
          padding-top:7px;
          li{
            padding:19px 0;
          }
        }
      }
    }
}

.gnb-wrap {
  margin-top:$gnb-top-margin;
  height:$gnb-height;
  line-height: $gnb-height;
}
.gnb {
    > ul {
      padding-left: 316px;
      height:inherit;
      li {
        list-style: none;
        float: left;
        font-family: $font-sans-semibold;
        font-size: ($font-size-value  + 3) * 1px;
        margin-right: 45px;
      }
    }
}

.snb-wrap {
  float:right;
  //height:$gnb-height - 6;
 line-height: $gnb-height - 6;
}

.befor-snb {
  >li{
    float: right;
    margin-left:15px;
  }
  .btn-snb{
      margin:20px 0 0 0;
  }
}



.after-snb {
  >li{
    float: right;
    &:first-child{
      margin-right: 12px;
      margin-left: 16px;
    }
  }
  .profile-picture-exsmall {
    margin:0 3px 0 8px;
  }
  .user-menu {
    padding:23px 37px 20px;
    >.user-name > a {
      display:block;
      width:162px;
      white-space:nowrap;
      text-overflow:ellipsis;
      overflow:hidden;
    }
  }
  .fa-bell-o{
      font-size:($font-size-h3 - 2);
      vertical-align: middle;
      margin-top:8px;
  }
  .dsc-dropmenu-sm-snbmenu{
    width:238px;
    margin-left:0;
  }
  .chem-dropdown-menu{
    left:auto;
    right:-25px;
  }
  .chem-arrow-menu{
    &:after,
    &:before{
      left:173px;
    }
    &.dsc-dropmenu-sm-snbmenu{
      .snb-dropmenu{
        margin:11px 30px 0;
      }
    }
  }
}


.badge-cust-3 {
  position: absolute;
  display: inline-block;
  margin:14px 0 0 -15px;
  padding: 3px 4px;
  font-size: ( $font-size-small - 1);
  color: #fff;
  line-height: 1;
  white-space: nowrap;
  text-align: center;
  background-color: $brand-lightblue;
  border-radius: 3px;
  z-index: 1;
  }
.badge-cust {
  position: absolute;
  display: inline-block;
  margin:14px 0 0 -11px;
  padding: 3px 4px;
  font-size: ( $font-size-small - 1);
  color: $normal-color;
  line-height: 1;
  min-width:20px;
  max-width:26px;
  min-height:18px;
  white-space: nowrap;
  text-align: center;
  background-color: $brand-lightblue;
  border-radius: 9px;
  z-index:1;
  }

.sign-in-btn{
  line-height: 70px;
}
