// main: ../main.scss

.mypageTabmenu {
  width:100%;
  //background-color:$brand-lightgray;
  margin-bottom:28px;
  padding:0 5px 14px 5px;
  border-bottom:1px solid $panel-compound-border;
.Tabmenu {
  text-align:left;
  > li {
    display: inline-block;
    padding:0 25px;
    border-left:1px solid $panel-compound-border;
    vertical-align: middle;

    &:first-child {
      padding-left:0;
      border-left:0;
    }

    a{
      color:$brand-normaltext;
      display:block;
      line-height:10px;
      &:hover {
        text-decoration: none;
        color: $bookmark-tabmenu-active;
      }
    }//end a
    &.active > a {
      color: $bookmark-tabmenu-active;
      &:hover,
      &:focus,
      &:active {
          text-decoration: none;
        }
      }//end .active
    }//li end
  }//bookmarkTab end-color
}


.bookmark-content,
.discussion-list-content{
  min-height: $bookmark-content-min-height;
  .article-inside{
    clear:both;
    img{
      max-height:200px; //161219
      margin-bottom: 20px; //161220
    }
  }
  .article-title, .article-text{
    @extend .text-wrap-word;
  }
}


// cheminfoSearchResult.html - customizing
.bookmark-cheminfo-tab{
  .cheminfo-search-list-wrap{
    margin-top:0;
    .cheminfo-search-list{
      padding:0;
      border-top:0;
    }
  }

  // cheminfo bookmark page
  .molecule-wrap{
    .molecule-thumb{
      height:200px;
    }
  }
  .table{
    .bookmark-cheminfo-iconline{
      padding:17px 0 7px 30px;
    }
  }
}

// mypage contents
.confirm-msg {
  padding:2px 13px 3px;
  font-size:($font-size-base - 3);
  color:$normal-color;
  vertical-align: middle;
  border:none;
  background-color:$brand-lightrockblue;

  &.pending-msg {
    background-color:$brand-dark-orange;
  }
  &.confirmed-msg {
    background-color:$brand-lightblue;
  }
}

&button.confirm-msg {
  &:hover {
    background-color:$btn-brand-opinion-h;
  }
}

.mypage-contents {
  .write-notice {
    margin-bottom:10px;
  }

  .article-box {
    padding-top:0;
  }
}

.mypage-contents-list {
  > li {
    position:relative;
    padding:32px 26px;
    border-bottom:1px solid $panel-compound-border;
    @extend .clearfix-block;

    &:last-child {
      border-bottom:none;
    }

    .artice-sort-right {
      top:-100px;
    }
    .confirm-msg {
      position:absolute;
      top:32px;
      left:386px;
    }
  }

  .btn-VOD-play {
    top:30px;
    left:65px;
    width:36px;
    height:36px;
    border-width: 2px;
    > i {
      font-size:10px;
      padding-top:11px;
    }
  }
}

.thumb-VOD-mypage {
  float:left;
  width:169px;
  height:96px;
  margin-right:18px;
  > img {
    width:169px;
    height:96px;
  }
}

.thumb-Recreation-mypage {
  float:left;
  width:169px;
  height:133px;
  margin-right:18px;
  > img {
    width:169px;
    height:133px;
  }
}

.VOD-content-mypage {
  float:left;
  width:578px;
  height:96px;
  margin:0;
}

.card-title-VOD-mypage {
  font-size:$font-size-base;
  color:$brand-normaltext;
}

.card-text-VOD-mypage {
  font-size:($font-size-base - 2);
  @include text-overflow-multi(36px, 2);
}

.card-text-Recreation-mypage {
  font-size:($font-size-base - 2);
  @include text-overflow-multi(68px, 4);
}

.VOD-date {
  font-size:($font-size-base - 1);
  color:$brand-gray;
}

.title-delete-VOD {
  padding-left:58px;
  font-size:($font-size-large + 3);
}

.modal-vod-delete {
  padding:75px 0 50px;
  text-align:left;

  .thumb-VOD-mypage,
  .thumb-VOD-mypage>img  {
    width:125px;
    height:70px;
  }

  .thumb-Recreation-mypage,
  .thumb-Recreation-mypage>img  {
    width:90px;
    height:71px;
  }

  .VOD-content-mypage {
    width:370px;
    height:70px;
  }

  .card-title-VOD-mypage {
    margin:6px 0;
  }
}

.vod-delete-content {
  margin-top:-2px;
  padding:15px 48px 15px 58px;
  background-color:$brand-lightergray;
  @extend .clearfix-block;
}

.text-VOD-sure {
  margin:30px 0 45px;
  padding:0 48px 0 58px;
  color:$brand-headertext;
}
