// main: ../main.scss
@charset "UTF-8";


/*  */
@font-face {
    font-family:'feather';
    src: url('#{$feather-font-path}/feather-webfont.eot');
    src: url('#{$feather-font-path}/feather-webfont.eot?#iefix') format('embedded-opentype'),
         url('#{$feather-font-path}/feather-webfont.woff') format('woff'),
         url('#{$feather-font-path}/feather-webfont.ttf') format('truetype'),
         url('#{$feather-font-path}/feather-webfont.svg#feather') format('svg');
    font-weight: normal;
    font-style: normal;
}
/* Character Mapping Method */

[data-icon]:before {
  display: inline-block;
  font-family: "feather";
  content: attr(data-icon);
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* CSS Class Mapping Method */

[class^="icon-"],
[class*=" icon-"] {
  display: inline-block;
  font-family: "feather";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

