// main: ../main.scss


/*
* pages: signup
* ----------------------
*/

.signup-bgcolor{
  background-color:$section-bg;
}

.signup-bgimg{
  left:0;
  right:0;
  background:url('#{$img-path}/membership/signup-bg-complete.png') no-repeat;
  background-size: cover;
}

.signup-noheader{
  display:table-cell;
  width:$content-width;
  padding:20px 0;
}

.only-border1 {
  border-top: 1px solid $list-group-border;
  margin: 28px 0 17px 0;
}

.btn-showhide-right {
  float:right;
  margin:-40px 45px 0 0;
  @include clearfix;
}

.signup-text-agreement {
  font-size : $font-size-brand-small;
  margin: 14px auto 20px 0;
  color:$brand-normaltext;
  line-height: 17px;
}

.signup-text-warning {
  font-size : $font-size-base;
  margin: 14px auto 19px 0;
  color:$brand-normaltext;
  line-height: 17px;
}

.ext-padding-top{
  padding-top:10px;
}

.modal-guide-text {
  font-size:$font-size-base;
  padding:35px 0 0 0;
  color:$brand-lightblue;
}

.modal-body-text {
  padding-bottom:25px;
  line-height: 20px;
  margin-top: 20px;
}

.signup-form-complete {
  margin: 295px auto 0;
}

.complete-title,
.complete-text {
  color:$normal-color;
}

.complete-title{
  font-size: ($font-size-h3 * 2);
  text-transform: uppercase;
}

.complete-text{
  font-family:$font-sans-light;
  font-size: ($font-size-h3 - 2);
}

.btn-signup-complete{
  margin-top:87px;
  color:$brand-normaltext;
  font-size: ($font-size-large - 2);
  width:295px;
  height:50px;
  line-height: 46px;
  border-radius:$border-radius-base;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  &:hover,
  &:focus {
    //color:$brand-normaltext;
    @include box-shadow(none);
  }
}

// email overlap check - btnbox
.overlap-check-btnbox{
  .btn-base{
    padding:0 10px;
  }
}
