// main: ../main.scss

//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------


.btn {
  display: inline-block;
  margin-bottom: 0; // For input.btn
  font-weight: $btn-font-weight;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  @include button-size($padding-base-vertical, $padding-base-horizontal, $font-size-base, $line-height-base, $border-radius-base);
  @include user-select(none);

  &,
  &:active,
  &.hover &.active{
    &:focus,
    &.focus {
      @include tab-focus();
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: $btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
   @include box-shadow(inset 0 0px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: $cursor-disabled;
    pointer-events: none; // Future-proof disabling of clicks
    @include opacity(.65);
    @include box-shadow(none);
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-normal {
    color: $btn-normal-color;
    background-color: $btn-normal-bg;
    //border-color: $btn-normal-border;
    &:hover,
    &.hover {
      color: $btn-normal-color;
      background-color: $btn-normal-activeBg;
      text-decoration: none;
    }
    &:active
    &.active,
    &:focus,
    &.focus {
      color: $btn-normal-color;
    }
}

.btn-lg-structure {
  @include button-size($padding-large-vertical, $padding-structure-horizontal, ($font-size-large - 2), $line-height-large, $border-radius-base);
}

.btn-lg-brand-tsize {
  @include button-size(($padding-large-vertical + 4), $padding-large-horizontal, ($font-size-large - 2), $line-height-large, $border-radius-base);
  &.disabled , &[disabled]{
  pointer-events: none;
  background-color:$brand-text-gray;
  color:$normal-color;
  cursor: default;
  background-image: none;
  opacity:1;
  }
}
.btn-sm-brand-tsize {
  @include button-size($padding-large-vertical, $padding-large-horizontal, $font-size-base, $line-height-large, $border-radius-base);
  &.disabled , &[disabled]{
  pointer-events: none;
  background-color:$brand-text-gray;
  color:$normal-color;
  cursor: default;
  background-image: none;
  opacity:1;
  }
}

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  @include button-size($padding-large-vertical, $padding-large-horizontal, $font-size-large, $line-height-large, $border-radius-large);
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  @include button-size($padding-small-vertical, $padding-small-horizontal, $font-size-small, $line-height-small, $border-radius-small);
}
.btn-xs {
  @include button-size($padding-xs-vertical, $padding-xs-horizontal, $font-size-small, $line-height-small, $border-radius-small);
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}

// Danger and error appear as red
.btn-write {
  @include button-variant($btn-normal-color, $brand-btn-ligtgray, transparent);
  @include button-size($btn-dsc-vpadding, $btn-dsc-hpadding, ($font-size-base - 1), 13px, $border-radius-base);
}

.form-btn-close {
    display: block;
    background: $btn-gray;
    color: $btn-primary-color;
    border: none;
    border-radius: $border-radius-base;
    width: 25px;
    height: 24px !important;
    line-height: 22px !important;
    padding: 0px !important;
    margin-top: 0px !important;
    text-shadow: 0 1px 0 #fff;
}

.btn-article-close{
  position: absolute;
  width: 25px;
  height: 24px !important;
  line-height: 22px !important;
  padding: 0px !important;
  color : #fff;
  text-shadow: 0 1px 0 #fff;
  //& span{ font-size: 24px; }
}
.btn-rt{
  right: 15px;
  margin-top: 0px !important;
  & span{
    font-size: 24px;
  }
}

.btn-base{
  border-radius: $border-radius-base;
  padding: 0 20px;
  height: $myp-component-height;
  line-height: ($myp-component-height - 2);
  font-size: ($font-size-base - 1);
  display: inline-block;
  border:none;
  color: $btn-normal-color;
  cursor: pointer;
  &:hover, &:active{
    color: $btn-normal-color;
  }
  &:disabled{
    color:rgba(256, 256, 256, 0.5);
    pointer-events: none; // Future-proof disabling of clicks
  }
}
