.btn-add-data{
  padding-left:0;
  font-family:$font-sans-semibold;
  color: $btn-normal-color;
  border:1px solid $btn-add-data;
  background: $btn-add-data;
  overflow:hidden;

  .icon-add-data {
    display:inline-block;
    line-height: $myp-component-height;
    margin:0 20px 0 0;
    padding:0 11px;
    @include border-left-radius($border-radius-base);
    background: $btn-add-data-icon;
  }

  &:hover,
  &:active,
  &:focus,
  &.hover,
  &.active{
    border:1px solid $btn-add-data-h;
    background: $btn-add-data-h;
    color: $btn-normal-color;
    .icon-add-data {
        background: $btn-add-data-icon-h;
    }
  }


}
.btn-detail{
  color: $brand-lightblue;
  background: #fff;
  border:1px solid $brand-lightblue;
  &:hover,
  &:focus,
  &:active,
  &.active{
    border:1px solid $brand-lightblue;
    background: $brand-lightblue;
    color: $btn-normal-color;
  }
  &:disabled{
    color:$brand-lightblue;
    pointer-events: none; // Future-proof disabling of clicks
  }
}
.btn-showhide{
