  height: 26px;
  label{
    &:before,
    &:after{
      color: $valid-input-color;
      background: $btn-showhide-bg;
      padding: 0px;
    }
  }
}
.btn-following{
  width: 80px;
  // height: 30px;
  // border: 1px solid $brand-lightblue;
  label{
//     border-radius: $border-radius-base;
//     cursor:pointer;
    &:before{
      color:$btn-normal-color;
      background: $brand-lightblue;
    }
    &:after{
      color: $brand-lightblue;
      background: #fff;
//       padding: 0px;
    }
  }
}
.toggle-btn{
  //background: green;
  // border-radius: $border-radius-base;
  display:block;
//   text-align:center;
  position:relative;
  height: 30px;
//   overflow: hidden;
  label{
//     border-radius: $border-radius-base;
//     cursor:pointer;
//     height: 100%;
//     display:block;
    &:before,
    &:after{
      border: 1px solid $brand-lightblue;
      border-radius: $border-radius-base;
      width: 100%;
      height: 30px;
      line-height: 28px;
      font-size: ($font-size-base - 1);
      display: block;
      position: absolute;
      top:0;
      left: 0;
      text-align: center;
    }
    &:before{
    content:attr(data-on);
    }
    &:after{
      content:attr(data-off);
      //padding: 0px;
    }
  }
  input:checked + label:before{
    z-index:1;
  }
  input:checked + label:after{
    z-index:0;
  }
  input {
    width:100%;
    height:100%;
    margin:0 !important;
//     padding:0 0;
    position:absolute;
    top:0;
//     right:0;
//     bottom:0;
    left:0;
    z-index:2;
    cursor:pointer;
    opacity:0;
    filter:alpha(opacity=0);
  }
}

