// main: ../main.scss

.discussion-main-search{
  margin:-20px 0 65px 0;
  height:$dsc-search-height;
  .inner-main-search{
    position: absolute;
    left:0;
    right:0;
    background:url('#{$img-path}/forum/forum-search-bgimg.jpg') no-repeat ;
    background-size: cover;
    height:($dsc-search-height - 20);
  }
}

.discussion-main-content{
    @include clearfix();
    width:$content-width;
    margin:0 auto;
    clear:both;
}

//discussion adide
.dsc-rnb{
  float:right;
  width: $dsc-rnb-width;

  > .title-topic {
    margin-top:0;
    padding-bottom:18px;
    border-bottom:1px solid $panel-compound-border;
  }
}

.featured-user-name {
  display: block;
  margin-top:7px;
  font-size:($font-size-base - 1);
  color:$brand-lightrockblue;
  overflow: hidden;
  white-space: nowrap;
}

.side-title-text{
  font-family: $font-sans-semibold;
  font-size: ($font-size-base + 2);
  color: $brand-normaltext;
  padding-bottom:23px;
  border-bottom:1px solid $panel-compound-border;
}

.side-opinion-list{
  margin-top:32px;
  li{
    float:left;
    margin-bottom:25px;
    > a {
      &:hover,
      &:focus,
      &:active {
        .interest-user-info p{
          color:$brand-lightblue;
        }
      }
    }
  }
  .side-profile-picture{
    float:left;
  }
  .interest-user-info{
    float:left;
    width:236px;
    p{
      font-family: $font-family-sans;
      color:$brand-normaltext;
      max-height:60px;
      overflow: hidden;
      line-height:$line-height-base;
      @extend .text-wrap-word;
    }
    .inter-user-name{
      display: block;
      margin-top:7px;
      font-size:($font-size-base - 1);
      color:$brand-lightrockblue;
      overflow: hidden;
      white-space: nowrap;
    }
    .opinion-bubble{
      display:block;
      margin-top:5px;
      font-size:($font-size-base - 1);
      color:$forum-side-opinion-bubble-textcollor;
      i{
        margin-right:5px;
      }
    }
  }
}

.interest-category-showbox{
  display:block;
  width:84px;
  padding:6px 0;
  margin-bottom:7px;
  text-align:center;
  vertical-align: middle;
  background-color: $forum-side-interest-boxcolor;
  font-family: $font-sans-semibold;
  font-size:($font-size-base - 3);
  color:$forum-side-interest-textcolor;
}

.dsc-contents{
  width:$dsc-con-w;
  float: left;

  .discussion-header{
    padding:0 10px 0 0;
    float: left;
    width:100%;
    margin-bottom: 20px;

    .dsc-header-ddmenu{
      float:left;
    }
    .dsc-header-rmenu{
      float:right;
      .icon-cross{
        border-radius: ($border-radius-base - 1 );
        font-size: ($font-size-base + 2);
        line-height:13px;
        height: 20px;
        color:$brand-btn-ligtgray;
        border:2px solid $brand-btn-ligtgray;
        margin-right: 5px;
      }
      .icon-square-cross{
        font-size:($font-size-base + 5);
        margin-right:2px;
      }
    }
    .dsc-checkbox-group{
      float: left;
    }
    .dsc-btn-reset{
      color:$brand-btn-ligtgray;
      &:hover,
      &:focus,
      &:active {
        color:$brand-normaltext;
        text-decoration: none;
        outline:0;
      }
    }
  }//discussion-header end-color
  .dsc-user-info{
    clear:both;
    position: relative;
    margin-bottom: 12px;
    background-color:$brand-lightblue;
    color:$normal-color;
    text-align:left;
    padding:28px 30px 29px 30px;
    border-radius:$border-radius-base;
    .user-photo {
      border-radius: $user-img-radius;
      margin-right: $user-img-Rmargin;
    }
    p{
      display: inline-block;
      font-size:$font-size-large;
      vertical-align: middle;
      .title-dsc-write {
        color:$normal-color;
        &:hover,
        &:focus,
        &:active {
          color:$brand-normaltext;
        }
      }
    }
    img{
      border:none;
    }
    span{
      position: absolute;
      top:27px;
      right:30px;
      a{
        color:$form-error;
      }
    }
    .profile-picture-medium{
      float:none;
    }
  }
  .sub-box{
    float: left;
    width:$myp-con-w;
    background-color: $normal-color;
    border:solid 1px $article-border-color;
    border-radius: $border-radius-base;
    margin-bottom:10px;
    padding:52px 47px 40px 47px;
    text-align: left;
  }
}

//discussion write
.discussion-sub-content{
  @include clearfix;
  margin:0 auto;
  //border:1px solid red;
  width:$dsc-subContent-width;
  clear:both;
}


.write-notice{
  padding:20px 28px;
  border:1px solid $list-group-border;
  border-radius:$border-radius-base;
  font-size:($font-size-base - 1 );
  line-height: 17px;
  margin-bottom:29px;
  span {
    color:$brand-lightblue;
  }
}
.modify-marginT{
  margin-top:35px;
}

.discussion-write,.discussion-modify{
  > h1 {
    font-size: $font-size-h1;
    text-align: center;
    color:$brand-normaltext;
    margin:40px 0 20px 0 ;
  }
  .close{
    position: relative;
    top:0;
    font-size:($font-size-large + 1);
  }

  .off{
    opacity: 0;
    display:none;
    transition: opacity 0.5s linear;
    -webkit-transition: opacity 0.5s linear;
    -moz-transition: opacity 0.5s linear;
    -o-transition: opacity 0.5s linear;
    -ms-transition: opacity 0.5s linear;
  }

  .write-user-info{
    .user-photo{
      float: left;
      border-radius: $user-img-radius;
      margin-right: $user-img-Rmargin;
    }
    p{
      height:40px;
      line-height: 40px;
      color:$brand-lightblue;
    }
  }

  .editor-content{
    clear:both;
    position: relative;
    top:$dsc-contents-margintop;
    .editTitle{
      width:100%;
      display: block;
      border: none;
      background-color: transparent;
      font-size:32px;
    }
    .editArea{
      width:100%;
      min-height: 300px;
      font-size:$font-size-large;
      color:$brand-normaltext;

      textarea{
        width:100%;
        resize:none;
        overflow-y: auto;
        line-height:24px;
        @include placeholder($brand-text-gray);
        &:focus::-webkit-input-placeholder { // WebKit browsers
          color:transparent;
        }

        &:focus:-moz-placeholder { // Mozilla Firefox 4 to 18
          color:transparent;
        }

        &:focus::-moz-placeholder { // Mozilla Firefox 19+
          color:transparent;
        }

        &:focus:-ms-input-placeholder { // Internet Explorer 10+
          color:transparent;
        }
      }
      div[contenteditable=true] {
        width:100%;
        line-height: 24px;
        //min-height: 50px;
        padding:0 0 25px 0;
        &:empty:before {
          content: attr(placeholder);
          display:block;
          color: $brand-text-gray;
        }
      }
      .bd-none {
        color: $brand-normaltext; // $brand-text-gray;
        border:none;
      }
    }
  }
}//end discussion-write

.editTitle{
  color:$gnb-menu-color;
  &::-moz-placeholder {font-family:$font-sans-semibold; }
  &:-ms-input-placeholder { font-family:$font-sans-semibold; } // Internet Explorer 10+
  &::-webkit-input-placeholder  { font-family:$font-sans-semibold; } // Safari and Chrome
}
.write-footer{
  padding:25px 0;
  color:$article-btn-article-seemore;
  position: relative;
  > div:first-of-type {
    border-top:1px solid $dsc-borderColor;
  }
  > div:last-of-type {
    margin-top:21px;
  }
  .border-none {
    border-top:0 !important;
  }
  .radiogroup {
    padding:17px 0 12px 0;
    line-height:30px;
    border-bottom:1px solid $dsc-borderColor;
    >p{
       float:left;
       display:inline-block;
       margin-right: 45px;
       color:$brand-normaltext;
    }
    input[type=radio]{
      display:none;
    }
    label{
      font: inherit;
      margin-right:18px;
    }
    input[type=radio]+label::before{
        background-clip:content-box;
        border:1px solid $list-group-border;
        border-radius:50%;
        content:"";
        display:inline-block;
        height:16px;
        margin:7px 4px 0 0;
        padding:4px;
        vertical-align:top;
        width:16px;
    }
    input[type=radio]:checked + label::before{
        background-color:$brand-lightblue;
    }
    input[type=radio]:checked + label{
        color:$brand-lightblue;
    }
  } //end radiogroup

  button{
    float:right;
  }

  .desc-topic-type {
    margin:10px 0 10px 195px;
    font-size:($font-size-base - 1);
    line-height:18px;
  }
}//end footer

.editor-sidemenu{
  width:35px;
  margin-left: -45px;
  text-align: center;
  float:left;
  .editor-btn-sidemenu{
    border-radius: 50%;
    background-color:$brand-lightblue;
    border:none;
    width:34px;
    height:34px;
    color:$normal-color;
    padding-bottom: 4px;
    display: block;
    > i{
      margin-top:6px;
    }
  }

  ul > li > a {
    display: block;
    width:34px;
    height:34px;
    border:1px solid $brand-btn-ligtgray;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    margin-top: 10px;
    padding-bottom: 5px;
    text-decoration: none;
    color:$brand-btn-ligtgray;
    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
      color:$brand-normaltext;
      border:1px solid $brand-normaltext;
    }
    > i{
      vertical-align: middle;
    }
  }
}// end deitor-sidemenu

.dsc-write-wrapImg {
  position: relative;
  overflow: hidden;
  margin-bottom: $dsc-write-PaddingBottom;
  &:before,
  &:after{
    content: '';
    clear:both;
  }
}
.dsc-write-img{
  position: relative;
  float:left;
  left:0;
  top: 0;

}
.img-delete {
  position: absolute;
  top:7px;
  right:7px;
}
.dsc-write-wrapDoc {
  position: relative;
  margin-bottom: $dsc-write-PaddingBottom;
    &:before,
    &:after{
      content: '';
      clear:both;
    }
}
.file-upload{
  margin-bottom: 20px;
  img {
    margin-right: 5px;
  }
  p{
    display: inline-block;
    color: $brand-normaltext;
    font-size:($font-size-base + 4);
    font-family:$font-sans-semibold;
    vertical-align: middle;
    a{
      color: inherit;
      font-size: inherit;
    }

  }
  a {
    float: right;
    font-size:$font-size-h3;
    color:$brand-download-ic;

  }
  a + a{
    margin-right: 26px;
  }

  .file-icon-name-box{
    display:inline-block;
    padding:11px 20px 11px 13px;
    background: $dsc-write-doc-backColor;
    border-radius:$border-radius-small;
  }
  button {
    position:relative;
    display: inline-block;
    right:auto;
  }
}
.dsc-write-doc{
  overflow: hidden;
  left:0;
  top: 0;
  padding: 20px 30px 20px 40px;
  margin: 0;
}

.dsc-write-form {
  margin-bottom:0;
  .font-semibold{
    font-family:$font-sans-semibold;
  }
  dt{
    &:first-child{
     margin-top: 0;
    }
    color: $brand-text-gray;
    font-weight: normal;
    font-size:($font-size-base - 1 );
    margin-top: 13px;
    margin-bottom: 5px;
  }
  dd{
    color:$gnb-menu-color;
    hr.dsc-view-hr{
      height: $dsc-view-hrHeignt;
      background: $dsc-borderColor;
      padding:0;
      margin: 15px 0 0 0;
      border:none;
    }
  }
  .res-position {
    //font-family:$font-sans-semibold;
    input,textarea {
      width: 100%;
      font-family:$font-family-sans;
    }
    .doc-modity-bd{
      font-family:$font-sans-semibold;
      font-size: ($font-size-base - 1);
      color: $gnb-menu-color;
      border-radius:$border-radius-base;
      border: 1px solid $list-group-border;
    }
  }
  .datepicker{
    font-size:($font-size-base - 1);
    line-height: 13px;
  }
  .fa{
    margin-left: -18px;
    margin-right: 5px;
    cursor: pointer;
    padding: 2px;
    color: $brand-text-gray;
  }
}

.doc-delete {
  position: absolute;
  top:0;
  right:-36px;
}

// .dsc-detail-comment-view,
.dsc-detail-opinion-view{
  padding:  0;
  margin-bottom: 10px;
  .dsc-view-header
    {
      float: left;
      width:100%;
      padding: 0;
    }
}
.dsc-view-content{
  clear:both;
  //overflow: hidden;
  position: relative;
  top:$dsc-contents-margintop;

  p{
    color:$brand-normaltext ;
    @extend .text-wrap-word;
    a {
      &:hover,
      &:focus,
      &:active {
