    margin-top: 30px;
    .file-icon-name-box{
      img + p {
        margin-top:0;
      }
    }
  }
  .article-btngroup{
    margin:0;
  }
  hr{
      height: $dsc-view-hrHeignt;
      background: $dsc-borderColor;
