.write-opinion-wrap{
  clear:both;
  width:100%;
  bottom: 0;
  height: $dsc-write-bar-height;
  .write-op-bar{
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    background-color: $brand-lightblue;
    height: inherit;
    margin-top:20px;

   .write-op-inner{
    margin: 0 auto;
    width: $dsc-subContent-width;
    line-height: ($dsc-write-bar-height - 5);
      .user-photo {
        border-radius: $user-img-radius;
        margin-right: $user-img-Rmargin;
      }
