//forum modal
.forum-modal{
  margin-top:28px;
  margin-bottom: 23px;
  color:$profile-lnb-hover;
  font-size: ($font-size-base - 1);
  line-height: 15px;
  margin-left:-47px;
  text-align: center;
  .btn-follow{
    width:280px;
    float: left;
    border-right:1px solid $myp-border;
    &:last-child{
      width:230px;
      border:none;
    }
    a,
    a:hover{
      text-decoration:none;
    }
    span{
      font-size: $font-size-large;
      font-family: $font-sans-semibold;
    }
  }
  .modal{
    .modal-content{
      width:628px;
      padding: 0;
      border-radius: $border-radius-large;
    }
    .modal-header{
      background-color: $brand-normaltext;
      // height: 93px;
      border-radius: $border-radius-base $border-radius-base 0 0;
      color: $follow-modal-header;
      font-family: $font-sans-semibold;
      border-bottom: 1px solid $list-group-border;
      margin:0;
      padding: 30px 0 16px;
      .modal-title-h1{
        line-height: 19px;
        text-align: center;
        text-indent: 0;
        //padding-top:47px;
        font-size:($font-size-base + 1);
        color:$normal-color;
      }
      .follower-num {
        font-family:$font-sans-light;
        font-size: ($font-size-large + 1);
      }
      button{
        margin-top:-75px;
        margin-right:0;
      }
    }
    .modal-filename-title{
      background:#fff;
      padding:22px 0 24px;
      .file-title-box {
        padding:11px 18px;
        display: inline-block;
        border: 3px;
        img {
          margin-right: 5px;
        }
        > p{
          display: inline-block;
          color: $brand-normaltext ;
          font-size:($font-size-base + 4) ;
          font-family:$font-sans-semibold;
          vertical-align: middle;
          a{
            color: inherit;
            font-size: inherit;
          }
        }
      }
      a {
        display: inline-block;
        margin-left:20px;
        font-size:$font-size-h3;
        color:$brand-download-ic;

      }
      a + a{
        margin-right: 26px;
      }
    }
    .modal-body{
      padding: 0 50px 0 50px;
      border-top:1px solid $list-group-border;
      border-radius:0 0 $border-radius-base $border-radius-base;
      //-webkit-margin-right: 5px;
      &:hover{
        overflow-y:auto;
      }
      height:420px;
      overflow-y: auto;
      //margin-right: 5px;
      background:$brand-moreligtgray;
      .dsc-write-doc{
        background: $brand-moreligtgray;
        border:0;
        padding:5px 0 13px;
        .dsc-write-form{
          text-align:left;
          .res-position{
              font-family: $font-sans-semibold;
              font-size:($font-size-base + 3) ;
              line-height: 22px;
          }
          .fileinput {
            padding:9px 10px;
          }
          .fileinput-filename {
            font-size:($font-size-base + 3) ;
          }
          dt {
            margin-top:20px;
          }
          dd {
            line-height: 20px;
            font-family: $font-family-sans;
            font-size:($font-size-base + 1);
            color:$gnb-menu-color;
          }
        }
      }
    }
  }
  // s- addfile upload modal
  .addfile-modal-tap{
    background-color:$brand-normaltext;
    .modal-header{
      padding:16px 0 0 0;
      height:47px;
      background-color:$brand-normaltext;
      button{
        margin-top:-61px;
      }
      .nav-tabs{
        padding-left:15px;
        background:$brand-normaltext;
        border:0;
        li{
          a{
            padding:13px 30px 14px 30px;
            font-size:($font-size-base + 1);
            color:$normal-color;
            margin-right:5px;
            &:hover {
              color:$brand-normaltext;
              background:$normal-color;
            }
          }
          &.active{
           a{
            font-size:($font-size-base + 1);
            color:$brand-normaltext;
            }
          }
        }
      }
    }
    .modal-body{
      padding:0;
      width:100%;
      height:565px;
      background-color:$body-bg;
      border-radius:0 0 $border-radius-base $border-radius-base;
      border:0;
      overflow: hidden;
      .upload-file-secondbox{
        height:350px;
        overflow: hidden;
      }
      .upload-file-all {
        height:auto;
        overflow:visible;
      }
    }
    .modal-body-VOD {
      height:auto;
    }
    .photo-modal-body{
      height:auto;
      border-radius:0;
    }
    .modal-footer{
      border:0;
    }

    .myfile-select-page{
      .modal-footer{
        border-top: 1px solid $list-group-border;
      }
    }

  }
  // e- addfile upload modal

  .modify-height{
    .modal-filename-title{
      padding:22px 0 24px;
    }
    .modal-body{
      height:335px;
      padding:0 50px 0 50px;
      overflow-y: hidden;
      &:hover{
        overflow-y: hidden;
