
// [converter] $parent hack
@mixin bg-variant($parent, $color) {
  #{$parent} {
    background-color: $color;
  }
  a#{$parent}:hover,
  a#{$parent}:focus {
    background-color: darken($color, 10%);
  }
}

@mixin page-visual($height, $padding-top, $bgimg) {
  height:$height;
  margin:-20px 0 0 0;

  .inner-visual-bg {
    position:absolute;
    left:0;
    right:0;
    height:inherit;
    padding-top:$padding-top;
    background:url('#{$bgimg}') no-repeat;
    background-size:cover;
  }

  .inner-width {
    width:$content-width;
    margin:0 auto;
    color:$normal-color;

    .inner-title {
      font-family:$font-montserrat-ExLight;
      font-size:$font-size-visual-title;
      line-height:67px;
      text-align:center;
    }

    .inner-description {
      font-family:$font-sans-light;
      font-size:$font-inner-description;
      font-style:italic;
      text-align:center;
    }
  }
}
