// main: ../main.scss
/*
* Component: mypage - common
* ----------------------
*/
.mypage-article{
  display: block;
  margin: 0 auto;
  margin-top:33px;
  @include clearfix();
  .info-interests{
    .editable-field > a,
    .editable-field
    > a{
      text-decoration: none ;
      color:$edit-field-color;
    }
  }
}
//mypage-lnb
.myp-lnb{
  float:left;
  width:292px;
  background: $myp-lnb-bg;
  border:solid 1px $myp-border;
  border-radius: $border-radius-base;
  padding-bottom: 40px;
}
.profile-lnb{
  color:$brand-normaltext;
  text-align: center;

  .profile-photo{
    position: relative;
    background:url('#{$img-path}/img-profile-defult.gif') no-repeat;
    width:100%;
    height:$myp-lnb-w;
    overflow: hidden;
    > img{
      //height:auto;
      height:100%;
      width:100%;
    }
    &:hover .update-profile-photo{
      display: block;
      bottom: 0;
    }
    &:hover .del-profile-img {
      display: block;
    }
  }
}
//update-profile-photo
.update-profile-photo{
  display: none;
  position: absolute;
  width: 100%;
  bottom : -40px;
  background-color: rgba(0,0,0,0.7);
  color: $myp-update-profile-color;
//   cursor: text;
  line-height: $update-profile-photo-height;
  font-size: $font-size-small;
  transition: bottom 150ms ease-in 0ms;
  .icon-camera{
    font-size: $font-size-h3;
    transform: scale(0.87);
    margin-right: 5px;
  }
  &:focus{
    color: $myp-update-profile-color;
  }
  .chooseupload{
    position: absolute;
    right : 0;
    top: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    cursor: pointer;
  }
}

.del-profile-img {
  display:none;
  position:absolute;
  top: 6px;
  right: 6px;
  width:25px;
  height:25px;
  line-height:22px;
  background: #3c454c;
  border-radius: 2px;
  .icon-cross {
    color: #fff;
  }
}

.user-connect{
  width: 100%;
  height: 49px;
  margin: 0;
  overflow:hidden;
  > .btn-connect{
    width: 50%;
    float: left;
    background-color: $brand-lightblue;
    border:none;
    height: inherit;
    line-height: 49px;
    color: $btn-connect-color;
    font-family: $font-sans-semibold;
    font-size: $font-size-base;
    &:first-child{
      box-shadow: inset -1px 0 1px $btn-connect-first-shadow;
    }
    &:last-child{
      box-shadow: inset 1px 0 1px $btn-connect-last-shadow;
    }
  }
  .btn-follow{
    label{
      cursor:pointer;
      &:before,
      &:after{
        border-radius: 0;
        height: inherit;
        line-height: inherit;
        background: $brand-lightblue;
        box-shadow: inset -1px 0 1px $btn-connect-first-shadow;
        font-size:$font-size-base;
      }
    }
  }
}

.profile-name{
  line-height: 90px;
  padding-top: 20px;
  color:$brand-normaltext;
  font-family: $font-sans-semibold;
  font-size: $font-size-large;
  border-bottom: 1px solid $brand-nav-bordercolor;
}

.profile-summary {
  width:252px;
  margin:0 auto;
  padding: 35px 0 38px;
  color:$brand-normaltext;
  font-family: $font-sans-semibold;
  font-size: $font-size-large;
  border-bottom: 1px solid $brand-nav-bordercolor;
  .name {
    @extend .text-wrap;
  }
  .email {
    color: #888e93; //     .
    font-size: 12px;
    font-family: open_sansregular,Arial,sans-serif;
    margin: 5px 0 0 0;
    word-wrap: break-word;
    word-break: break-all;
  }
}

//file upload style
.fileinput{
  border: 1px solid $btn-brand-gray-light-h;
  border-radius: $border-radius-base;
  padding: 7px 10px;
  background-color: $fileinput-bg;
  .fileinput-new{
    vertical-align: middle;
    color: $brand-text-gray;
  }
  &.fileinput-new{
    .fileinput-exists{
      display:none;
    }
  }
  &.fileinput-exists{
    .fileinput-new{
      display: none;
    }
    .fileinput-exists{
      display:block;
    }
  }

  .btn-file{
    position: relative;
    overflow: hidden;
    vertical-align: middle;
    > input{
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      filter: alpha(opacity=0);
    }
  }
  .btn{
    @extend .btn-brand-gray-light;
    width: 112px;
    height: 30px;
    margin-right: 3px;
    font-size: ($font-size-base - 1);
    color: $btn-add-data-h;
    text-shadow: none;
  }
  .fileinput-filename{
    display: inline-block;
    overflow: hidden;
    color: $brand-normaltext;
    vertical-align: middle;
    max-width:385px;
    white-space: nowrap;
    word-break:break-all;
    text-overflow:ellipsis;
  }
  &.upload-re{
    .fileinput-filename{
      max-width:290px;
    }
  }
  &.upload-re2{
    .fileinput-filename{
      max-width:561px;
    }
  }

  .close{
    margin-top:1px;
    .icon-cross{
      font-size: ($font-size-base + 2);
    }
  }
}
.upload-write{
  .fileinput-filename{
    max-width:362px;
  }
}
.myfile-upload{
  .fileinput-filename{
    max-width:610px;
  }
}
.profile-lnb-menu{
  padding: 21px 0 0 0;
  width:100%;
  a {
    width:252px;
    margin: 0 auto;
    line-height: 47px;
    font-family: $font-primary;
    color:$brand-normaltext;
    text-decoration: none;
    font-size:($font-size-base + 1);
    display: block;
    //border-bottom: 1px solid $brand-nav-bordercolor;
    &:hover{
      color:$profile-lnb-hover;
    }
    &.active{
      color:$profile-lnb-hover;
      font-family: $font-sans-semibold;
    }
    &.update-profile:after{
      content: '';
      position:absolute;
      width:5px;
      height: 5px;
      background:$noti-after-color;
      border-radius: 50%;
      margin-top:20px;
      margin-left:6px;
    }
  }
}
// my-page profile
.myp-contents{
  width:$myp-con-w;
  float: right;
  min-height: $mypage-content-min-height;

  .user-profile{
    padding-top:2px;
    padding-bottom: 28px !important;
      h1{
        font-family: $font-sans-semibold;
        font-size: ($font-size-base * 2);
        margin: 0;
        height:36px;
      }
      h1.editable-field,
      h1.editable-field-summary{
        float:left;
        left:-5px;
      }
      h1.editable-field > span {
        display: inline-block;
        max-width: inherit;
        padding-bottom:5px;
        white-space: nowrap;
        overflow: hidden;
      }
      .my-country.active-f{
        z-index: auto;
        width: 255px;
       }
      .my-city.active-f{
        z-index: auto;
        width:379px;
      }
      .my-location{
        width: 100%;
        font-size: 14px;
        display: inline-block;
        margin-left:-3px;
        padding-top:2px;
        height:23px;       .my-city .myp-edit-input{
          padding:5px;
        }
        .my-country.active-f{
          width: 360px;
         }
      }
      .my-introduce{
        margin-top:27px;
        display: block;
        position: relative;
        dt{
          font-size: ($font-size-base - 1);
          color: $profile-head-color;
          font-family: $font-sans-light;
          width:110px;
          float: left;
          line-height: 22px;
          position: absolute;
        }
        dd{
          font-size: $font-size-large;
          font-family: $font-primary;
          color: $brand-normaltext;
          width: 100%;
          padding-left: 106px;
          line-height: $font-size-large;
          margin-bottom: 19px;
          &:first-child{
            font-family: $font-sans-semibold;
          }
          &:nth-of-type(1),
          &:nth-of-type(2){
            height:24px;
          }
          &:nth-of-type(3),
          &:nth-of-type(4){
            height:22px;
          }
          a{
            color: $brand-normaltext;
            //text-decoration: underline;
            font-size: $font-size-base;
          }
          .select-box {
            > input {
              font-size: ($font-size-large - 2);
              width:100%;
              max-height:32px;
              padding:7px 13px;
            }
            .fa{
              top:10px;
            }
          }
          &.info-interests{
            font-size: $font-size-base;
            .editable-field,
            .editable-field-summary{
              cursor: pointer;
            }
          }
        }
      }
    }

//sub  
  .sub-box{
    float: left;
    margin-bottom:10px;
    padding:44px 47px 40px 47px;
    width:$myp-con-w;
    text-align: left;
    @extend %box-common;
    //color: $brand-normaltext;
    &.tech-keyword {
      .sub-title {
        font-size:13px;
        color:#95a1aa;
        margin: 0 0 9px 0;
      }
      .additional {
        margin:40px 0 9px 0;
      }
    }
  }
}
.myp-contents h2, .box-payment h2{
  margin-bottom: 25px;
  font-size: $mypage-h2-size;
  font-family: $mypage-h2-font;
  color: $mypage-color;
  line-height: $mypage-h2-size;
}
//follow box
.follow-box{
  margin:28px 0 23px;
  color:$profile-lnb-hover;
  font-size: ($font-size-base - 1);
  line-height: 15px;
  margin-left:-47px;
  text-align: center;
  .btn-follow{
    width:280px;
    float: left;
    border-right:1px solid $myp-border;
    &:last-child{
      width:230px;
      border:none;
    }
    a,
    a:hover{
      text-decoration:none;
    }
    span{
      font-size: $font-size-large;
      font-family: $font-sans-semibold;
      margin-right: 5px;
    }
  }
}
.follow-modal-wrap.modal{
    .modal-content{
      width:628px;
      padding: 0;
      border-radius: $border-radius-base;
    }
    .modal-body{
      padding: 5px 50px;
      height: 380px;
      width: 620px;
      &:hover{
        overflow-y: auto;
      }
      overflow: hidden;
    }

}
//follow box popup
.follow-group{
//  margin-top: 6px;
  .follow-list{
    position: relative;
    padding: 23px 0 23px 0;
    min-height: 90px;
    border-bottom: 1px solid $list-group-border;
    text-align: left;
    //height: 87px;
    @media  (max-height:70px){
      margin-bottom:46px;
    }
    &:last-child{
      border-bottom:none;
    }
    img.user-image{
      width: 48px;
      height: 48px;
      float: left;
      margin-right:16px;
    }
    h1{
      font-size:($font-size-base + 2);
      font-family: $font-sans-semibold;
      color: $brand-normaltext;
      padding:0;
      padding-top: 5px;
      line-height: 23px;
      & span{
        font-size: $font-size-small;
        font-family: $font-primary;
        color: lighten($brand-normaltext, 40%);
        & i{
          margin-left: 5px;
        }
      }
    }
    .follow-text{
      padding-right:70px;
      font-size: ($font-size-base - 1);
      color: $brand-normaltext;
    }
    .toggle-btn-following{
      position:absolute;
      left:435px;
      top:50%;
      margin-top: -15px;
      //margin-top:-30px;
    }
  }
}
.self-intro textarea,
.self-intro-write textarea{
  height: 110px !important;
  padding: 10px !important;
}

.self-intro-write {
  .btn-edit-box{
    margin-top:($btn-editox-margin-top - 5) ;
  }
}
// .self-intro form{height: 170px;}
//   \\
.editable-field-summary{
  width:100%;
}
.editable-field{
  max-width: 100%;
  min-height:23px;
  display: inline-block;
  position: relative;
  cursor: text;
  padding:3px;
  &:hover{
    box-shadow:0 0 0 1px $overlay-color;
    z-index: 2;
    .overlay-icon{
      opacity: 1;
      z-index:auto;
    }
  }
  &.active-f{
    .overlay-icon{
      display: none;
    }
    width: 100%;
    &:hover,
    &:focus{
      box-shadow: 0 0 0 0  !important;
    }
  }
  &.my-research.active-f{
    z-index: auto;
    width: 350px;
  }
}
.editable-field-summary{
  .btn-edit-box{
    margin-top:19px;
  }
}

.other-profile{
  .editable-field,
  .editable-field-summary{
    &:hover,
    &:focus{
      box-shadow: 0 0 0 0  !important;
    }
  }
  .my-location,
  .my-introduce{
    .my-country,
    .my-city{
      padding:3px 0 3px 3px;
    }
  }
  .my-research{
    padding-left:3px;
  }
  .my-introduce{
    dd{
      > div{
        padding:3px;
      }
      &:nth-of-type(3){
        font-size:$font-size-base;
        color: $brand-normaltext;
      }
    }

  }
  .my-introduce dd.info-interests .editable-field,
  .my-introduce dd.info-interests .editable-field-summary{
    cursor: text !important;
  }
}
.my-introduce{
  .input-select,
  .editable-field,
  .editable-field-summary {
    .select-box{
      max-width:250px;
    }
  }
  .editable-field,
  .editable-field-summary{
    .select-box{
      margin-top:-1px;
    }
  }
}
.overlay-icon{
  position: absolute;
  top: 0;
  right: -23px;
  width: 23px;
  height: 100%;
  opacity: 0;
  z-index: -1;
  transition: opacity 150ms ease-in 0ms;
  background-color: $overlay-color;
  box-shadow:0 0 0 1px $overlay-color;
  display: inline-block;
  cursor: pointer;
  line-height: 100%;
  vertical-align: middle;
}

.overlay-icon:before{
  font-family: 'Glyphicons Halflings';
  content: '';
  display: block;
  width: 23px;
  background: url('#{$img-path}/icon/ico-edit.jpg') no-repeat 1px center;
  color:$profile-overlay-icon;
  margin-left: 3px;
  font-size: ($font-size-base + 2);
  height: 100%;
  line-height: 100%;
  vertical-align: middle;
}
.myp-form-edit{
  width: 100%;
  position: absolute;
  top: -1px;
  left: -1px;
  opacity: 0;
  z-index: 1;
  filter: alpha(opacity=0);
  .sr-only{
    display:none;
  }
  .myp-edit-input{
    min-width: 50%;
    width: 100%;
    height: inherit;
    padding: 0 4px;
    font-size: inherit;
  }
}

textarea.myp-edit-input{
  height: inherit;
}

.myp-form-edit-view{
  opacity: 100 !important;
  filter:alpha(opacity=100) !important;
}
