// main: ../main.scss
/*
* Component: proleaders - common
* ----------------------
*/

//proleaders main
//search

.search-box-proleaders{
  margin:-20px 0 0 0;
  .inner-search-box{
    background:url('#{$img-path}/proleaders/proleaders-search-bg.jpg') center no-repeat;
    background-size: cover;
  }
  + .tab-content,
  + .dsc-contents-full{
    padding-top:60px;
  }
  .search-desc{
    margin-top:23px;
    text-align: center;
  }
  .search-desc-title,
  .search-desc-text{
    color:$normal-color;
  }
  .background-desc{
    margin-top:95px;
    text-align: center;
    span{
      display:block;
      line-height: 48px;
    }
    .search-desc-title{
      font-family: $font-montserrat-ExLight;
      font-size: $font-size-h1 + 9;
      text-transform: uppercase;
    }
    .search-desc-text{
      font-family: $font-sans-light;
      font-size: $font-size-h3 - 1;
      font-style: italic;
    }
  }
}

.main-article-cheminfo{
  .dsc-contents-full{
    background-color: $normal-color;
    .expert-area{
      width:$content-width;
      @include center-block();
      padding:5px 0 60px;
    }
  }
}
.dsc-contents-full {
  .discussion-header{
    margin-bottom:36px;
    @include clearfix();
    .dsc-header-ddmenu{
      float:left;
    }
    .dsc-header-rmenu{
      float:right;
    }
    .check-box-content{
      display:inline-block;
    }
  }
}
.research-wrap{
  width:$content-width;
  padding:63px 65px;
  margin-bottom:10px;
  @include center-block();
  @extend %box-common;
  .article-content{
    //word-break:break-all;
    color: $brand-normaltext;
    //font-size: $font-size-large;
    line-height: 26px;
    //@extend .text-wrap-word;
  }
  .tab-sub-title{
    margin-bottom:25px;
    font-family: $font-sans-semibold;
    font-size: $font-size-large;
    color: $brand-normaltext;
  }
  .user-keyword {
    margin:16px 0 0 0;
    li {
      display:inline-block;
      height:31px;
      line-height:29px;
      text-align:center;
      padding:0 18px 0 22px;
      font-size:14px;
      color:#3c454c;
      border-radius: 4px;
      border:1px solid #d2dae0;
      margin:0 10px 10px 0;
      &.selected {
        border-color:#ffdb95;
        background: #ffdb95;
      }
    }
  }
}
.proleaders-dropmenu-Large{
  margin-left:10px;
  padding:($dsc-dropmenu-padVertical + 12) $dsc-dropmenu-padHorizontal ($dsc-dropmenu-padVertical + 8) ($dsc-dropmenu-padHorizontal + 19);
  width:190px;
  font-size:$font-size-base;
  >ul li{
    border:none;
    &:not(:first-child){
      margin-top:15px;
    }
  }
}

// main
.proleaders-info{
  background:url('#{$img-path}/proleaders/proleaders-list-bg.jpg') no-repeat;
  color:$normal-color;
  text-align: center;
  .card-title{
    margin-top:40px;
    font-family: $font-sans-semibold;
    font-size:($font-size-h3 + 1);
  }
  .card-text{
    margin-top:14px;
    font-size:($font-size-large + 1);
  }
  .btn-prol{
    margin-top:25px;
  }
}

//expert detail
.discussion-header{
  padding-top:14px;
  @include clearfix();
  .desc-info,
  .link-similar{
    font-style: italic;
    text-decoration: underline;
  }
  .desc-info{
    float:left;
    color: $brand-normaltext;
    font-size: ($font-size-large - 1); //~17px
    .fa-ra{
      margin-right:10px;
      color:$brand-lightblue;
    }
  }
  .link-similar{
    float:right;
    color:$btn-gray;
    font-size:$font-size-base;
    .fa-angle-right{
      margin-left:17px;
      font-size: $font-size-large;
      vertical-align: middle;
    }
  }
}
.expert-profile{
  .user-name,
  .user-affi{
    color:$brand-normaltext;
  }
  .author-header{
    //margin-bottom:44px;
    @include clearfix();
    .author-img{
      float:left;
      margin-top:6px;
      margin-right:23px;
    }
    .user-name{
      float:left;
      font-family:$font-sans-semibold;
      font-size:$font-size-h2 + 2;
    }
  }
  .author-cont{
    .user-affi{
      margin-top:43px;
    }
  }
  .user-affi,
  .user-url > a{
    font-size: ($font-size-large - 1); //~17px
  }
  .user-url{
    margin-top:6px;
    > a {
      &:hover,
      &:focus,
      &:active {
        text-decoration:underline;
      }
    }
  }
}
.proleaders-content{
  //margin-top:60px;
  .tabpanel{
    @include clearfix();
    margin-bottom:20px;
    height: 69px;
  }
  .tab-title{
    width:$content-width;
    margin:25px auto;
    color:$normal-color;
    font-size:$font-size-base + 2;
    font-family: $font-sans-semibold;
    .icon-paper{
      margin:-15px 10px 0 0;
      font-size:$font-size-base + 6;
    }
  }
}

.proleaders-info-modal{
  position:relative;
  .modal{
    .modal-header{
      margin:0;
    }
    .close{
      margin-top: -107px;
      margin-right: -51px;
    }
    .modal-dialog{
      width:$content-width;
    }
    .modal-content{
      padding:50px 50px;
    }
    .modal-header .artice-sort-right{
      margin-top: 9px;
      margin-right: -18px;
    }.modal-body{
      margin-bottom:50px;
      max-height:568px;
      overflow: hidden;
    }
  }
  .modal-footer{
    padding:0;
    border:none;
    > a{
      position: absolute;
      left:0;
      width:100%;
      height:50px;
      background-color: $btn-card;
      display: inline-block;
      padding-top:5px;
      color:$dsc-gray;
      font-size:$font-size-h3 ;
      text-align: center;
      @include border-bottom-radius($border-radius-large);
      &:hover,
      &:focus,
      &:active{
        background-color: $brand-lightblue;
        color:$normal-color;
      }
    }
  }
}
.similar-box{
  .modal-header{
    .close{
      margin-top: -100px;
      margin-right: -5px;
    }
  }
  .modal-dialog{
    overflow-y: initial !important;
  }
  .modal-content{
    width:628px;
    padding: 0;
    border-radius: $border-radius-base;
  }
  .modal-body{
    padding: 5px 50px;
    height: 380px;
    width: 620px;
    &:hover{
      overflow-y:auto;
    }
    overflow: hidden;
  }
  .follow-list{
    min-height:90px;
    .btn-detail{
      position: absolute;
      left: 435px;
      top:50%;
      margin-top:-15px;
    }
  }
}
.proleadersinfo-group{
  text-align: left;
}
.modal-expert-profile{
  @include clearfix();
  font-size: ($font-size-base + 1);
  color:$brand-normaltext;
  display: table;
  .card-profile,
  .card-profile-cont{
    display:table-cell;
    vertical-align: top;
  }
  .card-profile{
    float:left;
    width:42px;
    margin-right:20px;
  }
  .card-profile-cont{
    padding-right:57px;
    width:100%;
    .user-name{
      font-family: $font-sans-semibold;
      font-size: ($font-size-h3 + 1);
      span{
        margin:-4px 10px 0 0;
      }
    }
    .user-affi{
      margin-top:13px;
      line-height: 19px;
    }
    .user-url{
      margin-top:21px;
    }
    .user-major{
      margin-top:17px;
      font-style: italic;
      text-decoration: underline;
    }
    .user-keyword {
      margin:16px 0 0 0;
      li {
        display:inline-block;
        height:22px;
        line-height:20px;
        text-align:center;
        padding:0 7px;
        font-size:11px;
        color:#3c454c;
        border-radius: 4px;
        border:1px solid #d2dae0;
        margin:0 0 4px 0;
        &.selected {
          border-color:#ffdb95;
          background: #ffdb95;
        }
      }
    }
    .modal-proleadersinfo-conetent{
      margin-top:36px;
      @extend .text-wrap;
    }
    .modal-conetent-title{
      font-family: $font-sans-semibold;
      font-size: ($font-size-base + 2);
      &:not(:first-child){
        margin-top:25px;
      }
    }
    .modal-conetent-text{
      line-height: 19px
    }
  }
}
.radiopanel{
  display: none;
  margin-top:10px;
  .write-notice{
    float:left;
    width :100%;
    padding:20px 29px 20px 28px;
  }
  .upload-write .upload-input{
    padding:14px 13px 15px;
  }
}
 .radiogroup input:nth-of-type(1):checked ~ .radiopanel:nth-of-type(1),
 .radiogroup input:nth-of-type(2):checked ~ .radiopanel:nth-of-type(2),
 .radiogroup input:nth-of-type(3):checked ~ .radiopanel:nth-of-type(3),
 .radiogroup input:nth-of-type(4):checked ~ .radiopanel:nth-of-type(4),
 .radiogroup input:nth-of-type(5):checked ~ .radiopanel:nth-of-type(5){
   display: block;
 }
.complete-box{
  //float:none;
  margin-bottom:33px;
  width :100%;
  @extend .box-normal;
  @include clearfix();
  .complete-img{
    float:left;
    margin:32px 71px;
  }
  .complete-desc-area{
    margin-left:371px;
    padding:47px 45px 38px 39px;
    height:290px;
    background-color:$brand-lightblue;
    color:$normal-color;
    @include border-right-radius($border-radius-base);
    .complete-desc-title,
    .complete-desc-text{
      text-align: center;
    }
    .complete-desc-title{
      font-family: $font-sans-semibold;
      font-size:$font-size-h3;
    }
    .complete-desc-text{
      margin-top:16px;
      font-size:$font-size-base + 2;
      line-height: 22px;
    }
  }
  .upload-write,
  .enter-input{
    width:552px;
    @include center-block();
  }
  .upload-write{
    margin-top:40px;
    button{
      position: absolute;
      margin-top: 0;
      top: 10px;
      right: 10px;
    }
  }
  .enter-input{
    margin-top:52px;
  }
}
.complete-link{
  .enter-input{
    .close{
      position: absolute;
      right: 12px;
      top: 15px;
      font-size: 16px;
    }
  }
}
.proleaders-dsc{
  color: $btn-gray;
  text-align: center;
  .proleaders-dsc-title{
    font-family: $font-sans-semibold;
    font-size:$font-size-h2 + 2;
  }
  .proleaders-dsc-text{
    line-height: 22px;
    > p{
      margin-top:44px;
      &:first-child{
        margin-top:40px;
      }
    }
  }
  .btn-edit-box{
    margin-top:60px;
    text-align: center;
  }
}
.btn-edit-box.btn-right{
  button{
    float:right;
  }
}
.recommend-box{
  padding:83px 120px;
  h2,
  .title-dsc{
    text-align: center;
  }
  h2{
    font-family: $font-sans-semibold;
    font-size:$font-size-h3;
    color: $btn-gray;

  }
  .title-dsc{
    margin-top:12px;
    font-size:$font-size-base + 2;
    color:$brand-gray;
    line-height:22px;
  }
  .recommend-write{
    margin:57px auto 0 auto;
    padding-top:52px;
    width:552px;
    border-top: 1px solid $list-group-border;
    dt{
      margin-bottom: 18px;
      font-size:$font-size-base + 2;
      color:$btn-gray;
      text-align: center;
    }
    dd:not(:first-child){
      margin-top:10px;
      &.write-textarea{
        margin-top: 66px;
      }
    }
    input,
    textarea{
      width:100%;
    }
  }
}
.myp-contents-full,
.myp-contents-full .sub-box{
  width:$content-width;
}
.res-career-view{
  &.res-career-view-show{
    display: block;
  }
}
.myp-contents-full{
  float:none;
  @include center-block();
  .res-career-write .res-location input:last-child{
    width:808px;
  }
  .tech-res-preview{
    h2{
      margin-bottom:5px;
    }
    .tech-res-list-view{
      margin-top:0;
    }
  }
}

.user-profile-preview{
  padding-top:2px;
  padding-bottom: 28px !important;
  h1{
    display: inline-block;
    font-family: $font-sans-semibold;
    font-size: ($font-size-base * 2);
    &:not(:first-child){
      margin-left: 5px;
    }
  }
  .my-location{
    font-size: 14px;
    padding-top:2px;
  }
  .my-introduce{
    display: block;
    position: relative;
    margin-top:27px;
    dt{
      float: left;
      position: absolute;
      font-size: ($font-size-base - 1);
      color: $profile-head-color;
      font-family: $font-sans-light;
      line-height: 22px;
    }
    dd{
      margin-bottom: 19px;
      padding-left: 106px;
      font-size: $font-size-large;
      font-family: $font-primary;
      color: $brand-normaltext;
      line-height: $font-size-large;
      a{
        color: $brand-normaltext;
        text-decoration: underline;
        font-size: $font-size-base;
      }
    }
  }
}

// button
.btn-prol{
  @include button-variant($btn-normal-color, $brand-lightblue, transparent);
  @include button-size(($btn-dsc-vpadding+7), $btn-dsc-hpadding, $font-size-large, 13px, 0);
}
.btn-follow-area{
  margin-top:44px;
  @include clearfix();
  .btn-brand-white,
  .btn-following,
  .btn-message{
    float:left;
  }
  .btn-following{
    width:98px;
  }
  .btn-message{
    margin-left:11px;
    padding:0 21px
  }
  .btn-view{
    float:right;
    padding:0 21px
  }
}
.btn-send-box{
  margin-top:16px;
  text-align: right;
  button{
    display: inline-block;
    + button{
      margin-left:6px;
    }
  }
  .btn-edit,
  .btn-preview,
  .btn-savedraft{
    float:left;
  }
  .btn-savedraft{
    margin-left:10px;
  }
}

//form
.input-category{
  display: table;
  width:100%;
  &.enter-input{
    .input-base{
      max-height:49px;
    }
  }
  .input-select,
  .input-text-search{
    display: table-cell;
    vertical-align: middle;
  }
  .input-select{
    width:235px;
  }
  .input-select-search{
   position: relative;
    .input-base{
     background-color: $btn-card;
      @include border-right-radius(0);
    }
  }
  .select-box{
    .input-base{
      max-height: 47px;
    }
    &.open-select{
      @include border-right-radius(0);
    }
  }
  .input-text-search{
     position: relative;
    .input-base{
      border-left:none;
      @include border-left-radius(0);
      &:focus{
       margin-left: -1px;
       border-left:1px solid $brand-lightblue;
      }
    }
  }
}
.tab-content .editable-field.my-research.active-f{
  width:700px;
}

.radiopanel .myp-contents .user-profile .my-city.active-f{
  width:500px;
}

.tech-keyword {
  width:1170px;
  float:left;
  margin-bottom: 10px;
  padding: 44px 47px 40px 28px;
  border:1px solid #e1e1e1;
  border-radius: 4px;
  background: #fff;
  h2 {
    margin-bottom: 25px;
    font-size:18px;
    color:#3c454c;
    line-height: 18px;
    font-family: open_sanssemibold,"Microsoft YaHei",Arial,sans-serif;
  }
  .sub-title {
    font-size:13px;
    color:#95a1aa;
    margin: 0 0 9px 0;
  }
  .additional {
    margin:40px 0 9px 0;
  }
}

// paid 
.proleaders-keyword {
  margin-top:15px;
  font-size:($font-size-base - 1);
  word-break: keep-all;
  @include overflow-line(3);
  height:56px;
}
.box-payment {
  margin-bottom:40px;
  padding:44px 47px 40px 47px;
  @extend %box-common;

  > h2 {
    margin-bottom:10px;
  }

  .txt-final-selling {
    > strong {
      font-family:$font-primary;
      font-size:($font-size-base + 2);
      > em {
        color:$brand-normaltext;
      }
    }
  }
}

.proleaders-paid {
  position:relative;
  height:80px;
  overflow:hidden;

  > .lay-paid {
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:78px;
    background:url('#{$img-path}/proleaders/bg_payment.png') repeat-x 0 0;
    z-index:2;
  }

  > .btn-payment {
    position:absolute;
    top:20px;
    left:45%;
    z-index:3;
    border:0;
    background-color:transparent;
    font-size:($font-size-base - 1);
    color:$btn-add-data-icon;
  }
}

.bg_comingsoon {
  position:fixed;
  width:100%;
  height:100%;
  top:0;
  left:0;
  background-color:rgba(0,0,0,0.5);
  z-index:1;
}

.wrap_comingsoon {
  position:absolute;
  top:20%;
  left:50%;
  width:696px;
  margin-left:-348px;
  z-index:3;
}

.box_comingsoon {
  width:550px;
  background-color:#12a0c4;

  > h1 {
    margin:0 45px;
    padding:60px 0 28px 0;
    color:#fff;
    font-family:$font-montserrat-regular;
    text-align:center;
    border-bottom:1px solid #99d6e6;
  }
  > p {
    margin:0 45px;
    padding:25px 0 48px 0;
    font-family:$font-sans-semibold;
    color:#fff;
    font-size:($font-size-base + 1);
    text-align:center;
    line-height:25px;
  }
}

.list_comingsoon {
  list-style:decimal;
  padding:18px 50px 60px 55px;
  background-color:#fff;
  color:#057895;
  font-size:($font-size-base - 1);

  > li {
    margin-top:25px;
  }
}

.bg_man {
  position:absolute;
  bottom:0;
  right:0;
  width:190px;
  height:502px;
  background:url('/assets/images/proleaders/img_man.png') no-repeat 0 0;
}
