 @mixin datepicker-button-variant($color, $background, $border) {
  color: $color;
  background-color: $background;
  border-color: $border;

  &:focus,
  &:active,
  &.active {
    color: $color;
    background-color: $background;//darken($background, 8%);
    border-color: darken($border, 12%);
  }

  &:hover{
    color: $color;
    background-color: darken($background, 8%);
    border-color: darken($border, 12%);
  }
}

.datepicker {
  border-radius: 4px;
  direction: ltr;
  &.datepicker-inline {
    width: 220px;
  }
  &.datepicker-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: $zindex-navbar;
    display: none;
    float: left;
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: 14px;
    list-style: none;
    background-color: $input-bg;
    border: 1px solid $input-border;
    border: 1px solid rgba(0,0,0,0.15);
    border-radius: 4px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,0.175);
    box-shadow: 0 6px 12px rgba(0,0,0,0.175);
    background-clip: padding-box;
  }
  &.datepicker-days {
    display: block;
  }
  &.datepicker-months {
    display: block;
  }
  &.datepicker-years {
    display: block;
  }
  table{
    margin: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    tr {
      td,
      th {
        text-align: center;
        width: 30px;
        height: 30px;
        border-radius: 4px;
        border: none;
      }
    }
  }
  // Inline display inside a table presents some problems with
  // border and background colors.
  .table-striped & table tr {
    td,
    th {
      background-color:transparent;
    }
  }
  table tr td {
    &.day:hover,
    &.day.focused {
      background: $gray-lighter;
      cursor: pointer;
    }
    &.old,
    &.new {
      color: $push-text-sm; //161031 
    }
    &.disabled,
    &.disabled:hover {
      background: none;
      color: $btn-link-disabled-color;
      cursor: default;
    }
    &.today,
    &.today:hover,
    &.today.disabled,
    &.today.disabled:hover {
      $today-bg: $brand-info;
      @include datepicker-button-variant($gray-dark, $today-bg, darken($today-bg, 20%));
    }
    &.today:hover:hover { // Thank bootstrap 2.0 for this selector...
      // TODO : Bump min BS to 2.1, use $textColor in buttonBackground above
      color: $datepicker-today-hover;
    }
    &.today.active:hover {
      color: $input-bg;
    }
    &.range,
    &.range:hover,
    &.range.disabled,
    &.range.disabled:hover {
      background:$gray-lighter;
      border-radius: 0;
    }
    &.range.today,
    &.range.today:hover,
    &.range.today.disabled,
    &.range.today.disabled:hover {
      $today-bg: $brand-info;
      @include datepicker-button-variant($datepicker-today-hover, $today-bg, darken($today-bg, 20%));
      border-radius: 0;
    }
    &.selected,
    &.selected:hover,
    &.selected.disabled,
    &.selected.disabled:hover {
      @include datepicker-button-variant($input-bg, $gray-light, $gray);
      text-shadow: 0 -1px 0 rgba(0,0,0,.25);
    }
    &.active,
    &.active:hover,
    &.active.disabled,
    &.active.disabled:hover {
      @include datepicker-button-variant($input-bg, $brand-primary, $brand-primary);
      text-shadow: 0 -1px 0 rgba(0,0,0,.25);
    }
    span {
      display: block;
      width: 23%;
      height: 54px;
      line-height: 54px;
      float: left;
      margin: 1%;
      cursor: pointer;
      border-radius: 4px;
      &:hover {
        background: $gray-lighter;
      }
      &.disabled,
      &.disabled:hover {
        background: none;
        color: $btn-link-disabled-color;
        cursor: default;
      }
      &.active,
      &.active:hover,
      &.active.disabled,
      &.active.disabled:hover {
        @include datepicker-button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border);
        text-shadow: 0 -1px 0 rgba(0,0,0,.25);
      }
      &.old,
      &.new {
        color: $btn-link-disabled-color;
      }
    }
  }

  th.datepicker-switch { width: 145px; }

  thead tr:first-child th,
  tfoot tr th {
    cursor: pointer;
    color: $brand-primary;
    &:hover{
      background: $gray-lighter;
    }
  }

  // Basic styling for calendar-week cells
  .cw {
    font-size: 10px;
    width: 12px;
    padding: 0 2px 0 5px;
    vertical-align: middle;
  }
  thead tr:first-child th.cw {
    cursor: default;
    background-color: transparent;
  }
}
.input-group {
  &.date {
    .input-group-addon i {
      cursor: pointer;
      width: 16px;
      height: 16px;
    }
  }
}
.input-daterange {
  input {
    text-align:left;
  }
  .input-group-addon {
    width: auto;
    min-width: 16px;
    padding: 4px 5px;
    font-weight: normal;
    line-height: $line-height-base;
    text-align: center;
    text-shadow: 0 1px 0 $input-text-shadow;
    vertical-align: middle;
    background-color: $input-group-addon-bg;
    border: solid $input-group-addon-border-color;
    border-width: 1px 0;
    margin-left:-5px;
    margin-right:-5px;
  }
}
