.chem-arrow-menu:after,
.chem-arrow-menu:before {
  bottom: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.chem-arrow-menu:after {
  border-color: rgba(255, 255, 255, 0);
	border-bottom-color: #ffffff;
	border-width: 9px;
	margin-left: -9px;
}
.chem-arrow-menu:before {
	border-color: rgba(213, 220, 226, 0);
	border-bottom-color: #d5dce2;
	border-width: 10px;
	margin-left: -10px;
}
