// main: ../main.scss

/*
* Component: Side Notification
* ----------------------
*/
.modal-lnb button.lnb-head-left{
  left:636px ;
}//position of the close btn
.modal-lnb button.lnb-head-left-close{
  left:288px;
}//position of the close btn

.lnb {
  top: 0;
  left:0;
}
/*lnb title area*/
.lnb-title{
  display: block;
  padding:40px 38px 32px;
  font-family:$font-sans-light;
  line-height: 28px;
}
.lnb-notification{
  width: $lnb-head-width;
  background: $lnb-bg;
  min-height: 100%;
  position: absolute;
  // z-index: $lnb-z-index-out;
  .lnb-title{
    border-bottom: 1px solid rgba(256,256,256,0.28);
    font-size:24px;
    color:$lnb-menu-title;
    line-height: 22px;
  }
}
.lnb-menu{
  padding:12px 0 0 0;
  font-size:$font-size-base;
  li{
    list-style: none;
    display:block;
    margin-bottom:7px;
    a{
      padding:10px 35px 9px 40px;
      display:block;
      text-decoration: none;
      font-family:$font-family-sans;
      color: $lnb-menu-text ;
      list-style: none;
      .count{
        float: right;
      }
      &:hover{
          // font-family:$font-sans-semibold;
          color:$lnb-menu-text-ov;
        }
    }
  }
  .lnbbtn-focus{
    background:$lnb-btn-bg;
    font-family:$font-sans-semibold;
    color:$lnb-menu-text-ov;
  }
}
// lnb sub-area
.lnb-sub {
  width: 350px;
  background: $lnb-submenu-bg;
  min-height: 100%;
  position: absolute;
  left:$lnb-head-width;
  display: block;
  .lnb-title{
    border-bottom: 1px solid $list-group-border;
    background-color: $lnb-sub-head-bg;
    font-size:16px;
    color:$brand-modal-title;
    line-height: 12px;
    margin:0;
    padding-top:50px;
    padding-left:35px;
    font-family:$font-sans-semibold;
  }
}


.lnb-sub-list{
  position: absolute;
  width:100%;
  padding:0 0 0 0;
  overflow-y: auto;
  li{
    position: relative;
    list-style: none;
    display:block;
    overflow: hidden;
    font-size: 13px;
    border-bottom:1px solid $lnb-border;
    img.user-image{
      width: 37px;
      height: 37px;
      margin-right:10px;
      margin-top:2px;
      float:left;
    }
  }
}
.lnb-sub-list li a{
  padding:16px 33px 20px;
  display:block;
  text-decoration: none;
  list-style: none;
  color:$brand-modal-title;
  cursor:pointer;
  transition:all 0.4s ease-out;
  .count{
    float: right;
  }
  &:hover {
    background:$lnb-bg-ov;
  }
}
// background list color
.lnb-bg-myfourm,
.lnb-bg-following,
.lnb-bg-cheminfo{
  &:before{
    content: '';
    position: absolute;
    width:5px;
    height: 100%;
  }
}
.lnb-bg-myfourm:before{
  background: $lnb-bg-myfourm;
}
.lnb-bg-following:before{
  background: $lnb-bg-following;
}
.lnb-bg-cheminfo:before{
  background: $lnb-bg-cheminfo;
}
.select-lnb{
  &:before{
    content: "";
    position:absolute;
    width:6px;
    height: 6px;
    background:$noti-after-color;
    border-radius: 50%;
    margin-top:5px;
    right:21px;
  }
}
.lnb-sub-text{
  //white-space: nowrap;
  overflow: hidden;
  margin:0;
  .lnb-time{
    color: $lnb-time;
    font-size:11px;
    font-family:$font-family-sans;
    margin-top:6px;
    display: block;
  }
  strong{
    color:$brand-lightblue;
    font-weight: normal;
    display: block;
    &.long-name{
      display: block;
      overflow: hidden;
    }
  }
  span{
    display: block;
  }
  .icon-profile-crown{
    display: inline-block;
  }
}
.btn-viewall-wrap{
  width:100%;
  padding:10px;
  position: absolute;
  bottom:0;
  background-color: $normal-color;
  .btn-desc{
    font-size: ($font-size-base - 1);
    text-align: center;
    padding:0 0 8px ;
  }
}
.btn-viewall{
  width:330px;
  height: 50px;
  padding-top:15px;
  font-size: ($font-size-base + 2);
}
.lnb-sub-wrap{
  display: none;
}
.lnb-slide-in{
  transition:all 0.4s ease-out;
  @include opacity(1);
  @include translate(0, 0);
  z-index: $lnb-z-index-in;
}
.lnb-slide-out{
  transition:all 0.4s ease-out;
  @include opacity(0);
  @include translate(-0.5%, 0);
  z-index: $lnb-z-index-out;
}
