// main: ../main.scss
/*
* footer landing page
* ----------------------
*/
//about
.about-header {
  height:724px;
  margin:-125px 0 0 0;
}

.about-header-bg {
  position:absolute;
  left:0;
  right:0;
  height:inherit;
  padding-top:327px;
  background:url('#{$img-path}/footer/about-bg.jpg') no-repeat;
  background-size:cover;
}

.about-inner-width {
  margin:0 auto;
  width:$content-width;
}

.about-head-title {
  width:680px;
  margin:0 auto;
  line-height:62px;
  font-size:ceil(($font-size-base * 3.71));
  font-family:$font-montserrat-regular;
  color:$normal-color;
  text-align:center;
  letter-spacing:1px;
}

.about-copy {
  padding:92px 0 7px;
  background-color:$body-bg;
  @extend .clearfix-block;
}

.about-copy-list {
  >li {
    float:left;
    width:350px;
    margin:0 30px 57px 30px;
    text-align:center;

    &:nth-child(1) {
      margin:0 40px 57px 20px;
    }

    &:nth-child(3) {
      width:290px;
      margin:0 0 57px 60px;
    }
    &:nth-child(4) {
      margin-left:215px;
    }
    &:nth-child(5) {
      margin-left:45px;
    }
  }
  >li >span {
    display:inline-block;
    width:60px;
    height:56px;
    background:url('#{$img-path}/footer/about-copy-bg.png') no-repeat;

    &.copy-bg1 {
      background-position:0 0;
    }
    &.copy-bg2 {
      background-position:0 -80px;
    }
    &.copy-bg3 {
      background-position:0 -160px;
    }
    &.copy-bg4 {
      background-position:0 -240px;
    }
    &.copy-bg5 {
      background-position:0 -320px;
    }
  }
}

.about-list-title {
  margin-top:10px;
  color:$brand-normaltext;
  font-family:$font-sans-semibold;
  font-size:($font-size-h4 + 1);
  line-height:28px;
  font-weight:$font-weight-bold;
}

.about-description {
  margin-top:11px;
  font-size:($font-size-base + 2);
  color:$brand-normaltext;
  line-height:25px;
}

.mail-infomation {
  padding:71px 0 40px;
  @extend .clearfix-block;
}

.mail-info-text {
  font-size:($font-size-base + 2);
  color:$brand-normaltext;
  line-height:25px;
  text-align:center;
  > a:hover {
    text-decoration:underline;
  }
}

