//help detail, Terms
.help-location-wrap {
  margin-top:-20px;
  height:56px;
  line-height:56px;
  background-color:$body-bg;
  @extend .clearfix-block;

  .help-location {
    position:absolute;
    left:0;
    right:0;
    height:inherit;
    border-bottom:1px solid $discussion-search-result-bd;
    font-style:italic;
  }
}

.text-home {
  color:$brand-normaltext;
  &:hover {
    color:$brand-lightblue;
  }
}

.text-location {
  margin-left:12px;
  padding-left:21px;
  background:url('#{$img-path}/common/icon-forward.png') no-repeat center left;
  color:$brand-normaltext;
}

.terms-wrap {
  margin-top:48px;
  color:$brand-normaltext;

  > h1 {
    margin-bottom:34px;
    font-size:($font-size-h2 + 2);
  }

  &.terms-wrap-policy {
    margin-top:34px;
  }
}

.terms-paragraph {
  margin-bottom:30px;

  > h2 {
    margin:44px 0 35px;
    padding-top:39px;
    font-size:$font-size-h3;
    border-top:1px solid $list-group-border;
  }

  > h3 {
    margin-bottom:12px;
    font-size:$font-size-base;
    font-family:$font-sans-semibold;
  }

  ol {
    list-style:decimal;
    padding-left:14px;
    margin:15px 0;

    &.list-alpha-upper {
      list-style:upper-alpha;
      > li {
        margin-bottom:10px;
      }
    }
    &.list-alpha-lower {
      list-style:lower-alpha;
    }
  }

  > ul {
    margin:15px 37px;
    li {
      margin-top:10px;
      padding-left:8px;
      background:url('#{$img-path}/common/dot-li.png') no-repeat 0 8px;

      > ol > li {
        padding-left:0;
        background:none;
      }

      > ul {
        margin:0 20px;
        >li {
          margin-top:0;
        }
      }
    }
  }

  strong {
    font-family:$font-sans-semibold;
  }
  em {
    color:$brand-lightblue;
    font-family:$font-sans-semibold;
    font-style:normal;
  }

  a:hover {
    text-decoration:underline;
  }

  .terms-font-brand {
    color:$brand-lightblue;
  }

  .terms-font-underline {
    text-decoration:underline;
  }

  .inner-paragraph {
    margin-bottom:26px;
  }

  .text-addition {
    margin-top:11px;
    font-style:italic;
  }
}

// contact us
.contact-us-wrap {
  width:1062px;
  margin:0 auto 38px auto;
  @extend .clearfix-block;

  > h1 {
    margin:58px 0 53px;
    font-family:$font-sans-semibold;
    font-size:($font-size-h2 + 2);
    text-align: center;
    color:$brand-gray-dark;
  }

  .customer-center {
    float:left;
    width:$customer-center-width;
  }

  .cs-description {
    padding-bottom:23px;
    margin-bottom:32px;
    border-bottom:1px solid $brand-nav-bordercolor;
    font-size:($font-size-base + 2);
    text-align:center;
  }

  .cs-file-text {
    margin:-3px 0 15px 15px;
    font-size:($font-size-base - 2);
  }
  .cs-text {
    margin:0 0 30px 15px;
    line-height:17px;
  }

  .office-address {
    float:right;
    width:$office-address-width;
  }

  .text-alert {
    display:block;
    margin-top:-5px;
    padding:0 0 10px 17px;
    overflow:hidden;
  }
  .text-alert-subject {
    margin:-70px 0 70px 0;
  }
}

%input-item-common {
  width:($customer-center-width - 15);
  margin-bottom:10px;
  font-size:($font-size-base + 2);
  //@include placeholder($valid-input-color-placeholder);
}
.input-cs-email, .input-cs-subject, .input-cs-textarea, .input-cs-q, .input-cs-box {
  @extend %input-item-common;
  //padding:13px;
}
//form 
.input-cs-box.select-choose-only {
  @include select-new-mixin($customer-center-width - 15,50px);

  .select-new-layer {
    max-height:220px;
  }
}
.input-cs-subject {
  margin-bottom:76px;
}

.input-cs-textarea {
  display:block;
  height:298px;
}

.fileinput-cs {
  @extend %input-item-common;
  margin:0 0 10px 15px;
  padding:9px 10px;

  &.fileinput-exists {
    .fileinput-filename {
      width:380px;
      text-overflow:ellipsis;
    }
  }
}

.cs-required {
  float:left;
  width:15px;
  line-height:13px;
  color:$brand-lightblue;
}

.office-list {
  background-color:$brand-moreligtgray;
  padding:13px 64px 6px;

  > li {
    padding:40px 0 44px;
    border-bottom:1px solid $office-list-bd-color;
    text-align:center;

    &:last-child {
      border-bottom:none;
    }

    > h2 {
      margin-bottom:12px;
      color:$brand-normaltext;
      font-size:$font-size-h3;
      font-family:$font-sans-semibold;
    }

    > address {
      margin-bottom:7px;
      line-height:18px;
    }

    > .contact-tel {
      margin-bottom:5px;

      > span {
        padding-left:10px;
        color:$brand-normaltext;
      }
    }

    a:hover {
      text-decoration:underline;
    }
  }
}
