// main: ../main.scss

.total-search{
  margin:-20px 0 20px 0;
  height:$total-search-height;
  .inner-total-search{
    position: absolute;
    left:0;
    right:0;
    background-color:$discussion-search-result-bg;
    background-size: cover;
    height:$total-search-height;
  }
  .area-main-search{
    width:$content-width;
  }
}

.tab-total-search{
  >ul li {
    float:left;
    margin-left:5px;
    &:first-child{
      margin-left:0;
    }
  }
  @include clearfix();
}


.tabs-search {
  width: $content-width;
  margin:39px auto 0;
  overflow: hidden;
  //border:1px solid red;
}
.nav-tabs-search {
  li {
    position: relative;
    display: block;
    float: left;
    > a {
      position: relative;
      display: inline-block;
      padding: $nav-link-padding;
      min-width: 140px;
      text-align: center;
      background-color: $brand-normaltext;
      border-top-left-radius: $border-radius-small;
      border-top-right-radius: $border-radius-small;
      text-decoration: none;
      color: $nav-pills-active-link-hover-color;
      font-size:($font-size-base - 1);
      &:hover,
      &:focus {
        background-color:$brand-lightblue;
        color: $nav-pills-active-link-hover-color;
      }
    }
    + li {
      margin-left: 5px;
    }

    // Active state
    &.active > a {
      &,
      &:hover,
      &:focus {
        color: $nav-pills-active-link-hover-color;
        background-color: $brand-lightblue;
      }
    }//end active state
  }//end li
}//end nav-tabs-search

.search-total-count{
  width: $content-width;
  font-size: ($font-size-base + 2);
  text-align: center;
  color: $result-message-color;
  padding:30px 0;
  strong{
    display: inline-block;
    font-style: italic;
    font-family:$font-sans-semibold;
    color:$brand-normaltext;
    line-height:22px;
    word-break: break-all;
  }
}

.result-box {
  @include clearfix();
  @extend .text-wrap-word;
  margin-bottom: 20px;
  .article-inside{
    clear: both;
    img{
      max-height: 200px;
    }
  }
  .article-title, .article-text{
    word-break:break-word;
  }
  .article-title {
    a {
      &:hover,
      &:focus,
      &:active {
        color:$brand-lightblue;
      }
    }
  }
  .chem-arrow-menu{
    ul li a{
      white-space: nowrap;
    }
  }
}

.result-box + .result-box {
  border-top: 1px solid $discussion-search-result-bd;
  //overflow: hidden;
}

.noresult-img {
  display: block;
  margin: 110px auto -25px auto;
}

.search-result-title{
  padding:35px 0 15px 0;

  &.border-top{
    border-top: 1px solid $discussion-search-result-bd;
  }

  > p{
    font-family:$font-sans-semibold;
    font-size: ($font-size-large - 2);
    color:$brand-normaltext;
    > strong{
      font-family:$font-primary;
    }
  }
}


.no-result-text{
  width:100%;
  text-align: center;
  line-height: 135px;
  color:$brand-normaltext;
  font-size:$font-size-large;
  font-family:$font-sans-light;
}

.no-result-text-big{
  width:100%;
  text-align: center;
  line-height: 135px;
  color:$brand-normaltext;
  font-size:$font-size-h3;
  font-family:$font-sans-light;
}

.cheminfo-search-border {
  .cheminfo-search-list{
    border-top:0;
  }
}

//people search result
.people-search-box{
  position:relative;
  padding:33px 30px 34px 30px;
  .people-info {
    //padding-right:30px;
    p{
      font-family: $font-family-sans;
      color:$brand-normaltext;
      font-size: ($font-size-base - 1);
      line-height:20px;
      white-space: nowrap;
      overflow: hidden;
      &.p-uesr-name{
        font-family: $font-sans-semibold;
        font-size: ($font-size-base + 2);
      }
    }
  }
  .view-profile-btn{
    position:absolute;
    top:33px;
    right:34px;
  }
}
