// main: ../main.scss
// Forms
// --------------------------------------------------

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
  min-width: 0;
}

legend {
  display: block;
  width: 100%;
  padding: 0;
  margin-bottom: $line-height-computed;
  font-size: ($font-size-base * 1.5);
  line-height: inherit;
  color: $legend-color;
  border: 0;
  border-bottom: 1px solid $legend-border-color;
}

label {
  display: inline-block;
  max-width: 100%; // Force IE8 to wrap long content (see https://github.com/twbs/bootstrap/issues/13141)
}

//16.5.10 -- by kka. input autofill
input::-webkit-autofill {
   background-color: white !important;
}
input::-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px white inset;
}

input[type="search"] {
  @include box-sizing(border-box);
}

// Position radios and checkboxes better
input[type="radio"],
input[type="checkbox"] {
  margin: 4px 0 0;
  margin-top: 1px \9; // IE8-9
  line-height: normal;
}

// Set the height of file controls to match text inputs
input[type="file"] { display: block; }

// Make range inputs behave like textual form controls
input[type="range"] {
  display: block;
  width: 100%;
}

// Make multiple select elements height not fixed
select[multiple],
select[size] {
  height: auto;
}

// Focus for file, radio, and checkbox
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus {
  @include tab-focus();
}

.disabled{
  cursor: $cursor-disabled;
  background-color: $input-bg-disabled;
  opacity: 1; // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655
}

// input type
//inpu + textarea
.input-base,
.graystyle,
.cheminfo-add-info,
.cheminfo-add-value,
// .cheminfo-text-area,
.cheminfo-add-direct,
.cheminfo-nmr-input{
  color:  $brand-normaltext;
  border:1px solid $list-group-border;
  border-radius: $border-radius-base;
  padding:14px 13px 15px;
  &:focus{
    border-color:$brand-lightblue;
  }
}
input.input-base{ max-height: 50px; }

input,
textarea{
  @include placeholder();
}

// webkit placeholder  -- focus   placeholder text delete
input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder { /* WebKit browsers */
  color:transparent;
}

input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color:transparent;
}

input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder { /* Mozilla Firefox 19+ */
  color:transparent;
}

input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder { /* Internet Explorer 10+ */
  color:transparent;
}

//enter  input : form (class=enter-input).
.enter-input{
  position: relative;
  .btn-base{
    position: absolute;
    margin-top:0;
    top:10px;
    right:10px;
  }
  .input-base{
    width: 100%;
    height: 50px;
    padding-right: 90px;
  }
}

//upload
.btn-upload-box{
  position: relative;
  width: 100%;
  .upload-input{
    width: 100%;
    text-indent: 120px;
  }
  .btn-upload{
    width: 112px;
    height: 30px;
    top:10px;
    left:10px;
    text-align: center;
    overflow:hidden;
    cursor:pointer;
    display: block;
    position: absolute;
    z-index: 1;
    & span{
      font-family: $font-family-sans !important;
    }
  }
  input[type='file']{
    filter:alpha(opacity=0);
    opacity:0;
    position: absolute;
    width: 112px;
    height: 30px;
    top:10px;
    left:10px;
  }
}
