.keychain-lineup{ padding: 6px 107px 6px 12px !important; }

.chem-form-control {
  display: block;
  width: 100%;
  height: 50px; // Make inputs at least the height of their button counterpart (base line-height + padding + border)
  padding: 6px 30px 6px 12px;
  font-size: ($font-size-base + 2);
  line-height: $line-height-base;
  color: $valid-input-color;
  background-color: $input-bg;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid $list-group-border;
  border-radius: $border-radius-base ; // Note: This has no effect on <select>s in some browsers, due to the limited stylability of <select>s in CSS.
  @include form-control-focus($brand-form-blue);
  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    cursor: $cursor-disabled;
    background-color: $input-bg-disabled;
    opacity: 1; // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655
  }

  // Reset height for `textarea`s
  textarea & {
    height: auto;
  }
}//end form-control-focus

.valid-error { @include form-control-focus($form-error); }

.ic-info {
  display:inline-block;
  float:left;
  top:0;
  left:0;
  width:18px;
  height:18px;
  border-radius: 25px;
  line-height: 18px;
  font-size:12px;
  color:#fff;
  background-color: #95a1aa;
  text-align: center;
  margin: -32px 0 0 -25px;
    &:hover,
    &:focus {
      color: #fff;
      text-decoration: none;
      background-color: $hr-signin;
    }
}

.ic-qustion {
  display:inline-block;
  width:16px;
  height:16px;
  border-radius:8px;
  background-color:#4e565c;
  text-align:center;
  color:$normal-color;
  font-size:($font-size-base - 3);
}

input[type="search"] { -webkit-appearance: none; }

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  input[type="date"],
  input[type="time"],
  input[type="datetime-local"],
  input[type="month"] {
    line-height: $input-height-base;

    &.input-sm,
    .input-group-sm & {
      line-height: $input-height-small;
    }

    &.input-lg,
    .input-group-lg & {
      line-height: $input-height-large;
    }
  }
}

.form-group { margin-bottom: 15px; }

.margin-bottom-none { margin-bottom:0; }

input[type="radio"],
input[type="checkbox"] {
  &[disabled],
  &.disabled,
  fieldset[disabled] & {
    cursor: $cursor-disabled;
  }
}
// These classes are used directly on <label>s
.radio-inline,
.checkbox-inline {
  &.disabled,
  fieldset[disabled] & {
    cursor: $cursor-disabled;
  }
}
// These classes are used on elements with <label> descendants
.radio,
.checkbox {
  &.disabled,
  fieldset[disabled] & {
    label {
      cursor: $cursor-disabled;
    }
  }
}

.ck-style { display:none; }

.ck-style + label {
  display:inline-block;
  cursor:pointer;
  position: relative;
  font-size:13px;
  padding-left: 24px;
  min-height: 0;

}

.ck-style + label:before {
  content:"";
  display: inline-block;
  width:16px;
  height:15px;
  position: absolute;
  left:0;
  bottom:1px;
  background-color: #fff;
  border:1px solid #d2dae0;
}

.ck-style:checked + label:before {
    font-family: FontAwesome;
  content:"\f00c";
  line-height:13px;
  color:#00c6e7;
  border:1px solid #95a1aa;
}