// main: ../main.scss

%ui-float-left{
  float:left;
}
%ui-float-left-marginRight{
  margin-right:13px;
}
%user-profile-img-radius{
  > img {
    display:block;
    border-radius:$border-radius-circle;
    vertical-align:middle;
    width:inherit;
    height:inherit;
  }
}


.profile-picture-large{
  @extend %ui-float-left;
  @extend %ui-float-left-marginRight;
  @include user-profile-imgSize(($profile-img-base-size + 12),($profile-img-base-size + 12),$profile-img-base-largesize);
  @extend %user-profile-img-radius;
  }

.profile-picture-medium{
  @extend %ui-float-left;
  @extend %ui-float-left-marginRight;
  @include user-profile-imgSize(($profile-img-base-size + 4),($profile-img-base-size + 4),$profile-img-base-mediumsize);
  @extend %user-profile-img-radius;
  }

.profile-picture-small{
  @extend %ui-float-left;
  @extend %ui-float-left-marginRight;
  @include user-profile-imgSize($profile-img-base-size,$profile-img-base-size,$profile-img-base-smallsize);
  @extend %user-profile-img-radius;
  }


.profile-picture-exsmall{
  display:inline-block;
  vertical-align: middle;
  margin-right:5px;
  @include user-profile-imgSize(($profile-img-base-size + 6),($profile-img-base-size + 6),$profile-img-base-mediumsize);
  @extend %user-profile-img-radius;
  > img {
    border-radius:$border-radius-circle;
    width:inherit;
    height:inherit;
  }
}

.profile-picture-left-exsmall{
  display:inline-block;
  vertical-align: middle;
  margin-right:15px;
  @extend %ui-float-left;
  @include user-profile-imgSize(($profile-img-base-size + 6),($profile-img-base-size + 6),$profile-img-base-mediumsize);
  @extend %user-profile-img-radius;
  > img {
    border-radius:$border-radius-circle;
    width:inherit;
    height:inherit;
  }
}
