// main: ../main.scss
// 
.select-box {
  display: none;
  opacity: 0;
  filter:alpha(opacity=0);
	//*zoom:1;
  outline: 0px;
	background:$input-bg;
	line-height:normal;
	position:absolute;
	vertical-align:middle;
	cursor:pointer;
  border:1px solid $brand-lightblue;
  border-radius: $border-radius-base;
  width:inherit;
  top:0;
  left:0px;
  z-index: 100;
  .input-base,
  .cheminfo-select-input{
    width:inherit;
    border: none;
  }
	ul.aList {
		display:none;
		z-index:3;
    max-height: 210px;
    overflow-y: scroll;
	}
	li {
		overflow:hidden;
		white-space:nowrap;
    &:last-child{
      padding-bottom: 6px;
    }
		a {
			color:$valid-input-color;
			display:block;
			text-decoration:none;
			text-indent:10px;
      padding: 4px 0px;
			&:hover {
				background:$list-group-hover-bg;
			}
		}
	}
}

.select-box.open-select{
  opacity: 100;
  filter:alpha(opacity=100);
  display: inline-block;
  overflow: hidden;
  *zoom:1;
	ul.aList {
		display:block;
    border-top:1px solid $list-group-border;
	}
}

// //  
.input-box,
.input-add-box,
.input-cs-box{
  display: inline-block;
  .datepicker-input{
    padding: 5px 25px 5px 13px;
    &,
    + i + .aList a{
    font-size: ($font-size-base - 1);
    }
  }

  //input search
  .input-select{
    width:100%;
    float: left;
    position: relative;
  }
  .input-select-search{
    .input-search{
      background-color: $input-bg;
    }
  }
}
.input-add-box{
  width: 100%;
}
.input-select-search{
  // 161027 p element color 
  p.input-base{
    color:$valid-input-color-placeholder;
    &.input-value{
      color:$valid-input-color-value;
    }
  }
}

// 161027   
.input-select-search .input-base,
.input-add-box.input-cs-box .input-cs-q,
.select-box,
.input-add-box.input-cs-box .select-box{
  .fa{
    position:absolute;
    font-size:$font-size-base;
    color:$brand-pattensblue;
  }
}
.input-select-search .input-base,
.input-add-box.input-cs-box .input-cs-q{
  .fa{
    top:18px;
    right:10px;
  }
}
.select-box,
.input-add-box.input-cs-box .select-box {
  .fa{
    top:17px;
    right:9px;
    color:$brand-normaltext;
  }
}
.select-box .fa{color:$brand-normaltext;}
.input-select-search .datepicker-input .fa{ top:10px; }
.select-box .datepicker-input + .fa{
    top:9px;
    right:9px;
}
.input-add-box .input-search .fa{
  top:12px;
}
.input-add-box .cheminfo-select-input + .fa,
.input-add-box .unit-sel-input + .fa{
  top:11px;
  right:9px;
}

.res-location .input-box .input-select{ width:194px; }

// new select
@mixin select-new-mixin($width, $height) {
  width:$width;
  height:$height;
  > .select-new-layer {
    top:($height - 2);
  }
}

.select-new, .select-choose-only {
  position:relative;
  display:inline-block;
  @include select-new-mixin($select-box-wm,$select-box-hm);

  .select-new-input {
    width:inherit;
    height:inherit;
    padding:0 20px 0 13px;
    border:1px solid $brand-pattensblue;
    border-radius:4px;
    color:$valid-input-color;
    background:#fff url('#{$img-path}/common/bg-select.png') no-repeat right center;
    @include placeholder($valid-input-color-placeholder);

    &::-ms-clear {
      display:none;
      width:0;
      height:0;
    }

    &.chooseLi {
      color:$valid-input-color;
    }
  }

  .select-new-layer {
    display:none;
    position:absolute;
    left:0;
    width:100%;
    max-height:148px;
    overflow-x:hidden;
    overflow-y:scroll;
    padding:3px 0;
    border:1px solid $brand-lightblue;
    border-top:1px solid $brand-pattensblue;
    border-bottom-left-radius:3px;
    border-bottom-right-radius:3px;
    background-color:#fff;
    z-index:2;

    > li {
      padding:2px 13px;
      > a {
        color:$valid-input-color;
      }
    }
  }

  &.active {
    > .select-new-input {
      border-color:$brand-lightblue;
      background-image:url('#{$img-path}/common/bg-select-on.png')
    }
  }
  &.keyDown {
    > .select-new-layer {
      display:block;
    }
  }
}