// main: ../main.scss
/*
* Component: forum search
* ----------------------
*/

.forumsearch-page{
  aside{
    margin-top:60px;
  }
  .dsc-contents{
    .discussion-header{
      margin-bottom:20px;
    }
  }
}

.forum-search-box{
  margin:-20px 0 0 0;
  .inner-main-search{
      position: absolute;
      left:0;
      right:0;
      background-color: $discussion-search-result-bg;
      background-size: cover;
      height:$forum-searchbar-bgheight;
  }
}

.search-result-all{
  height: $forum-searchbar-bgheight;
  .inner-main-search{
    .area-main-search{
      width:$content-width;
    }
  }
}

.area-main-search{
  width:$discussion-search;
  margin: 0 auto;
  .discussion-search-form{
    position: relative;
    margin-top:45px;
    input.dis-search-input{
      width:100%;
      height:46px;
      font-size: $font-size-large;
      border: none;
      padding: 12px;
    }
    button.dis-search-submit{
      position: absolute;
      top:0;
      right: 4px;
      border: 0;
      width: 49px;
      height: 46px;
      font-size: ($font-size-large + 2);
      color:$brand-lightblue;
      background: $normal-color;
    }
  }
  .popular-keywords{
    margin-top:14px;
  }
}

.dsc-contents-tab-wrap{
  display: none;
}

//search-tab
.search-result-tab-container{
  ul.search-tabs-id{
    position: absolute;
    bottom: 0;
    li{
      float: left;
      padding : 10px 15px;
      display: block;
      cursor: pointer;
      margin-right:23px;
    }
    & .active-tab-s{
      box-shadow : inset 0 -3px 0 0 $brand-lightblue;
    }
  }
}

.dsc-contents .search-result-header,
.aside-search h3{
  padding-top:30px;
}

.discussion-list-content{

  .dis-search-box{
    padding:0 0 40px 0;
  }
  .dis-people-box {
    //background: none;
    // border: none;
    // border-radius: 0;
    // padding-top:0;
    .follow-group{
      .follow-list{
        padding: 20px 0;
      }
      .follow-list:last-child{
        border-bottom: 1px solid $list-group-border;
      }
      .toggle-btn-following{
        left: initial !important;
        right:0;
      }
    }
  }
}

//furom search result text
.forum-search-result-text{
  width:100%;
  padding:40px 0;
  margin-bottom:20px;
  text-align:center;
  border-bottom:1px solid $discussion-search-result-bd;
  p{
    font-family: $font-sans-semibold;
    font-size:$font-size-large;
    color:$result-message-color;
    em{
      color:$brand-normaltext;
    }
  }
}

// no contents page
.forum-search-no-contents{
  width:100%;
  text-align:center;
  img{
    margin-top:100px;
  }
  p{
    font-family: $font-sans-light;
    color:$brand-normaltext;
    font-size:$font-size-h3;
    letter-spacing: -0.75px;
    margin-top:30px;
    .sub-description {
      display:block;
      margin-top:8px;
      font-size:($font-size-base + 1);
    }
  }
}

//no contents Back to Topic list btn
.search-back-topic{
  display:block;
  margin-top:50px;
  font-family: $font-family-sans;
  font-size:($font-size-base + 2);
  color:$brand-normaltext;
  em{
    margin-left:13px;
  }
  &:hover,
  &:focus,
  &:active {
    font-family: $font-family-sans;
    font-size:($font-size-base + 2);
    color:$brand-normaltext;
    outline:0;
  }
}
