// main: ../main.scss
/*
* Component: chemcast - common
* ----------------------
*/

//chemcast main
.chemcast-main-text,
.chemcast-info-text{
  font-style: italic;
}
.chemcast-main{
  margin: -145px 0 0 0;
  height: 744px;
  //
  .bg-chemcast-main{
    background:url('#{$img-path}/chemcast/chemcast_main_bg.jpg') 0 0 no-repeat;
    background-size:cover;
  }
  .chemcast-main-inner{
    position: absolute;
    left: 0;
    right: 0;
    height: inherit;
    overflow: hidden;
    .caption{
      position: absolute;
      width:100%;
      height:100%;
      padding-top:309px;
      color:$normal-color;
      .chemcast-main-title{
        margin-top:67px;
        font-family: $font-montserrat-ExLight;
        font-size: ceil(($font-size-base * 8.75)); //~120px
        letter-spacing:20px;
        line-height: 86px;
      }
      .chemcast-main-text{
        font-size:ceil(($font-size-base * 2)); //~28px
        line-height: $line-height-computed;
      }
    }
  }
}
.chemcast-info{
  height: 386px;
  .bg-chemcast-info{
    background-color:$article-box-bgcolor;
  }
  .chemcast-info-inner{
    position: absolute;
    left: 0;
    right: 0;
    height: inherit;
    padding-top:136px;
    .caption{
      .chemcast-info-title{
        font-family: $font-sans-semibold;
        //font-size: ceil(($font-size-base * 2.65));
        font-size: ($font-size-large + 10);
        color:$brand-normaltext;
        text-transform: uppercase;
      }
      .chemcast-info-text{
        margin-top:15px;
        font-family: $font-sans-light;
        //font-size: ceil(($font-size-base * 1.85));
        font-size: ($font-size-large + 4);
        line-height: 30px;
        color:$brand-text-gray;
      }
    }
  }
  .btn-move{
    position:absolute;
    top:-25px;
  }
}

.area-chemcast-lnb{
  position: relative;
  padding-bottom:87px;
  height:1150px;
  .article-box,
  .chemcast-link{
    margin-top:73px;
  }
  .article-box{
    position: relative;
    float:left;
    width:575px;
    height:985px;
    padding-top:151px;
    padding-bottom:103px;
    text-align: center;
    .chemcast-content{
      display: none;
      &.active{
        display:block;
      }
    }
    .cont-title{
      font-size:$font-size-h2;
      color:$btn-gray;
      text-transform: uppercase;
    }
    .cont-text{
      margin-top:62px;
      font-family:$font-sans-light;
      font-size:$font-size-large;
      line-height: floor(($font-size-base * 2.6));
      color:$btn-gray;
    }
    .cont-summary{
      margin-top:60px;
      font-size:ceil(($font-size-base * 1.5));
      font-style: italic;
    }
    .btn-move{
      position: absolute;
      bottom:52px;
    }
  }
  .chemcast-link{
    float:right;
    text-align: center;
  }
}

.chemcast-link li{
  display:table;
  margin-top:10px;
  &:first-child{
    margin-top:0;
  }
  >p,
  a{
    width:585px;
    height:189px;
    display:table-cell;
    border-radius:$border-radius-base;
    color:$normal-color;
    font-family: $font-sans-semibold;
    font-size:$font-size-h3;
    text-decoration: none;
    vertical-align: middle;
    &.link-cpr{
      background:url('#{$img-path}/chemcast/chemcast-link-cpr.jpg') no-repeat ;
    }
    &.link-textbook{
      background:url('#{$img-path}/chemcast/chemcast-link-textbook.jpg') no-repeat ;
    }
    &.link-journal{
      background:url('#{$img-path}/chemcast/chemcast-link-journal.jpg') no-repeat ;
    }
    &.link-vod{
      background:url('#{$img-path}/chemcast/chemcast-link-vod.jpg') no-repeat ;
    }
    &.link-job{
      background:url('#{$img-path}/chemcast/chemcast-link-job.jpg') no-repeat ;
    }
    span{
      display: block;
      font-family: $font-family-sans;
      font-size: ($font-size-large + 2);
    }
  }
  a{
    &:hover{
      background:none;
      background-color:$brand-lightblue-alpah;
    }
  }
}
.area-chemcast-tab{
  min-height: $content-min-height;
  .search-box-sm{
    //margin:0 0 109px 0;
    margin:0 0 20px 0;
  }
  .tab-content{
    //padding-top:109px;
    .article-box-row{
      margin-left:-10px;
    }
    .tab-margin{
      padding-top:40px;
    }
  }
}

.no-result{
  &.border-top{
    border-top: 1px solid $search-tab-t-bd;
  }
}

%search-box-full{
  position: absolute;
  left:0;
  right:0;
  height:inherit;
}
.search-box-sm,
.search-box-lg{
  .inner-search-box{
    @extend %search-box-full;
  }
}
.search-box-sm{
  height:$searchbox-height-sm;
  &.search-box-chemcast{
    .inner-search-box{
      background:url('#{$img-path}/chemcast/chemcast-search-bg.jpg') no-repeat;
      background-size:cover;
    }
    .area-main-search{
      width: $content-width;
    }
  }
}
.search-box-lg{
  height:$searchbox-height-lg;
}
.search-box-proleaders .glyphicon-search,
.search-box-chemcast .glyphicon-search,
.total-search .glyphicon-search{
  top:2px;
}

//chemcst-detail
.chemcast-detail{
  > h1{
    padding:42px 0 49px;
    font-size: ($font-size-h1 + 2);
    color: $chemcast-detail-title;
    text-align: center;
  }
  .chemcast-img-wrap{
    padding:60px 0;
    border-bottom: 1px solid $dsc-borderColor;
    height:406px;
    line-height:286px;
    text-align: center;
    .card-img{
      width:auto;
      height:auto;
      max-width:558px;
      max-height:286px;
    }
  }
  .chemcast-info-dsc {
    position: relative;
    width:$content-width;
    margin-top:60px;
    font-size: $article-cont-fontsize;
    line-height: 32px;
    color:$brand-normaltext;
    @include clearfix();
    > dt,
    > dd{
      min-height:22px;
    }
    > dt{
      position: absolute;
      padding-left:20px;
      width:100%;
    }
    > dd{
      margin-bottom: 19px;
      padding-left:195px;
      width:100%;
      //@extend .text-wrap;
      @extend .text-wrap-word;
      ul,
      ol{
        -webkit-margin-before: 1em;
        -webkit-margin-after: 1em;
        -webkit-margin-start: 0;
        -webkit-margin-end: 0;
        -webkit-padding-start: 40px;
      }
      ul{
        list-style-type: disc;
      }
      ol{
        list-style-type: decimal;
      }
      .math-tex {
        font-size:12px !important;
      }
      table + p:last-child {
        clear: both;
      }
    }
  }
  .chapter{
    margin-top:30px;
    font-size: $article-cont-fontsize;
    color:$brand-text-gray;
    .fa{
      margin-right:10px
    }
  }
}

//tab
.search-box-sm,
.search-box-lg{
  .tabpanel{
    width: $content-width;
    overflow: hidden;
  }
}
.search-box-sm{
  .tabpanel{
    margin: 49px auto 0;
  }
}
.search-box-lg{
  .tabpanel{
    margin: 69px auto 0;
  }
}

.tabpanel{
  .nav {
    @include clearfix();
    > li{
      float:left;
      &:first-child{
        margin-left:0;
      }
      a{
        display: inline-block;
      }
    }
  }
}
.tab-set1 .nav{
  @include clearfix();
  > li{
    margin-left:5px;
    > a{
      padding: $nav-tabs-padding;
      min-width: 96px;
      max-height:40px;
      //height:30px;
      background-color:$brand-normaltext;
      color:$normal-color;
      text-align: center;
      @include border-top-radius($border-radius-small);
      &:hover,
      &.hover,
      &:focus,
      &.focus,
      &:active{
        background-color:$noti-after-color;
      }
    }
    &.active > a{
      background-color:$noti-after-color;
    }
  }
}

.tab-set2{
  position:absolute;
  left:0;
  width:100%;
  height:69px;
  &.tab-set2-bg{
    background-color: $noti-after-color;
  }
  .nav{
    width:$content-width;
    @include center-block();
    > li {
      > a{
        padding:24px 48px 23px;
        height:inherit;
        max-height: 69px;
        font-family: $font-sans-semibold;
        font-size: $tabpanel-fontsize;
        color:$normal-color;
        &:hover,
        &:focus,
        &.focus,
        &:active,
        &.active{
          background-color: $brand-normaltext;
        }
      }
      &.active a{
        background-color: $brand-normaltext;
      }
    }
  }
}
.tab-set2 + .tab-content{
  padding-top:69px;
}
.area-chemcast-tab{
 .border-top{
    padding-top:23px;
    border-top: 1px solid $discussion-search-result-bd;
  }
}

.dsc-header-ddmenu.ddmenu-bottom{
  margin-bottom: 11px;
  padding-top:0;
}

//button
.btn-move{
  //display:inline-block;
  left:50%;
  margin-left:-25px;
  width:50px;
  height:50px;
  border-radius: $border-radius-circle;
  text-align: center;
  .fa{
    margin-top:8px;
    font-size: 36px;
  }
  &.lightblue{
    background-color: $btn-normal-color;
    color:$brand-lightblue-alpah;
  }
  &.darkslategray {
    background-color: $btn-normal-color;
    border: 1px solid $border-btn-bordercolor;
    color:$brand-btn-arrow;
  }
}

#vod {
  .card-text {
    height:90px;
  }
}

.modal-vod-view {
  width:863px;
  text-align:left;
  .close {
    margin:-70px -9px 0 0;
  }
  .modal-content {
    padding:23px 9px 35px 47px;
  }
  .author-info {
    margin-top:14px;
  }
  .modal-header {
    margin-bottom:0;
  }
  .modal-body {
    height:604px;
    overflow-x:hidden;
    overflow-y:scroll;
  }
  .modal-vod-content {
    width:767px;
    .floatingUI-user-info, .user-name a {
      cursor:default;
    }
  }
  .card-text-VOD {
    height:auto;
    display:block;
  }

  .article-btngroup {
    margin:28px 0 0;
    .btn-article-function li {
      padding-right:30px;
    }
    span > a {
      display:inline-block;
      height:11px;
      padding:0 16px;
      line-height:11px;
      font-size:($font-size-base - 1);
      color:$btn-brand-opinion-h;
      &:hover {
        color:$brand-normaltext;
      }
      &.vod-previous {
        background:url('#{$img-path}/common/icon-back-s.png') left center no-repeat;
        border-right:1px solid #c9d2d8;
      }
      &.vod-next {
        background:url('#{$img-path}/common/icon-forward-s.png') right center no-repeat;
      }
    }
  }

  .article-cheminfo {
    //display:none;
    padding:0;
    border-bottom:0;
    &.article-comment {
      padding-top:0;
    }
    .author-enter-input {
      width:93.5% !important;
    }
  }
  .article-cheminfo.article-comment .list-article-comment>li {
    margin-top:0;
  }
}
.vod-comment {
  margin:18px 0;
  padding-bottom:15px;
  color:$brand-normaltext;
  border-bottom:1px solid $write-comment-cheminfo-bd;
  span {
    padding-left:7px;
  }
}

.modal-vod-footer {
  margin:20px 40px 0 0;
  text-align:right;
}
.vod-area {
  width:767px;
  height:446px;
  margin:0 0 5px 0;
  padding-top:14px;
  overflow:hidden;
  video {
    width:767px;
  }
}
