// main: ../main.scss
/*
* Component: mypage - profile
* ----------------------
*/
.sub-box{
  position: relative;
  .btn-base{
    float: right;
    &:after{
      clear: both;
    }
  }
  textarea{
    width:100%;
    resize: none;
  }
}
//Self -Introduction
.self-intro{
  font-size: $mypage-font-size;
  color: $mypage-color;
  .view-self-intro{
    padding:0 5px 0 7px;
    width:100%;
    //height:110px;
    //overflow-y:auto;
    word-wrap: break-word;
  }
}


// Technical reserves
.tech-res-list{
  margin-top:20px;
  li{
    display: inline-block;
    position: relative;
    margin: 6px 7px 0 0;
    padding:0 25px 0 10px;
    max-width: inherit;
    height: 33px;
    line-height: 31px;
    border: 1px solid $list-group-border;
    border-radius: $border-radius-base;
    font-family: $font-sans-light;
    color:$brand-normaltext;
    vertical-align: middle;
    overflow: hidden;
    @extend .text-wrap;
    .tech-like-num{
      display: inline-block;
      margin:-1px 10px 0 -10px;
      padding:0 10px 0;
      height:inherit;
      color: $brand-normaltext;
      background: $brand-moreligtgray;
      border-right:1px solid $list-group-border;
      vertical-align: middle;
    }
    .close-tech-res{
      position: absolute;
      top:10px;
      right:0;
      display: inline-block;
      width: 23px;
      height: $close-tech-res-size;
      font-size: $font-size-base - 3;
      cursor: pointer;
      text-align: center;
    }
  }
  &.tech-res-list-view  li{
    padding-right:10px;
  }
  &.keyword-list {
    margin-top:14px;
  }
}

//res-career -> new name: Education
.res-career{
  h2{
    margin-bottom: 15px;
  }
}
.res-career-box {
  position: relative;
  & dl{
    margin: 0;
    dt{
      color: $valid-input-color-placeholder;
      font-weight: normal;
      font-size: ($font-size-base - 1);
      margin-bottom: 10px;
    }
  }
}
.res-career-write{
  .res-career-box{
    padding: 30px;
    margin-top:30px;
    &:first-child{
      margin-top:0;
    }
  }
  .btn-edit-box{
    margin-top:$btn-editox-margin-top;
  }
  & dl{
    dt{
      margin-top:20px;
      &:first-child{
        margin-top:0;
      }
    }
  }
  .res-location{
    input:last-child{
      margin-left: 10px;
      width:478px;
    }
  }
  .res-position{
    input{
      width:100%;
    }
  }
}
.res-career-view,
.res-career-write{
  .btn-article-close{
    position: absolute;
    top: 0;
    right: -32px;
    text-align: center;
  }
}
.res-career-view{
  //display: none;
  .res-career-box{
    margin-bottom: 10px;
  }
  .res-box-years{
    font-size: ($font-size-base + 2);
    display: block;
    float: left;
    width: 130px;
    height: 100%;
    padding : 30px 0 0 23px;
    .res-years-total{
      display: block;
      margin-top: 18px;
      font-size:  ($font-size-base - 1);
      color : $brand-text-gray;
    }
  }
  & dl{
    @include border-right-radius($border-radius-base);
    margin-left: 130px;
    padding: 30px;
    border-left: 1px solid  $list-group-border;
    background: $normal-color;
    dt{
      margin:23px 0 4px;
      &:first-child{
        margin-top:0;
      }
    }
    dd{
      font-size: $font-size-base;
      //display: inline-block;
      word-wrap: break-word;
      > a {
        &:hover,
        &:focus,
        &:active {
          text-decoration:underline;
        }
      }
    }
  }
}

//interests area modal
.interest-modal{
  .modal{
    .modal-header{
      padding:31px 0 26px;
      height:72px;
      .close{
        margin-top:-78px;
      }
    }
    .modal-content{
      width:628px;
      padding: 0;
      border-radius: $border-radius-base;
    }
    .modal-body{
      width:620px;
      height: 543px;
      overflow: hidden;
      &:hover{
        overflow-y:auto;
      }
    }
    .modal-footer{
      text-align: center;
      button{
        width:520px;
      }
      .btn + .btn {
        margin-left:0;
      }
    }
  }
}
.interest-group{
  width: $interest-group-width;
  padding-top:20px;
  .checkbox{
    position: relative;
    display: inline-block;
    width: 136px;
    padding: 0;
    margin: 26px 0 0;
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4){
      margin-top:5px;
    }
    .ckb-label{
      width: 100px;
      height: 118px;
      background-position-x:center;
      background-size : 90px 102px;
      .answer-text{
        top: 96px;
        font-size: $font-size-base;
        font-family: $font-family-base;
        font-weight: normal;
      }
    }
    &:last-child{
      margin-bottom: 60px;
    }
    .insert-text{
      top:($checkb-custom-bgsizeh - 75);
      width: 100px;
    }
  }
}

//edit btn-box
.btn-edit-box{
  width:100%;
  .btn-cancel{
    margin-right: 10px;
  }
  .btn-add{
    float:left;
  }
}

// time period line right checkbox lineup
.time-check-lineup{
  display:inline-block;
  margin-left:7px;
}
