// main: ../main.scss

/* Custom page CSS
-------------------------------------------------- */
/* Not required for template or sticky footer method. */

body > .container { padding: 60px 15px 0; }

// .container .text-muted { margin: 20px 0; }

.footer > .container {
  padding-right: 15px;
  padding-left: 15px;
}

code { font-size: 80%; }

// gray-box
.gray-box{
  width: 100%;
  background: $carreer-bg ;
  border: 1px solid $list-group-border;
  border-radius: $border-radius-base;
  display: inline-block;
}

//Enter input & search
.enter-input{
  .brand-list-group{
    z-index: 1000;
    width: 100%;
    height: 150px;
    background-color: $input-bg;
    overflow-x:hidden;
    overflow-y:scroll;
    border: $brand-lightblue 1px solid;
    border-top:1px solid $input-list-border;
    @include border-bottom-radius($border-radius-base);
    .brand-list-group-item{
      position: relative;
      border: none;
      border-top:1px solid $input-list-border;
      &:first-child{
        border-top: none;
        @include border-bottom-radius(0);
      }
    }
  }
}
//Scroll bars
::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: rgba(0,0,0,0.11);
}
::-webkit-scrollbar-track {
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: transparent;
}
::-webkit-scrollbar * {
  background:transparent;
}
::-webkit-scrollbar-thumb:horizontal:hover,
::-webkit-scrollbar-thumb:vertical:hover {
  background: rgba(0,0,0,0.5);
}
::-webkit-scrollbar-thumb:horizontal:active,
::-webkit-scrollbar-thumb:vertical:active {
  background: rgba(0,0,0,0.11);
}

//bg color
.bg-w{ background-color: $bg-w; }

//position
.rps10{ right:10px; }
.lps10{ left:10px; }

// for common sample
.common-sample{
  table,
  th,
  td {
    border: 1px solid $table-border-color;
    border-collapse: collapse;
  }
  th,
  td {
      padding: 15px;
      text-align: center;
      vertical-align: middle;
  }
}
.table-sample{
  width:100%;
  button{
    float: none !important;
    margin:0 auto;
  }
  .toggle-btn-following{
    float: none;
    margin: 0 auto;
  }
  .btn-upload-box{
    margin: 0;
    top: -25px;
    left: -12px;
  }
  thead{
    background:rgba(0,0,0,0.1);
  }
  tbody th{
    background:rgba(0,0,0,0.05);
  }
}
.display-opacity{
  display: none;
  opacity: 0;
  filter:alpha(opacity=0);
	// *zoom:1;
  outline: 0;
}

///*Checkboxes*/
.check-box-content{
  label {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  }

  input.css-checkbox{
  	  position: absolute;
      overflow: hidden;
      clip: rect(0 0 0 0);
      height:1px;
      width:1px;
      margin:-1px;
      padding:0;
      border:0;
  }
  input.css-checkbox + label.css-checkbox-label {
  	padding: 0;
    display: inline-block;
    font-size: $font-size-base;
    color: $brand-btn-ligtgray;
    cursor:pointer;
    vertical-align:middle;

    &:before{
      content: "";
      display: inline-block;
      width: 20px;
      height: 20px;
      box-shadow: inset 0 0 0 2px $brand-btn-ligtgray;
      border-radius: 2px;
      -webkit-font-smoothing: antialiased;
      line-height: 20px;
      margin-right: 3px;
      float: left;
      text-align:center;
      font-weight: bold;
    }
  }
  input.css-checkbox:checked + label.css-checkbox-label {
    color: $brand-gray;
    &:before{
      content: "\e116";
      font-family: 'feather';
      box-shadow: none;
      background-color: $brand-lightblue;
      color: $checkbox-icon;
      font-size: 16px;
    }
  }
  //only checkbox
  input[type=checkbox].css-checkbox + label.css-checkbox-label:before { border-radius: 2px; }
  input[type=checkbox].css-checkbox:checked + label.css-checkbox-label:before { font-size: 16px; }

  // only radio
  input[type=radio].css-checkbox + label.css-checkbox-label:before {
    content: "\e116";
    font-family: 'feather';
      box-shadow: none;
      background:none;
      color: $brand-btn-ligtgray;
      font-weight: bold;
  }
  input[type=radio].css-checkbox:checked + label.css-checkbox-label:before {
      font-size: 14px;
      font-weight: bold;
      color: $brand-lightblue;
  }
}

// Popular Keywords
.popular-keywords{
  margin-top:30px;
  text-align: center;
  .popular-keywords-title{
    font-size: $font-size-small;
    color:$popular-keywords-title-color;
  }
  a{
    color: $main-font-color;
    margin-left: 25px;
  }

  &.popular-keywords-orange {
    .popular-keywords-title {
      color:$popular-title;
    }
    a:hover, a:focus {
      color:$popular-title;
    }
  }

}

// toggle button 3 (like, ,share, bookmark)
.toggle-button{
  label {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

  }

  input.css-toggle{
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height:1px;
    width:1px;
    margin:-1px;
    padding:0;
    border:0;
  }

  label.css-toggle-label{
    position: relative;
  }
  input.css-toggle + label.css-toggle-label{
    vertical-align:middle;
  	cursor:pointer;
    display: inline-block;
    font-size: $font-size-base;
    color: $brand-btn-ligtgray;
    padding: 0;
    //color: $brand-gray;
    &:before{
      box-shadow: none;
      background-color: $checkbox-icon;
      //color:$brand-normaltext;
      color: $brand-btn-ligtgray;
      font-size: 18px;
      display: inline-block;
    }
    &:hover{
      color:$brand-normaltext;
    }
    &:hover:before{
      color:$brand-normaltext;
    }
  }
  input.css-toggle:checked + label.css-toggle-label
  {
    //color: $brand-gray;
    &:before{
      box-shadow: none;
      background-color: $input-bg;
      color: $brand-lightblue;
      font-size: 18px;
      display: inline-block;

    }
  }

  // like toggle
  input.css-toggle + label.like{
    &:before{
      content: "\f08a";
      font-family: 'FontAwesome';
    }
  }
  input.css-toggle:checked + label.like{
    &:before{
      content: "\f004";
      font-family: 'FontAwesome';
    }
  }

  // share toggle
  input.css-toggle + label.share{
    &:before{
      content: "\e081";
      font-family: 'Feather';
      display: inline-block;
      //border:1px solid red;
      padding:0;
      margin:0;
    }
  }
  input.css-toggle:checked + label.share{
    &:before{
      content: "\f1e0";
      font-family: 'FontAwesome';
      display: inline-block;
      //padding-top:1px;
      transform:scale(0.89);
      //margin-top:12px;
      //border:1px solid red;
      padding: 4px 1px 0 0;
      margin:0;
    }
  }

  // bookmark toggle
  input.css-toggle + label.bookmark{
    &:before{
      content: "\f097";
      font-family: 'FontAwesome';
    }
  }
  input.css-toggle:checked + label.bookmark{
    &:before{
      content: "\f02e";
      font-family: 'FontAwesome';
    }
  }

}

.radiogroup {
  input[type=radio].css-radio{
    display:none;
  }
  .css-radio-label{
    font: inherit;
    line-height:30px;
    margin-right:10px;
  }
  input[type=radio].css-radio + .css-radio-label::before{
      background-clip:content-box;
      border:1px solid $list-group-border;
      border-radius:50%;
      content:"";
      display:inline-block;
      height:16px;
      margin:7px 4px 0 0;
      padding:4px;
      vertical-align:top;
      width:16px;
  }
  input[type=radio].css-radio:checked + .css-radio-label::before{ background-color:$brand-lightblue; }
  input[type=radio].css-radio:checked + .css-radio-label{ color:$brand-lightblue; }
} //end radiogroup

textarea.input-base {
  resize: none;
  word-wrap:break-word;
}