@charset 'UTF-8';


/*  */

@font-face {
    font-family:'open_sanslight';
    src: url('#{$opensans-font-path}/OpenSans-Light-webfont.eot');
    src: url('#{$opensans-font-path}/OpenSans-Light-webfont.eot?#iefix') format('embedded-opentype'),
         url('#{$opensans-font-path}/OpenSans-Light-webfont.woff') format('woff'),
         url('#{$opensans-font-path}/OpenSans-Light-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}

