// main: ../main.scss
/*
* Component: cheminfo Add T-dependent modal
* ----------------------
*/
.t-dependent-modal{
  .cheminfo-modal-footer{
    .btn-add{
      float:left;
      margin-left:50px;
    }
  }
}

.t-dependent-table{
  width:100%;
  border-collapse:separate;
  .t-Temp-width{
    width:45%;
  }
  .t-original-width{
    width:55%;
  }

  thead{
    tr{
      color:$normal-color;
      th{
        font-weight:normal;
        text-align:center;
        vertical-align: middle;
        font-size:($font-size-base + 2);
        &.detail-th{
          font-size:$font-size-base;
        }
      }
      td{
        text-align:center;
        vertical-align: middle;
      }
    }
  }
  tbody{
    td{
      padding:10px;
    }
    &.detail-table{
      td{
        text-align:left;
        padding:10px 30px;
        color:$brand-normaltext;
        font-size:($font-size-base + 1);
      }
    }
  }
  td{
    border-left:1px solid $list-group-border;
    border-bottom:1px solid $list-group-border;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    @extend .text-wrap;
    &:last-child{
      border-right:1px solid $list-group-border;
    }
  }
  // unit box area
  .t-unit-box-area{
    td{
      border-left:0;
      border-right:0;
      padding:17px 0 23px 0;
    }
  }
}

.t-dep-textarea-box{
  margin-top:17px;
  textarea{
    width:100%;
    height:355px;
  }
}


.title-cell-height{
  height:50px;
}

.detail-title-height{
  height:43px;
}

.select-cell-height{
  //height:70px;
  td.text-center {
    padding:17px 0;
  }
  .input-add-box{
    width:auto;
    display:inline-block;
    vertical-align: middle;
    text-align:left;
    .unit-select-box{
      margin-right:0;
      width: 154px;
      .unit-sel-input{
        width:inherit;
      }
    }
  }
}


// table tamp bgcolor
.table-th-temp{
  background-color:$brand-lightblue;
}
// table original bgcolor
.table-th-original{
  background-color:$brand-normaltext;
}
.select-unit-text{
  display:inline-block;
  font-family: $font-sans-semibold;
  font-size:$font-size-base;
  color:$brand-lightblue;
  vertical-align: middle;
  margin-right:10px;
}

// unit box inner
.unibox-title-textbox{
  width:100%;
  padding:12px 0;
  background-color: $text-reference;
  text-align:center;
  font-family: $font-sans-semibold;
  font-size:($font-size-base + 1);
  color:$normal-color;
}

.unitbox-compute-box{
  text-align: left;
  background-color: $brand-moreligtgray;
  padding:0 17px;
  >li{
    position:relative;
    padding:25px 0 15px 0;
    @extend .clearfix-block;

    .select-new {
      width:158px;
      margin-right:30px;
    }
    .select-new-layer li {
        word-wrap:break-word;
        word-break:keep-all;

        > span{
          padding-left:2px;
          font-size:($font-size-base - 3);
          color:$brand-btn-ligtgray;
          word-wrap:break-word;
          word-break:keep-all;
        }
      }


    &:first-child{
      border-bottom:1px solid $msgchat-box-border;
    }
    &:last-child{
      padding:12px 0 15px 0;
    }
    .input-base{
      padding:7px 10px 8px 10px;
      height:36px;
    }
    .unibox-input{
      width:158px;
      margin-right:30px;
    }
    .cross-icon{
      font-size:20px;
      margin:0 17px 0 7px;
    }

    // small input box
    .small-num-input{
      position:absolute;
      top:12px;
      padding:0 7px;
      vertical-align: middle;
      text-align:center;
      width:23px;
      height:21px;
      font-size:($font-size-base - 2);
    }
    .small-first{
      left:165px;
    }
    .small-second{
      left:404px;
    }
    .small-third{
      left:642px;
    }

    // nuitbox btn line
    .unitbox-cancel{
      font-family: $font-family-sans;
      font-size:($font-size-base - 1);
      color:$brand-normaltext;
      margin-right:15px;
    }
    .unitbox-apply{
      font-family: $font-family-sans;
      font-size:($font-size-base - 1);
    }
  }
}


// T-dependent result pager
.result-table-left-box{
  float:left;
  width:388px;
  height:495px;
  overflow-y: auto;
}

.detail-info-right-box{
  float:right;
  width:311px;
  height:495px;
  margin-bottom:32px;
  overflow-y: hidden;
  .detail-infor-box{
    margin-bottom: 20px;
    textarea{
      min-height:205px;
      &.cheminfo-placeholder-Reference-half {
        height:210px;
      }
    }
    &:last-child{
      margin-bottom: 0;
    }
  }
}

// modal body  title
.cheminfo-add-modal-h2{
  font-family: $font-family-sans;
  font-size:($font-size-base + 2);
  color:$brand-normaltext;
  margin-bottom:6px;
  .required {
    color:$brand-text-gray;
    font-size: 12px;
    > span {
      padding-right:1px;
      color:$brand-lightblue;
    }
  }
}

// cheminfor modal detail textarea
.detail-infor-box{
  margin-bottom: 32px;
  textarea{
    width: 100%;
    height:150px;
    line-height:17px;
    resize:none;
    font-family: $font-family-sans;
    font-size:($font-size-base - 2);
  }
  &:last-child{
    margin-bottom: 0;
  }
  .cheminfo-placeholder-Introduction {
    height:270px;
  }
  .input-select-search > .input-search, .select-box > .cheminfo-select-input {
    padding-right:22px;
  }
}
