// main: ../main.scss

/*
* Component: mypage - MyFile
* ----------------------
*/

.my-file-content,
.myfile-header{
  position: relative;
}
.addmyfile{
  right: 0;
  top: 0;
  display: none;
}
.my-file-content{
  .write-notice{
    margin-bottom:10px;
    .close{
      position: absolute;
      top:20px;
      right:20px;
    }
  }
  .cancelfile{
    display:none;
    visibility: hidden;
  }
  .addfile{
    display: block;
    visibility: visible;
  }
}
// 
.myfile-upload{
  position:relative;
  padding: 40px 30px 25px;
  margin-bottom: 10px;
  button.btn-article-close{
    top:0;
    right:-32px;
    visibility: visible;
  }
  .write-footer{
    padding-bottom: 0;
    p:first-child{
      line-height: 30px;
      float: left;
    }
    .radiogroup{
      float: left;
      padding: 0 0 0 30px;
    }
    .btn-edit-box{
      display: inline-block;
    }
    div:first-of-type{
      border: none;
    }
    div:last-of-type{
      margin-top:15px;
    }
  }
}
.upload-write{
  position: relative;
  li{
    margin-bottom: 10px;
    position: relative;
    input{
      font-size: ($font-size-base + 2);
      width: 100%;
    }
    & button{
      font-family: $font-sans-semibold;
    }
    button.inner-btn{
      position: absolute;
      left: 40px;
      top: 87px;
    }
    .upload-input{
      text-indent: 115px;
    }
  }
}

//list myfile
.myfile-list-box{
  padding:0;
}
.list-myfile{
  position:relative;
  padding:30px 50px 0 50px;
  border-bottom: 1px solid $article-border-color;
  line-height:19px;
  &:last-child{
    border-bottom: none;
  }
  .list-myfile-img{
    padding-left:15px;
    text-align: center;
    vertical-align: middle;
    img{
      vertical-align: middle;
    }
  }
  .myfile-subject{
    vertical-align: middle;
    .myfile-title{
      font-family: $font-sans-semibold;
      font-size: ($font-size-base + 1);
      color : $brand-normaltext;
      width : 100%;
      display: block;
      word-break: break-all;
    }
    .date-update{
      float:left;
      font-family: $font-family-sans;
      font-size: $font-size-base;
      color : lighten($brand-normaltext, 20%);
      display: inline-block;
      margin-top:3px;
    }
  }
  .myfile-edit-btn{
    position:relative;
    .dropdown{
      position:absolute;
      top:30px;
      left:30px;

    }
  }

  a.download-box{
    display:block;
    width:100%;//$download-box-size;
    height:$download-box-size;
    background-color:$download-box-color;
    text-align: center;
    color:$brand-download-ic;
    padding-top:35px;
    &:hover{
      background-color: $brand-lightblue;
    }
    &:hover > .icon-inbox,
    &:hover > .myfile-download-num{
      color: $download-num-color;
    }
    .icon-inbox{
      display: block;
      font-size: $font-size-h3;
      color:$brand-download-ic;
    }
    .myfile-download-num{
      margin-top:13px;
      font-size:($font-size-base - 1);
    }
  }

  .dropdown{
    margin-left:5px;
    .icon-globe{
      vertical-align: middle;
    }
  }
}
.off{
  opacity: 0;
  display:none;
  transition: opacity .5s linear;
}

.mypage-myfile-list-table{

  position:relative;
  width:100%;

  .myfile-icon-width{
    width:115px;
  }
  .myfile-title-width{
    width:520px;
  }
  .myfile-dots-width{
    width:80px;
  }
  .myfile-down-width{
    width:116px;
  }

  tr{
    height:($download-box-size + 0);
    border-bottom: 1px solid $article-border-color;
    &:last-child{
      border-bottom:0;
    }
  }
}

.myfile-add-btn{
  margin-bottom:10px;
  button{
    height:50px;
  }
}

.no-contents-box{
  width:100%;
  text-align:center;
  img{
    margin-top:130px;
  }
  p{
    font-family: $font-sans-light;
    color:$brand-normaltext;
    font-size:$font-size-h3;
    letter-spacing: -0.75px;
    margin-top:30px;
  }
}
