// main: ../main.scss
span.highlight{
  background-color:$brand-highlight-color;
}
//common
.article-header{
  float: left;
  width:100%;
}

.article-content {
    a{
        text-decoration: none;
        color:$gnb-menu-color;
        &:hover,
        &:focus,
        &:active {
          color:$gnb-menu-color;
          .article-title {
            color:$brand-lightblue;
          }
        }
    }
  .article-title{
    font-family:$font-sans-semibold;
    font-size:$artice-title-size;
    color:$artice-title-color;
    line-height: 26px;
    margin-bottom:($artice-normal-pdB - 4 );
  }

  .article-text{
    display: block;
    font-size:($font-size-base + 1);
    color:$artice-title-color;
    line-height: 21px;
    padding-bottom:10px;

    img{
      float:right;
      margin-left: 15px;
      margin-bottom: 20px;
      width:$artice-thumnail-width;
    }
  }

  /// bskim - 161017
  .article-inside{
    img{
      float:right;
      margin-left: 15px;
      margin-bottom: 5px;
      width:$artice-thumnail-width;
    }
  }
}

.see-more{
  float: left;
  display: block;
  font-size:($font-size-base - 1);
  margin-bottom:( $artice-normal-pdB + 5);
   > a{
     color:$aricle-more-btn;
     &:hover,
     &:focus,
     &:active {
       color:$artice-title-color;
       text-decoration:none;
     }
   }
    @include clearfix();
}

.btn-article-seemore {
  padding:25px 0;
  width:100%;
  clear:both;
  text-align: center;
  a {
      text-decoration: none;
      color:$article-btn-article-seemore;
      font-size:($font-size-base + 2);
      &:hover,
      &:focus,
      &:active {
        color:$artice-title-color;
        text-decoration: none;
      }
  }
}

.author-info {
  float: left;
  width:80%;
 a > img {
    position: relative;
    border-radius: $user-img-radius;
    margin-right: 13px;
    vertical-align: middle;
    float:left;
    border:none;
  }
}

.article-info{
 float:left;

 .btn-group {
   .dropdown{
    margin-right: 10px;
   }
 }
}
.user-name ,
.creation-date{
  display:block;
  margin-bottom: 0;
}
.user-name {
  a {
      font-size:$font-size-base;
      color:$brand-lightblue;
    }
}
.creation-date {

  font-size: ($font-size-base - 1);
  color:$article-date;
  float:left;
  > i.fa-circle{
    line-height: 24px;
    //margin-right:6px;
    -webkit-transform: scale(0.18);
    -moz-transform: scale(0.18);
    -o-transform: scale(0.18);
    -ms-transform: scale(0.18);
    transform: scale(0.18);
    //border:1px solid red;
  }
}
.creation-date-cheminfo {
  font-size: ($font-size-base - 1);
  color:$article-date;
  > i.fa-circle{
    padding:0 3px;
    -webkit-transform: scale(0.18);
    -moz-transform: scale(0.18);
    -o-transform: scale(0.18);
    -ms-transform: scale(0.18);
    transform: scale(0.18);
  }
}
.artice-sort-right{
  float:right;
}

// opinion detail view btn-group
.article-btngroup{
  clear:both;
  i{
    margin-right: $i-margin;
    font-size:($font-size-large + 1);
    vertical-align: middle;
    //border:1px solid green;

    &.icon-speech-bubble{
      padding-bottom: 3px;
    }
    &.fa-heart-o,
    i.fa-heart{
      //padding-top:2px;
    }
    &.icon-eye{
      padding-bottom: 5px;
    }
    &.icon-share{
      padding-bottom: 4px;
    }
    &.fa-share-alt{
      //border:1px solid red;
      padding-right: 2px;
      margin-left:1px;
      -webkit-transform: scale(0.89);
      transform: scale(0.89);
    }
  }

  //margin:0 $artice-normal-pdLR 13px $artice-normal-pdLR;
  ul.btn-article-function{
    display:inline;
    min-width: 300px;
    //border:1px solid red;
    li {
      display: inline-block;
      color:$brand-btn-ligtgray;
      font-size:($font-size-base - 1);
      padding-right: 10px;
      > a{
          text-decoration: none;
          color:$brand-btn-ligtgray;
          &:hover{
          color:$brand-normaltext;
          }
          &.active {
            color:$brand-lightblue;
          }
      }
    }//end li
  }
  span{
    float:right;
  }
}//end article-btngroup


.article-btngroup-big{
  clear:both;
  >ul{
      display:inline;
      li {
        display: inline-block;
        color:$brand-btn-ligtgray;
        font-size:($font-size-base + 2);
          > a{
              text-decoration: none;
              color:$brand-btn-ligtgray;
              &:hover{
              color:$brand-normaltext;
              }
              &.active {
                color:$brand-lightblue;
              }
          }
        + li{
          //content: '';
          padding: 0 5px;
         }
         vertical-align: middle;
           color:$brand-btn-ligtgray;
         i{
           font-size:$font-size-h3;
           vertical-align: middle;
         }
         i.icon-eye{
           padding-bottom: 5px;
         }
         i.icon-share{
           padding-bottom: 4px;
         }

      }

  }
  span{
        float:right;
        -ms-transform: scale($dsc-view-scale, $dsc-view-scale); /* IE 9 */
        -webkit-transform: scale($dsc-view-scale, $dsc-view-scale); /* Safari */
        transform: scale($dsc-view-scale, $dsc-view-scale);
  }
  span.bookmark-scale-lg {
    margin:5px 5px 0 0;
    -ms-transform: scale($dsc-view-scale-lg, $dsc-view-scale-lg); /* IE 9 */
    -webkit-transform: scale($dsc-view-scale-lg, $dsc-view-scale-lg); /* Safari */
    transform: scale($dsc-view-scale-lg, $dsc-view-scale-lg);
  }
}//end article-btngroup-big

.share-btn-toggle {
  color:$brand-btn-ligtgray;
  //border:1px solid blue;
    &:hover,
    &:focus,
    &:active,
    &.active {
      color:$brand-normaltext;
      text-decoration: none;
      outline:0;
    }
}

.bookmark-btn-toggle {
  color:$brand-btn-ligtgray;
  &:visited{
    color:$brand-btn-ligtgray;
  }
  &:hover,
  &.hover
  {
    color:$brand-normaltext;
    text-decoration: none;
  }
  &:active,
  &.active
  {
    color:$brand-lightblue;
  }
}

.write-comment{
  > a{
    text-decoration: none;
    font-size:($font-size-base - 1);
    //color:$brand-btn-ligtgray;
    color:$brand-normaltext;
    &:hover,
    &:active {
      color:$brand-text-blue;
      text-decoration: none;
    }
  }
}//end write-comment

//common
.article-box {
  width: $article-box-width;
  padding: 30px 0 0 0;
}

.article-box-full {
  width: 100%;
  padding: 30px 0 10px 0;
}

.article-box ,
.article-box-full{
    float: left;

    border: solid 1px $article-border-color;
    border-radius: $border-radius-base;

    background: $article-box-bgcolor;
    margin-bottom: 10px;
    text-align: left;

    /*@include box-shadow(0 3px 0 rgba(0,0,0,.02));*/

    .article-header{
      padding: 0 30px 0 30px;
      margin-bottom: 18px;
      //margin:0 30px 0 $artice-normal-pdLR;
      }
    .article-content {
        margin:0 $artice-normal-pdLR 20px $artice-normal-pdLR;
        a{
            text-decoration: none;
        }

    }
    .article-btngroup{
     margin:0 $artice-normal-pdLR 13px $artice-normal-pdLR;
    }//end article-btngroup



    .article-footer {
      display: inline-block;
      position: relative;
      width:100%;
      padding:19px $artice-normal-pdLR 19px $artice-normal-pdLR;
      background-color: $brand-lightergray;
      vertical-align: middle;

      //border: solid 1px $article-border-color;
      border-radius: $border-radius-base;
        @include clearfix;
        > span {
        display:inline-block;
        margin-bottom:0;
        vertical-align: middle;
          > a{
              text-decoration: none;
              color:$brand-btn-ligtgray;
              font-size:($font-size-base - 1);
              &:hover{
                  color:$artice-title-color;
                }
              i{
                margin-right: $i-margin;
                font-size:($font-size-large + 1);
                margin-bottom:2px;
                vertical-align: middle;
              }
          }
        }
      .write-opinion {
        position: absolute;
        top: 22%;
        right:$artice-normal-pdLR;
      }
    }//end article-footer
  }//article-box end


// article-box
.view-article-box {
    .article-header{
      margin-bottom: 8px;
      }
}//view-article-box end

.article-box-gray{
  width:$body-full-width;
  background-color: $brand-moreligtgray;
  padding:65px;
  > h2{
    position: relative;
    padding-left:35px;
    margin-bottom: 65px;
    color: $brand-normaltext;
    font-family: $font-sans-semibold;
    font-size: $font-size-h3;
    > i{
      position: absolute;
      top:6px;
      left:0;
      font-size:$font-size-large + 2;
    }
  }
  .article-content{
    color: $brand-normaltext;
    font-size: $font-size-large;
    line-height: 26px;
  }
}

.comment-list{
  margin-top:20px;
  margin-bottom: 10px;
}

.comment-article-box{
  background-color: $dsc-comment-bg;
  padding: 20px 30px 20px 54px;
  //border:1px solid blue;
  position: relative;
  margin-bottom: 10px;
  &:before{
    display: inline-block;
    position: absolute;
    top:33px;
    left:34px;
    content: '';
    width: 12px;
    height: 15px;
    background: transparent url('../images/icon/dsc-ic-comment.png') no-repeat;
  }
}
