// main: ../main.scss
/*
* Component: notifications - common
* ----------------------
*/

//notifications
.notification-contents{
  margin-top:36px;
  h1,
  .title-report{
    color:$brand-normaltext;
  }
  h1{
    margin-bottom:31px;
    font-size:$font-size-h3;
  }
  .title-report{
    font-size: $font-size-h2 + 2;
    margin-bottom: 62px;
  }
}
.noti-contents-inner{
  margin-top:10px;
}
//list
.list-box{
  @extend .box-normal;
  padding:0 40px;
  width:$content-width;
  .list-item{
    position: relative;
    padding:45px 0 62px;
    max-height:($download-box-size + 21);
    @include clearfix();
    &:not(:last-child){
      border-bottom: 1px solid $article-border-color;
    }
  }
}
.list-item{
  &.select-receive{
    &:before{
      content: "";
      position:absolute;
      top:44px;
      left:-12px;
      width:6px;
      height:6px;
      background:$noti-after-color;
      border-radius:50%;
    }
  }
  .list-subject{
    float:left;
    max-width:$list-subject-width;
    margin-top:-3px;
  }
  .btn-profile{
    position: absolute;
    right:3px;
    bottom:29px;
  }
  .list-user-name > a,
  .message-text > a,
  .message-cont > a{
    &:hover{
      text-decoration: underline;
    }
  }
  .list-user-name{
    &,
    > a{
      color:$brand-normaltext;
      font-family: $font-sans-semibold;
      font-size: $font-size-large;
    }
  }
  .timestamp{
    &:before{
      margin:0 9px 0 10px;
      content: '\00b7';
    }
  }
  .message-cont {
    margin-top:10px;
    max-width: $list-subject-width;
    &,
    > a{
      //display: block;
      color:$brand-normaltext;
      line-height: 24px;
      font-size:($font-size-base + 2);
      @extend .text-wrap;
      //white-space: nowrap;
      strong{
        font-family: $font-sans-semibold;
      }
    }
  }
  .close,
  .block{
    margin-top:-23px;
  }
}
.follow-area .list-item .list-subject{
    max-width: $list-subject-width - 20;
}
.follow-area .message-cont{
    max-width: $list-subject-width - 100;
}
//search
.message-search{
  position: relative;
  .input-base{
    width:$body-full-width;
    font-size: $font-size-large;
    //color: $valid-input-color-placeholder;
    padding: 12px 43px 12px 12px;
    max-height:46px;
    &:focus{
      border-color:$brand-lightblue;
    }
  }
  + .list-box{
    margin-top:30px;
  }
  button.dis-search-submit{
    position: absolute;
    top:2px;
    right: 4px;
    border: 0;
    width: 45px;
    height:43px;
    font-size: ($font-size-large + 2);
    color:$brand-lightblue;
    background: $normal-color;
  }
  + .no-result-box{
    margin-top:30px;
  }
}

.report-contents{
  color:$brand-normaltext;
  margin-left:36px;
  .report-subject{
    .report-title{
      font-family:$font-sans-semibold;
      font-size:$font-size-large;
    }
    .timestamp{
      &:before{
        margin:0 9px 0 10px;
        content: '\00b7';
      }
    }
  }
  .report-text{
    margin-top:20px;
    a{
      color:$brand-normaltext;
      font-family: $font-sans-semibold;
      &:hover,
      &:focus{
        text-decoration: underline;
      }
    }
  }
  .report-wrap{
    margin-top:30px;
    .report-info-title{
      font-size:$font-size-base - 1;
      &:not(:first-child){
        margin-top:30px;
      }
    }
    .report-info-cont{
      margin-top:5px;
      font-family: $font-sans-semibold;
      font-size:$font-size-base + 2;
    }
  }
  .report-info-cont{
    > a{
      &:hover,
      &:focus{
        text-decoration: underline;
      }
    }
  }
}

.btn-box-center{
  text-align: center;
  margin-top:50px;
  button{
    display: inline-block;
  }
}

//setting
.password-wrap{
  margin:0 auto;
  width:655px;
  @include clearfix();
  .password-title,
  .setting-input{
    float:left;
    &:not(:first-of-type){
      margin-top:12px;
    }
  }
  .password-title{
    width:153px;
    height:50px;
    line-height: 50px;
    font-size: $font-size-base + 2;
    color:$brand-text-gray;
    font-weight: normal;
    text-align: right;
    margin-right:30px;
  }
  .setting-input{
    width:470px;
  }
  .btn-password{
    width:100%;
  }
}

.report-contents{
  .password-box{
    margin-top:19px;
  }
  .sub-title{
    font-size:$font-size-large;
    font-family: $font-sans-semibold;
    font-weight:$font-weight-bold;
  }
}
.setting-desc-box{
  margin-top:57px;
  border-top: 1px solid $discussion-search-result-bd;
  .sub-title{
    margin-top:48px;
  }
  .setting-desc{
    margin-top:11px;
  }
}
.setting-list{
  margin-top:50px;
  .setting-list-title{
    font-size: $font-size-base + 2;
  }
  .list-box{
    margin-top:14px;
    padding:5px 60px 3px;
    .list-item{
      padding:17px 0 20px;
      max-height:($download-box-size + 21);
      .list-subject{
        margin:0;
      }
      .list-user-name a{
        font-size:$font-size-base + 2;
      }
      .message-cont{
        margin-top:-3px;
        a{
          font-size:$font-size-base - 1;
        }
      }
      .btn-profile{
        bottom:27px;
      }
    }
  }
  .list-noblocked{
    padding:54px 0;
    font-family: $font-size-base + 2;
    text-align: center;
  }
}

.lnb-sub-list .lnb-nodata{
  margin-top:88px;
  border-bottom:none;
  font-size: ($font-size-base + 1);
  font-family: $font-sans-semibold;
  color:$list-nodata-text;
  text-align:center;
  i{
    font-size: ($font-size-large + 2);
    margin-right:10px;
  }
}
