// main: ../main.scss

/*
* Component: notice popup
* ----------------------
*/

.modal-noticepopup{
  .modal-content{
      padding: 78px 77px;
  }

  .modal-header {
    margin: 0;
    .close{
      margin-top: -125px;
    }
  }

  .modal-body{
    text-align: left;
  }

  // () 
  .notice-img-box{
    text-align: center;
    margin-bottom: 57px;
    img{
      max-width:469px;
    }
  }

  //  
  .notice-text-box{
    margin-bottom: 37px;
    .date-area{
      font-size:$font-size-large;
      color: $brand-normaltext;
      margin-bottom:25px;
      text-align: center;
    }
    .notice-area{
      font-size: $font-size-base;
      color: $brand-normaltext;
      line-height: 22px;

    }
  }

  // Confirm  
  .notice-btn-box{
    .btn-brand-blue{
      height: 50px;
      font-size:($font-size-base + 2);
    }
  }

  //  
  .check-box-content{
    margin-top:7px;
    input.css-checkbox + label.css-checkbox-label {
      font-size:($font-size-base - 1);
      color:$brand-text-gray;
      line-height:15px;

      &:before{
        width:15px;
        height:15px;
        line-height:13px;
        box-shadow: inset 0 0 0 1px $tooltip-not-again;
        border-radius:1px;
        margin-right:6px;
      }
    }

    input.css-checkbox:checked + label.css-checkbox-label {
      color:$brand-normaltext;
      &:before {
        box-shadow: none;
        font-size:($font-size-base - 1);
      }
    }
  }


}
