// main: ../main.scss


/*
* page : main
* ----------------------
*/
.main-bgcolor{
  background-color: $main-font-color;
  padding: 0;
  margin: 0;
}
.main-article-width{ width:100%; }
//background
.bg-main-search{
  margin: 0 0 0 0;
  height: $bg-main-search-height;
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
  background:$brand-normaltext url('#{$img-path}/main/bg_main_factory.jpg') no-repeat 0 0;
  background-size:cover;
  .bg-main-search-img{
    width: 100%;
    min-width: 1080px;
    margin:0 auto;
    height: $bg-main-search-height;
  }
}
.videobg{
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  overflow: hidden;
}
.main-search-area{
  margin: 0 auto;
  height: $bg-main-search-height;
  width: $content-width;
  overflow: hidden;
  .main-search{
    position: absolute;
    left: 0;
    right: 0;
    margin-top: 385px;
  }
}
.main-search-tit{
  color: $main-font-color;
  line-height: 1;
  font-size: ($font-size-base * 4.14);
  font-family: $font-montserrat-regular;
  text-transform:uppercase;
  text-align: center;
}
//search area
.main-search-input-area{
  width:$main-search-input-area-width;
  margin:45px auto 0;
  .main-search-form{
    position: relative;
    .main-search-input{
      width:100%;
      font-size: ($font-size-large + 2);
      color: $main-font-color;
      background-color:$brand-lightblue;
      border: none;
      padding: 20px;
      height: $main-search-input-height;
      max-height: $main-search-input-height;
    }
    .main-search-submit{
      position: absolute;
      top:0;
      right: 4px;
      border: 0;
      width: 49px;
      height: 100%;
      font-size: ($font-size-large + 2);
      color:$main-font-color;
      background: $brand-lightblue;
    }
  }
}

// 20161101      ( hight  )
.main-search-input{ @include placeholder($main-font-color); }

.btn-main-carousel-arrow{
  display: block;
  width: 39px;
  height: 21px;
  margin: 270px auto 0;
  opacity: .7;
  color: $main-font-color ;
  font-size: 60px;
  &:hover{
    opacity: 1;
    color: $main-font-color ;
  }
}
.main-search-input-area .main-search-list-group{
  margin-top:1px;
  background-color: transparent;
  height: 200px ;
  overflow: visible ;
  border: none ;
  border-radius: $border-radius-base;
  .brand-list-group-item{
    background: $brand-lightblue;
    color: $main-font-color;
    padding: 20px;
    font-size: 16px;
    font-family: $font-family-sans;
    &:first-child{
      @include border-top-radius($border-radius-base);
    }
    &:hover{
      background: $brand-lightblue;
      color: $brand-normaltext;
    }
  }
  .search-item-seemore{
    font-family: $font-sans-semibold;
  }
}

//
.main-carousel-area{
  width: $content-width;
  height: $main-carousel-area-height;
  padding: 105px 0;
  margin: 0 auto;
  font-size: 0;
  .carousel-indicators{
    bottom: 60px;
    li{
      background-color: $brand-gray;
      width:  6px;
      height: 6px;
      border-radius: 6px;
      margin-left:2px;
      margin-right:2px;
      vertical-align: middle;
      &.active{
        background-color: $brand-lightblue;
      }
    }
  }
  .carousel-control{
    background: $main-font-color;
    width: auto;
    text-shadow: none;
    filter: none;
    margin-top: 260px;
  }
}

// carousel-contents
.carousel-inner{
  width: auto;
  margin: 0 50px;
  height: $main-carousel-inner-height;
  overflow: hidden;
  .main-carousel-contents{
    width: $main-carousel-contents-width;
    margin: 0 auto;
    text-align: center;
    color: $brand-normaltext;
  }
  .floatingUI-container{
    width:50px;
    margin:0 auto;
    .user-profile-contents{
      margin-top:15px;
      margin-left:-($floatingUI-width - ($profile-img-base-size + 16)) / 2;
    }
    .floatingUI-user-info > img{
      border-radius: 50%;
      width:48px;
      height:48px;
    }
  }
}

.main-carousel-forum{
  margin: 35px 0;
  .main-carousel-forum-subject{
    font-size: ($font-size-h2 + 2 );
    font-family: $font-sans-light;
    line-height: 46px;
    overflow:hidden;
    max-height: 92px;
    margin-bottom: 10px;
    &:before,
    &:after{
      content: '"';
    }
    &:hover {
      cursor:pointer;
    }
  }
  .main-carousel-forum-summary{
    width: $main-carousel-forum-summary-width;
    margin: 0 auto;
    overflow:hidden;
    max-height: 60px;
    font-size: $font-size-large;
    line-height: ($font-size-large + 10);
    &:hover {
      cursor:pointer;
    }
  }
}
//main menu link
.main-menu-link-container{
  width: 100%;
  left: 0;
  > li{
    width: 100%;
    height: 545px;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    text-align: center;
  }
}
.main-menu-link{
  display: inline-block;
  color: $main-font-color;
  font-family: $font-montserrat-ExLight;
  height: 100%;
  width: 100%;
  &:hover,
  &:link,
  &:visited{
    color: $main-font-color;
    text-decoration: none;
  }
  > .main-link-icon{
    margin: 0 auto;
    width: 100px;
    display: block;
    margin-top: 135px;
  }
  > .main-link-title{
    margin-top: 80px;
    font-weight: bold;
    font-size: 50px;
    text-transform:uppercase;
    display: block;
  }
  > .main-link-title:lang(zh-CN){
    font-weight: normal;
  }
  > .main-link-summary{
    font-size: 22px;
  }
}
.main-img-cheminfo{ background:url('#{$img-path}/main/main-menu-cheminfo.jpg') no-repeat ; }
.main-img-proleaders{ background:url('#{$img-path}/main/main-menu-proleaders.jpg') no-repeat ; }
.main-img-forum{ background:url('#{$img-path}/main/main-menu-forum.jpg') no-repeat ; }
.main-img-modules{ background:url('#{$img-path}/main/main-menu-modules.jpg') no-repeat ; }
.main-img-chemcast{ background:url('#{$img-path}/main/main-menu-chemcast.jpg') no-repeat ; }
.main-img-breaktime{  background:url('#{$img-path}/main/main-menu-breaktime.jpg') no-repeat ; }
