// main: ../main.scss

/*
* Component: Message Chat
* ----------------------
*/

.msg-chat-wrap{
  position: fixed;
  bottom: 0;
  right: 96px;
  width: 356px;
  z-index: $msgchat-wrap-zIndex;
  background-color: $msgchat-bgcolor;
  @include box-shadow($msgchat-wrap-boxShadow);
}

.msg-chat-header{
  width: 100%;
  height: 60px;
  background-color: $msgchat-head-bg;
  padding: 13px 17px 0 20px;
  border-bottom: 1px solid $msg-chat-inputArea-bg;
  color: $msgchat-head-textcolor;
  box-shadow: 0 3px 3px #f2f5f6;

  .msg-chat-stranger{
    float: left;
    .profile-picture-small {
      margin-right:10px;
    }
  }
   .fa{
    float: left;
    font-size: 16px;
    margin-top: 14px;
    margin-right: 3px;
  }
  p {
    display: inline-block;
    width: 176px;
    margin-top: 8px;
    margin-bottom:0;
    font-size: $msgchat-head-text-size;
    font-weight: bold;
    @include text-overflow;
  }
  .msg-chat-btn-group{
    float: right;
    .msg-chat-btn-toggle > li {
      float: left;
    }
    //.dropdown-menu-title, .msg-chat-btn-topcolor
    ul li a {
      //margin-left: 10px;
      font-size: $msgchat-head-toggle-size;
      color: $msgchat-head-textcolor;
      @include opacity(.9);
      &:hover,
      &:focus,
      &:active{
        color: $brand-lightblue;
      }
      &.msg-chat-btn-topcolor {
        margin-left:17px;
      }
    }
    .dsc-dropmenu-msgchat{
      margin: -4px 0 0 8px;
      width: 108px;
      a{
        margin-left: 0;
        font-size: $font-size-base;
      }
    }
  }
}

.msg-chat-body{
  width: 100%;
  height:400px;
  padding-top:6px;
  overflow-y: auto;
}

//
.msg-date {
  width:inherit;
  display: block;
  font-size: 12px;
  color: $msgchat-date-font-color;
  font-size:($font-size-small + 1);
  text-align: center;
  margin-top: 10px;
  margin-bottom: 8px;
}
//
.msg-received {
  @include clearfix();
  padding:0 10px;
  > p {
    float:left;
    max-width: 316px;
    background-color: $msgchat-boxbg;
    border: 1px solid $msgchat-box-border;
    border-radius: $border-radius-large;
    margin-bottom:6px;
    padding: 15px 20px;
    color: $msgchat-text-color;
    font-size: $msgchat-body-font-size;
    > span {
      @extend .text-wrap-word;
    }
  }
}

.msg-time {
  display:block;
  margin-top:4px;
  font-size:($font-size-small - 1);
  color:$brand-msg-date-color;
  @include opacity(.9);
}

// 
.msg-send {
  @include clearfix();
  padding:0 10px;
  > p{
    float: right;
    max-width: 316px;
    background-color: $msgchat-sent-boxbg;
    border-radius: $border-radius-large;
    margin-bottom:6px;
    padding: 15px 20px;
    font-size: $msgchat-body-font-size;
    color: $msgchat-sent-textcolor;
    text-align:right;
    > span {
      @extend .text-wrap-word;
    }
    > .msg-time {
      color:$normal-color;
    }
  }
}

//   
.msg-block-history{
  @include clearfix();
  margin:10px 0 16px;
  > p {
    background-color: $msg-block-history-bgcolor;
    font-size: $msg-block-date-fontsize;
    line-height: $msg-block-history-lineheight;
    text-align: center;
  }
}

//   
.msg-block-box-area{
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  height: 66px;
  padding: 19px 20px;
  background-color: $msgchat-head-textcolor;
  line-height: $msg-block-lineheight;
  z-index:2;
  .msg-block-text-area{
    color: $msgchat-sent-textcolor;
    &:before{
      content: '\e107';
      font-family: 'Feather';
      font-size: ($font-size-base + 10);
      float: left;
      margin-right: 13px;
    }
    .msg-block-name{
      display: inline-block;
      > span{
        float: left;
        &.block-name{
          max-width: 190px;
          @include text-overflow;
        }
      }
    }
    .msg-block-date{
      font-size: $msg-block-date-fontsize;
      line-height: $msg-block-date-lineheight;
      @include opacity(.7);
    }
  }
}

// 
.msg-chat-inputArea{
  width: 100%;
  background-color: $msg-chat-inputArea-bg;
  padding: 10px;
  overflow:hidden;

  //textarea
  textarea{
    width: 280px;
    height: $msgchat-btn-height;
    border-top-right-radius: $msgchat-border-radius-reset;
    border-bottom-right-radius: $msgchat-border-radius-reset;
    border-color: $btn-brand-gray-light-h;
    border-right: 0;
    font-size: $msgchat-body-font-size;
    color: $msg-chat-inputArea-textcolor;
    padding: 10px;
    resize: none;
  }

  // 
  .msg-chat-btn-enter{
    float: left;
    width: 56px;
    height: $msgchat-btn-height;
    background-color: $msgchat-btn-color;
    border: 1px solid $msgchat-btn-border-color;
    border-left: 0;
    border-radius: $border-radius-base;
    border-top-left-radius: $msgchat-border-radius-reset;
    border-bottom-left-radius: $msgchat-border-radius-reset;
    font-size: $msgchat-btn-text-size;
    color: $msgchat-sent-textcolor;
    text-align: center;
    text-decoration: none;
    line-height: $msgchat-btn-lineheight;
    cursor:pointer;
    &:hover,
    &:active {
      background-color: $btn-brand-gray-h;
      color: $msgchat-sent-textcolor;
      text-decoration: none;
      outline: 0;
    }

    @include clearfix();
  }
}
