//cheminfo main
%cheminfo-search-bg{
  position:absolute;
  left:0;
  right:0;
  padding-top:54px;
  background:url('#{$img-path}/cheminfo/cheminfo-search-bgimg.jpg') no-repeat;
  background-size:cover;
  >.area-main-search{
    width:$cheminfo-search-width;
  }
  .discussion-search-form{
    margin-top:0;
  }
  input.dis-search-input{
    padding-left:55px;
  }
}

.cheminfo-search{
  margin:-20px 0 0 0;
  height:$searchbox-height-cheminfo-main;
  line-height:normal;

  &.cheminfo-search-result{
    height:$searchbox-height-sm;
  }

  >.cheminfo-main-search{
    @extend %cheminfo-search-bg;
    height:$searchbox-height-cheminfo-main;
  }

  >.cheminfo-sub-search{
    @extend %cheminfo-search-bg;
    height:$searchbox-height-sm;
  }
}

.cheminfo-main-description{
  margin-top:6px;
  text-align:center;

  >.cheminfo-main-title{
    padding:10px 0 45px;
    font-family:$font-montserrat-regular;
    font-size:$font-size-h1;
    color:$normal-color;
  }

  >.cheminfo-search-description{
    padding-bottom:19px;
    font-size:($font-size-base + 2);
    color:darken($normal-color, 26%);
  }
}

.structure-search{
  position:absolute;
  top:6px;
  left:7px;
  display:inline-block;
  width:40px;
  height:34px;
  border-radius:2px;
  background-color:#96a2b1;

  > span {
    display:inline-block;
    width:40px;
    height:34px;
    background:url('#{$img-path}/cheminfo/structure-search-icon.png') no-repeat 9px center;
    text-indent:-9999px;
  }

  &:hover, &:focus {
    background-color:$structure-search-hover;
  }
}

.bg-structure {
  height:150%;
}

.cheminfo-example-show{
  @extend .clearfix-block;
  display:none;
  width:100%;
  height:101px;
  margin-top:-148px;
  color:$normal-color;
  line-height:25px;
  background-color:$cheminfo-example-bg-on;
  border-bottom:1px solid $cheminfo-example-bg-border;

  >ul {
    width:$content-width;
    margin:0 auto;
    padding-top:26px;

    >li {
      float:left;
      margin-left:45px;
      text-align:left;
      >.example-title {
        display:block;
        font-family:$font-sans-semibold;
      }
      >.example-value {
        font-size:($font-size-small + 1);
        opacity:$tooltip-opacity;
      }
    }
  }
}

.cheminfo-example{
  position:absolute;
  left:0;
  bottom:0;
  width:100%;
  height:75px;
  text-align:center;
  background-color:rgba(60, 69, 76, 0.3);

  >.cheminfo-example-text{
    display:inline-block;
    margin-top:28px;
    font-style:italic;
    color:darken($normal-color, 30%);
    cursor:pointer;

    &:hover {
      color:$normal-color;
    }
  }

  &.cheminfo-example-hover {
    background-color:$cheminfo-example-bg-on;
    >.cheminfo-example-text {
      color:$normal-color;
    }
    .cheminfo-example-show {
      display:block;
    }
  }
}

.ic-info-white {
  display:inline-block;
  width:18px;
  height:18px;
  margin-left:10px;
  border-radius:25px;
  line-height: 18px;
  font-size:$font-size-small;
  font-style:normal;
  color:$brand-lightblue;
  background-color:$normal-color;
  text-align:center;
}

.cheminfo-body-copy{
