}

.cheminfo-body-copy-figure{
  height:140px;
  margin-bottom:50px;
  text-align:center;
  background-position:center center;
  background-repeat:no-repeat;

  &.cheminfo-body-copy-figure01{
    background-image:url('#{$img-path}/cheminfo/img-compounds01.png');
  }
  &.cheminfo-body-copy-figure02{
    background-image:url('#{$img-path}/cheminfo/img-compounds02.png');
  }
  &.cheminfo-body-copy-figure03{
    background-image:url('#{$img-path}/cheminfo/img-compounds03.png');
  }
}

.cheminfo-body-copy-title{
  font-family:$font-sans-semibold;
  font-size:($font-size-h3 + 2);
  color:$brand-lightblue;
}
.cheminfo-body-copy-description{
  margin-top:9px;
  padding:0 10px;
  line-height:26px;
  color:$brand-normaltext;
}

.application-area-wrap{
  height:($application-height + 50);

  >.application-area{
    position:absolute;
    left:0;
    right:0;
    height:($application-height + 50);
    padding:68px 0 0 0;
    background-color:$brand-lightblue;
    color:$normal-color;
  }
}

.application-area-inner{
  @extend .clearfix-block;
  width:$content-width;
  margin:0 auto;
  padding:0 0 0 57px;
}

.application-area-h1 {
  float:left;
  width:300px;
  font-size:($font-size-h1 + 2);
  font-family:$font-sans-semibold;
  line-height:48px;
}

.application-list {
  float:right;
  width:716px;
  margin-top:28px;

  >li {
    float:left;
    width:105px;
    margin:0 88px 14px 10px;

    > .tooltip-cheminfo-wrap {
      position:relative;
      font-size:($font-size-base + 2);
      color:$normal-color;
      cursor:pointer;
      &:hover {
        color:$brand-normaltext;

        >.tooltip-cheminfo {
          display:block;
        }
      }
    }

    .tooltip-cheminfo {
      display:none;
      position:absolute;
      bottom:36px;
      left:-237px;
      margin-left:0;
      padding:38px 38px 33px;
      background-color:$brand-normaltext;

      &:before {
        top: 100%;
        left: 50%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-top-color:$brand-normaltext;
        border-width:9px;
        margin-left:-9px;
      }

      >h3 {
        font-size:$font-size-large;
        color:$brand-lightblue;
        font-family:$font-sans-semibold;
        line-height:23px;
        white-space:nowrap;
      }
      >p {
        margin-top:5px;
        font-size:($font-size-base - 2);
        color:$brand-lightblue;
        white-space:nowrap;
        @include opacity(0.7);
      }
      >h4 {
        margin:16px 0 10px 0;
        font-size:($font-size-base + 1);
        color:$normal-color;
        text-decoration:underline;
      }

      > ul {
        > li {
          float:left;
          width:37%;
          margin-bottom:5px;
          color:$normal-color;
          font-size:($font-size-base - 2);
          word-wrap:normal;
          white-space:nowrap;
          &:before {
            content:' ';
          }
        }
        > li:nth-child(3n+3) {
          width:26%;
        }
      }

      > ul.two-block {
        > li {
          width:50%;
        }
      }
      > ul.two-block-case1 {
        > li:nth-child(2n+1) {
          width:45%;
        }
        > li:nth-child(2n) {
          width:55%;
        }
      }
      > ul.two-block-case2 {
        > li:nth-child(2n+1) {
          width:70%;
        }
        > li:nth-child(2n) {
          width:30%;
        }
      }
    }

    &:nth-child(3n+1) {
      width:208px;
    }
    &:nth-child(1) {
      .tooltip-cheminfo {
        left:-190px;
      }
    }
    &:nth-child(4) {
      .tooltip-cheminfo {
        left:-208px;
      }
    }
    &:nth-child(6) {
      .tooltip-cheminfo {
        left:-216px;
      }
    }
    &:nth-child(7) {
      .tooltip-cheminfo {
        left:-170px;
      }
    }
    &:nth-child(8) {
      .tooltip-cheminfo {
        left:-222px;
      }
    }
  }
}

.layer-structure {
  position:fixed;
  top:0;
  right:0;
  bottom:0;
  left:0;
  width:100%;
  height:100%;
  z-index:-1;

  .modal-dialog {
    position:absolute;
    top:0px;
    left:50%;
    margin:120px auto 0 -420px;
    width:$modal-structure-width;
    z-index:3;
  }

  &.open-structure {
    z-index:1041;
    overflow-y:auto;
  }

  &.open-loading {
    z-index:1041;
  }

  &.fade .modal-dialog {
    @include translate(0, -25%);
    @include transition-transform(0.3s ease-out);
  }
  &.in .modal-dialog {
    @include translate(0, 0);
  }

  .modal-header{
    height:113px;
    .modal-title-h1 {
      font-size:($font-size-large + 3);
      text-align:center;
      text-indent:0;
    }
  }
  .modal-description {
    font-family:$font-family-sans;
    font-size:($font-size-base + 2);
    padding-left:12px;
  }
  .modal-body{
    padding:32px 0;
  }
  .radiogroup {
    margin-bottom: 10px;

    >.choose-one {
      display:inline-block;
      position:relative;
      margin-right:29px;
      >label {
        font-size:($font-size-base + 2);
        color:$brand-normaltext;
      }
    }
  }

  .editor-structure {
    width:590px;
    height:430px;
    margin:0 auto;
    >iframe {
      width:580px;
      height:422px;
    }
  }

  .modal-footer {
    border-top:0;
    padding:0 0 53px 0;

    button {
      margin-left:6px;
    }
  }
}

.body-open-layer {
  overflow:hidden;
  //@extend .fixed-header;
}

.tooltip-structure {
  position:absolute;
  width:$tooltip-structure-width;
  top:38px;
  left:-($tooltip-structure-width / 4);
  color:$brand-normaltext;
  text-align:left;

  >.chem-arrow-menu {
    left:0;
    padding:24px 15px 20px;
  }

  .tooltip-title {
    padding-bottom:11px;
    background-color:$body-bg;
    border-bottom:0;
    font-family:$font-sans-semibold;
    font-size:($font-size-base + 2);
  }

  .tooltip-content {
    line-height:16px;
    font-size:($font-size-base - 2);
  }

  .ex-paragraph {
    margin-top:8px;
    color:$brand-text-gray;
  }

  .check-box-content {
    margin-top:20px;

    input.css-checkbox + label.css-checkbox-label {
      font-size:($font-size-base - 1);
      color:$brand-normaltext;
      line-height:15px;

      &:before{
        width:15px;
        height:15px;
        line-height:13px;
        box-shadow: inset 0px 0px 0px 1px $tooltip-not-again;
        border-radius:1px;
      }
    }

    input.css-checkbox:checked + label.css-checkbox-label {
      &:before {
        box-shadow: none;
        font-size:($font-size-base - 1);
      }
    }
  }

  .tooltip-close {
    position:absolute;
    top:20px;
    right:15px;
  }
}

