.graph-spectra-list {
  width:306px;
  margin:0 auto;

  >li {
    padding:22px 12px;
    border-bottom:1px solid $gray-lighter;
    text-align:center;

    &:first-child {
      padding-top:2px;
    }

    &:last-child {
      padding-bottom:2px;
      border-bottom:none;
    }

    >a {
      position:relative;
      display:inline-block;
      padding:8px;
      outline:1px solid $gray-lighter;

      > span {
        position:absolute;
        top:0;
        left:0;
        display:block;
        width:100%;
        height:100%;
        background-color:$brand-dark-orange;
        opacity:0.1;
        visibility:hidden;
      }

      &:hover,
      &.active {
        outline:2px solid $brand-dark-orange !important;

        > span {
          visibility: visible;
        }
      }

      >img {
        width:262px;
      }
    }
  }
}

.graph-tag {
  display:block;
  width:49px;
  height:22px;
  margin-bottom:5px;
  line-height:22px;
  font-family:$font-sans-semibold;
  font-size:($font-size-small - 1);
  color:$normal-color;
  background-color:$brand-dark-orange;
}

.graph-mark-member {
