  > li.open-cheminfo .article-cheminfo {
    padding:25px 40px 22px;
  }
  .text-number-point {
    font-size:($font-size-base - 2);
    color:$brand-lightrockblue;
  }
}

.mark-graph {
  float:left;
  position:relative;
  margin:7px 16px 0 0;

  input.css-checkbox:checked + label.css-checkbox-label {
    &:before{
      background-color:$brand-lightrockblue;
    }
  }
}

.count-data-point {
  padding-left:5px;
  font-size:($font-size-base);
  color:$brand-lightblue;
}

.no-cheminfo-data {
  display:inline-block;
  margin:10px 0 16px;
  padding:3px 0 3px 35px;
  color:$brand-lightrockblue;
  font-style:italic;
  background:url('#{$img-path}/cheminfo/bg_nodata.png') no-repeat left center;
}

