}

.input-payment {
  &::-moz-placeholder { font-size:12px;}
  &:-ms-input-placeholder { font-size:12px;} // Internet Explorer 10+
  &::-webkit-input-placeholder  { font-size:12px;} // Safari and Chrome
}

.txt-label-point {
  padding-left:7px;
  font-family:$font-sans-semibold;
  font-size:$font-size-base;
  color:$brand-normaltext;
}

.txt-final-selling {
  margin-top:10px;
  font-size:($font-size-base - 1);
  color:$brand-text-gray;

  > strong {
    padding-right:10px;
    font-family:$font-sans-semibold;
    color:$font-tabpanel-titlecolor;
    font-size:$font-size-base;
    font-weight:400;

    > span {
      font-size:($font-size-base - 1);
      font-family:$font-primary;
    }

    > em {
      padding-left:7px;
      font-style:normal;
    }
  }
}

.txt-sale-price {
  margin-top:8px;
  font-size:($font-size-base - 2);
  color:$brand-text-gray;
}

.modal-pc {
  .modal-header .close {
    margin-top:-104px;
  }

  .modal-content {
    padding:58px 77px 30px;
  }

  .modal-footer {
    border-top:0;
    padding-top:23px;

    > button {
      margin-left:5px;
    }
  }
}

.pc-title {
  color:$brand-headertext;
  font-size:($font-size-h4 + 3);
}

.pc-table {
  border-collapse:unset;
  width:100%;
  border:1px solid $brand-pattensblue;
  border-radius:$border-radius-small;

  caption {
    margin-top:15px;
    color:$brand-headertext;
    font-size:$font-size-base;
  }

  .col-harf {
    width:50%;
  }

  .col1 {
    width:15%;
  }

  .col2 {
    width:22%;
  }

  .col3 {
    width:30%;
  }

  .col4 {
    width:40%;
  }

  th,td {
    padding:10px 0;
    text-align:center;
  }

  th {
    border-right:1px solid $brand-pattensblue;
    background-color:$brand-moreligtgray;
    font-weight:400;
    color:$brand-normaltext;
    font-size:($font-size-base - 2);
    vertical-align:middle;
    line-height:12px;

    &.pc-th {
      font-size:($font-size-base - 1);
    }

    &:last-child {
      border-right:0;
    }
  }

  td {
    color:$brand-text-gray;
    font-size:($font-size-base - 1);
    border-top:1px solid $brand-pattensblue;
    border-right:1px solid $brand-pattensblue;

    &:last-child {
      border-right:0;
    }

    &.pc-td {
      border-top:0;
      font-size:($font-size-base + 2);
      color:$brand-on-blue;
    }

    &.pc-point {
      color:$brand-on-blue;
      text-align:right;

      padding-right:30px;
    }

    &.pc-price {
      font-family:$font-sans-semibold;
      color:$brand-normaltext;
    }

    &.pc-select {
      font-family:$font-sans-semibold;
      color:$brand-on-blue;
    }

    &.pc-alignr {
      text-align:right;
      padding-right:30px;
    }

    &.pc-alignl {
      text-align:left;
      padding-left:18px;
    }
  }

  .css-radio-label, .css-radio-label::before {
    margin-right:0 !important;
  }
}

.layer-paid {
  position:absolute;
  top:35px;
  left:35px;
  width:1098px;
  height:400px;
  box-sizing:border-box;
  background-color:#e1e5e9;
  @include opacity(0.9);
  z-index:2;
}

.btn-show-graph {
  position:absolute;
  top:190px;
  left:500px;
  z-index:3;
}
