$brand-gray : #87919c;
$brand-lightblue : #00c6e7;
$brand-headertext : #292e33;
$brand-normaltext : #3c454c;
$brand-nav-bordercolor : #e4e9ec;
$brand-lightgray-text : #75959a;
$brand-lightblue-alpah : #00c6e7;
$brand-lightgray : #e9e9e9;
$brand-lightergray: #fafafa;
$brand-btn-ligtgray: #aab8c2;
$brand-text-gray: #95a1aa;
$dsc-btn-cancel: #b6c3cd;
$search-add-border:#e0ddd2;
$search-add-backcolor:#fbfaf8;
$brand-text-blue:#0bacc7;
$brand-download-ic:#898989;
$brand-ligtgray-background:#fbfbfb;
$normal-color:#ffffff;
$brand-lightgray-border:#727475;
$brand-moreligtgray:#f7fafb;
$brand-border-gray:#d5dce2;
$brand-highlight-color:#ffdb95;
$brand-on-blue:#00a7cc;
$brand-on-lightblue:#4dc2db;
$brand-gray-dark: #4c4c4c;
$brand-dark-orange: #ffa800;
$brand-form-blue: #10c9e8;
$brand-lightrockblue: #96a2b1;
$brand-darkturquoise: #00c6e7;
$brand-elephant:#283842;
$brand-pattensblue: #d2dae0;


