$brand-msg-boxShadow: rgba(0,0,0,0.2);
$brand-msg-text-darkgray: #4c4c4c;
$brand-msg-bg-darkgray: #e1e6ea;
$brand-msg-date-color: #7e8c9a;
$brand-msg-textarea-color: #30353a;
$brand-msg-block-bgcolor: #e3edf1;

