// main: ../main.scss
//
// cards
// --------------------------------------------------

// cards
.box-normal{
  float: left;
  margin-bottom: 10px;
  text-align: left;
  @extend %box-common;
}

.card-area{
  @include cards($content-width, $cards-height-sm, $cards-height-sm, 0, 0);
  min-height: $cards-height-sm ;
  position:relative;
  margin-bottom:10px;
  .card-cont{
    margin-bottom:20px;
    padding:40px 50px;
  }
}

.card-collapse{
  @include cards-h($content-width, 0, 0);
  min-height: $cards-height-sm ;
  position:relative;
  margin-bottom:10px;
  .card-cont{
    margin-bottom:28px;
    padding:40px 383px 40px 50px;
  }
}

.card-twins{
  @include cards(($article-box-width - 270), ($cards-height-sm - 5), ($cards-height-sm - 5), $cards-padding-md, $cards-padding-md);
  margin-bottom:30px;
  padding-top:40px;
  &:nth-of-type(even){
    margin-left:30px;
  }
}

.card-triplets{
  @include cards(($article-box-width - 457), $cards-height-md, $cards-height-md, $cards-padding-md, $cards-padding-md);
  position:relative;
  margin:0 0 10px 10px;
  &:nth-child(3n+1){
    margin-left:0;
  }
}
.card-twins,
.card-triplets {
  float:left;
}

.card-collapse,
.card-area,
.card-twins,
.card-triplets{
  .see-more{
    position:absolute;
    margin-top:15px;
    left:50px;
    bottom:8px;
    a{
      &:hover,
      &:focus{
        color:$bnt-more-ov;
      }
    }
  }
}

.card-img-wrap{
  float:right;
  margin-left:50px;
  .card-img-box{
    display: table-cell;
    width: $cards-img-width;
    max-width: $cards-img-width;
    height:$cards-img-height;
    max-height:$cards-img-height;
    //line-height:$cards-img-height;
    overflow: hidden;
    vertical-align: middle;
    background-color: $cards-img-bg;
    text-align: center;
    @include border-right-radius ($border-radius-small);
    .card-img{
      width:inherit;
      height:auto;
      max-height:283px;
    }
  }
}

.card-chemcast{
  &,
  &:hover,
  &:focus,
  &:active{
    text-decoration: none !important;
  }
  .card-title{
    font-family: $font-sans-semibold;
    font-size: $font-size-h3;
  }
  .card-title,
  .card-title > a{
    color:$card-title-blue;
    @include text-overflow();
    display:block;
    &:hover,
    &:focus,
    &:active {
      color:$brand-lightblue;
    }
  }
  .card-text{
    margin-top:19px;
    font-size: $article-cont-fontsize;
    //word-wrap: break-word;
    //word-break: break-all;
    height:136px;
    overflow: hidden;
  }
  .card-text,
  .card-text > a {
    color:$card-text-normal;
  }
  .card-user-info{
    font-size:$font-size-base;
    a{
      color:$card-text-normal;
    }
    .name{
      margin-top:12px;
    }
    .link > a{
      color:$card-link;
      text-decoration: underline;
    }
  }
  .data > a{
    color: $card-data;
  }
  &.card-triplets{
    .card-title{
      line-height: 30px;
    }
    .card-text{
      height:($cards-height-md - 224);
    }
    .data{
      margin-top:12px;
    }
  }
}

.card-proleaders{
  position: relative;
  .card-cont{
    display: table;
    @extend .text-wrap;
  }
  .card-profile,
  .card-profile-cont{
    display:table-cell;
    vertical-align: top;
  }
  .card-profile{
    float:left;
    width:42px;
    margin-right:20px;
  }
  // .card-profile{
  //   float:left;
  // }
  .user-name,
  .user-affi,
  .user-major{
    font-size: ($font-size-base + 1);
    color:$card-text-normal;
  }
  .user-keyword {
    margin:16px 0 0 0;
    li {
      display:inline-block;
      height:22px;
      line-height:20px;
      text-align:center;
      padding:0 7px;
      font-size:11px;
      color:#3c454c;
      border-radius: 4px;
      border:1px solid #d2dae0;
      margin:0 0 4px 0;
      &.selected {
        border-color:#ffdb95;
        background: #ffdb95;
      }
    }
  }
  .card-profile-cont{
    //float:left;
    //margin-left:22px;
    width:100%;
    //width:410px;
    .user-name {
      a {
        font-size: ($font-size-h3 + 1);
        font-family: $font-sans-semibold;
        color:$brand-normaltext;
        &:hover,
        &:focus,
        &:active {
          color:$brand-lightblue;
        }
      }
      span{
        margin: -9px 5px 0 0;
        font-size:($font-size-h4 + 3);
      }
    }
    .user-affi,
    .user-url{
      margin-top:9px;
      > a {
        &:hover,
        &:focus,
        &:active {
          text-decoration:underline;
        }
      }
    }
    .user-affi{
      line-height: 19px;
    }
    .user-major{
      margin-top:16px;
      font-style: italic;
      text-decoration: underline;
    }
  }
  .card-btn{
    display: block;
    position: absolute;
    bottom:0;
    left:0;
    padding:11px 0 14px;
    width:100%;
    &.btn-zoom{
      background-color: $btn-card;
      @include border-bottom-radius ($border-radius-base);
      text-align: center;
      color:$dsc-gray;
      font-size:$font-size-h3;
      &:hover{
        background-color:$brand-lightblue;
        color:$btn-card-gray-ov;
      }
    }
  }
}
.text-word .card-text{
  word-wrap: break-word;
  word-break: break-word;
}
.btn-goto{
  margin-top:10px;
  font-size: ($font-size-base + 2);
  vertical-align:baseline;
  i{
    margin-left:9px;
    font-size: $font-size-large;

  }
}

// VOD card
.card-VOD-wrap {
  float:left;
  width:($article-box-width - 457);
  padding:$cards-padding-sm 0;
  margin:0 0 10px 10px;
  @extend %box-common;
  @include box-shadow(0 2px 0 rgba(0,0,0,.02));

  &:nth-child(3n+1){
    margin-left:0;
  }

  .author-info {
    margin:0 32px 8px;
  }

  .article-btngroup {
    margin:30px 32px 0;
    .btn-article-function li {
      padding-right:30px;
    }
  }
}

.thumb-VOD {
  clear:left;
  position:relative;
  height:215px;
  text-align:center;
  background-color:$brand-lightblue;
  overflow:hidden;

  > img {
    position:absolute;
    top:0;
    left:0;
    width:$thumb-VOD-width;
    height:$thumb-VOD-height;
    z-index:1;
    border:0;
  }

  .thumb-layer {
    position:absolute;
    top:0;
    left:0;
    z-index:2;
    width:$thumb-VOD-width;
    height:100%;
    background-color:#000;
    opacity:0.4;
  }
}

.thumb-Recreation {
  height:300px;
  > img {
    height:300px;
  }
}

.btn-VOD-play {
  position:absolute;
  top:80px;
  left:164px;
  width:52px;
  height:52px;
  background:transparent;
  border:4px solid #FFF;
  border-radius:26px;
  color:#fff;
  z-index:3;
  > i {
    padding:15px 0 0 3px;
  }
}
.VOD-content {
  height:161px;
  margin:8px 32px 10px;
}
.VOD-source {
  height:18px;
  font-size:($font-size-base - 1);
  font-style:italic;
  color:$brand-text-gray;
  @include text-overflow();
  > span {
    padding-left:8px;
    color:$card-title-blue;
  }
}
.card-title-VOD {
  margin:15px 0 10px;
  color:$card-title-blue;
  font-size:($font-size-large + 2);
  font-family:$font-sans-semibold;
  @extend .text-wrap;
}
.card-text-VOD {
  color:$brand-normaltext;
  font-size:($font-size-base + 1);
  @include text-overflow-multi(64px, 3);
}
.VOD-link:hover {
  .btn-VOD-play {
    color:$brand-lightblue;
    border-color:$brand-lightblue;
  }
  .thumb-layer {
    opacity:0.6;
  }
  .card-title-VOD {
    color:$brand-lightblue;
  }
}

// TextBook
.card-textbook {
  > li {
    position:relative;
    float:left;
    width:$book-cover-width;
    margin:0 0 10px 10px;
    &:nth-child(5n+1){
      margin-left:0;
    }
    > a:hover {
      .bookcover-layer {
        display:block;
      }
      .book-title {
        color:$brand-lightblue;
      }
    }
  }
}

.bookcover-layer {
  display:none;
  > div {
    position:absolute;
    top:0;
    left:0;
    z-index:3;
    width:$book-cover-width;
    height:$book-cover-height;
    background-color:$brand-lightblue;
    @include opacity(0.8);
    border-top-left-radius: $border-radius-base;
    border-top-right-radius: $border-radius-base;
  }
  > span {
    position:absolute;
    top:156px;
    left:67px;
    color:$normal-color;
    font-size:$font-size-large;
    font-family:$font-sans-semibold;
    z-index:4;
  }
}

%book-title-style {
  color:$card-title-blue;
  font-size:($font-size-base + 2);
  line-height:19px;
}

.card-bookcover {
  width:$book-cover-width;
  height:$book-cover-height;
  background-color:$forum-side-interest-boxcolor;
  border-top-left-radius: $border-radius-base;
  border-top-right-radius: $border-radius-base;
  overflow:hidden;

  img {
    position:absolute;
    top:0;
    left:0;
    z-index:2;
    width:$book-cover-width;
    height:$book-cover-height;
    border-top-left-radius: $border-radius-base;
    border-top-right-radius: $border-radius-base;
    //object-fit: cover;
  }

  .book-title-box {
    display:table;
    width:198px;
    height:308px;
    margin:14px auto 0 auto;
    border:2px solid $normal-color;
    text-align:center;
    overflow:hidden;

    > .book-title-img {
      display:table-cell;
      vertical-align: middle;
      font-family:$font-family-sans;
      @extend %book-title-style;
    }
  }
}

.book-title {
  height:40px;
  margin-bottom:8px;
  @extend %book-title-style;
  @include text-overflow-multi(40px, 2);
  font-family:$font-sans-semibold;
}

.book-writer {
  height:38px;
  color:$brand-normaltext;
  font-size:($font-size-base - 1);
  @include text-overflow-multi(38px, 2);
}

.card-information {
  height:128px;
  @extend %box-bottom;
  @include box-shadow(0 2px 0 rgba(0,0,0,.02));
  padding:20px 22px 26px;
  overflow:hidden;
}
