// Text overflow
// Requires inline-block or block for proper styling

@mixin text-overflow()
{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin text-overflow-multi($line-height, $count-line)
{
  height: $line-height;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: $count-line;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
}
