 // main: ../main.scss
/*
 * Component: footer
 * ----------------------
 */
footer {
 position:relative;
 width:$body-full-width;
 height: $footer-height;
 background-color: $brand-footer-gray;
 z-index:2;
 .footerInner{
   width:$content-width;
   padding:53px 0 0;
   @include center-block();
  }
}

.footer-logo{
  font-family: $font-montserrat-semibold;
  font-size: $font-size-h4 + 2;
  > a { //  
    display: block;
    text-indent: -9999px;
    overflow: hidden;
    background: url('#{$img-path}/footer/footer-logo-beta.png') 0 0 no-repeat;
    width:118px;
    height:27px;
  }
  /*
  > a{
    color: $normal-color;
    &:hover,
    &:focus,
    &:visited,
    &:active{
      color: $normal-color;
    }
  }
  */
}
.footer-menu-wrap{
  position:relative;
  margin-top:20px;
  @include clearfix();
  .footer-menu{
    float:left;
     > li{
      display: inline-block;
      &:not(:first-child){
       margin-left:36px;
      }
      > a{
       color:$footer-menu-color;
       &:hover,
       &:focus,
       &:active,
       &.active{
        color: $brand-darkturquoise;
       }
      }
    }
  }
  .copyright-area{
    float:right;
    font-size: $font-size-base - 1;
    color:$footer-menu-color;
  }
}

.link-language {
  position:absolute;
  top:-38px;
  right:0;
  li {
    float:left;
    height:12px;
    line-height:12px;
    padding:0 13px;
    vertical-align:middle;
    border-right:1px solid $brand-lightrockblue;

    &:last-child {
      padding-right:0;
      border-right:none;
    }
    > a {
      color:$brand-lightrockblue;
      font-size:($font-size-base - 1);
      &:hover,
      &:focus,
      &:active,
      &.active {
        color:$btn-normal-color;
      }
    }
  }
}

// back to top btn
.top-link-block{
  display: none;
  position:fixed;
  bottom: 10px;
  right: 27px;
  width:$back-to-top-width;
  height:$back-to-top-height;
  border-radius: $border-radius-chambase;
  background-color:$brand-lightblue;
  text-align: center;
  line-height:50px;
  &:hover,
  &:focus,
  &:active {
    background-color:$brand-on-blue;
  }
  > i {
    color:$normal-color;
    font-size:($font-size-base * 2.3);
    font-weight: bold;
  }
}
