// main: ../main.scss
// FloatingUI
// --------------------------------------------------

// floateingUI container
.floatingUI-container{
  &:hover .user-profile-contents{
    display:block;
  }
}

// user picture - mouse over point
.floatingUI-user-info{
  position:relative;
}


.pic-type-large{
  .user-profile-contents{
    margin-top:($profile-img-base-size + 12) + 15; //55px;
    margin-left: -($floatingUI-width - ($profile-img-base-size + 12)) / 2; //-128px;
  }
}

// user profile contents
.user-profile-contents{
  display:none;
  position:absolute;
  margin-top:($profile-img-base-size + 6) + 15; //55px;
  margin-left: -($floatingUI-width - ($profile-img-base-size + 6)) / 2; //-128px;
  background-color:$floatingUI-bg;
  width:$floatingUI-width;
  border:1px solid $brand-border-gray;
  border-radius:$border-radius-base;
  text-align:left;
  z-index: ($zindex-dropdown - 1);
  @include box-shadow($dsc-dropmenu-hLength $dsc-dropmenu-VLength $dsc-dropmenu-Blur rgba(0,0,0,$dsc-dropmenu-opacity));
  .profile-info-box:before{ // mouseover area suport
    position:absolute;
    top:-25px;
    left:50px;
    width:200px;
    height:50px;
    content:" ";
    background-color:transparent;
  }
  .profile-info-box{
    float:left;
    width:inherit;
    padding:21px 21px 8px;
    line-height:20px;
    //border-bottom:1px solid $brand-border-gray;
    .margin-and-radius{
      margin-right:12px;
      img{
        border-radius:$border-radius-circle;
      }
    }
    .user-name-group-wrap {
      float:left;
      width:210px;
      height:36px;
      display:table;
    }
    .user-name-group {
      display:table-cell;
      width:inherit;
      vertical-align:middle;
      overflow:hidden;
    }
    .user-name{
      //float:left;
      display:inline-block;
      width:210px;
      height:18px;
      overflow:hidden;
      vertical-align:middle;
      font-family: $font-sans-semibold;
      font-size:$font-size-base;
      color:$brand-normaltext;
      //padding:0;
      //margin:0;
      letter-spacing: -1px;
      line-height: 19px;
      white-space: nowrap;
      text-overflow:ellipsis;

      &:hover{
        text-decoration: none;
      }
    }

    .user-group{
      //float:left;
      width:210px;
      vertical-align:middle;
      font-family: $font-family-sans;
      font-size:($font-size-base - 2);
      color:$brand-lightgray-text;
      padding:0;
      margin:0;
      margin-top:3px;
      white-space: nowrap;
      text-overflow:ellipsis;
      overflow: hidden;
    }
  }
  .profile-detail-btn{
    float:left;
    //width:275px;
    //margin-right: 21px;
    width:inherit;
    //padding:26px 21px 25px;
    //white-space: nowrap;
    //word-break:break-all;
    overflow: hidden;
    .user-detail{
      //width:inherit;
      overflow:hidden;
      text-overflow:ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      max-height:38px;
      padding:0 24px;
      margin:0 0 15px 0;
      font-family: $font-family-sans;
      font-size:($font-size-base - 1);
      color:$brand-text-gray;
      //word-wrap:break-word;
      word-break:break-all;
    }
    .follow-send-btn{
      //margin-top:15px;
      padding:16px 24px 25px;
      //text-align: center;
      border-top:1px solid $brand-border-gray;
      .toggle-btn{
        display:inline-block;
        vertical-align:middle;
        margin-right: 5px;
      }
      > button{
        display:inline-block;
        vertical-align: middle;
      }
    }
  }
}

.icon-profile-crown{
  //padding:0;
  margin:0 5px 0 0;
  //width:24px;
  //height:20px;
  font-size:inherit;
  vertical-align:middle;
  font-family:'FontAwesome';
  color:$brand-lightblue;
  &:before{
    content: '\f19d';
  }
}
.follow-group .follow-list h1 span.icon-profile-crown{
  font-size:inherit;
  font-family:'FontAwesome';
  color:$brand-lightblue;
}
.user-profile .icon-profile-crown{
  font-size:85%;
}


// top arrow triangle
.arrow-menu-top:after, .arrow-menu-top:before {
  bottom: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}
.arrow-menu-top:after {
  border-color: rgba(255, 255, 255, 0);
	border-bottom-color: $floatingUI-bg;
	border-width: 9px;
	margin-left: -9px;
}
.arrow-menu-top:before {
	border-color: rgba(213, 220, 226, 0);
	border-bottom-color: $brand-border-gray;
	border-width: 10px;
	margin-left: -10px;
}
