// main: ../main.scss

/*
* Component: modules
* ----------------------
*/
// main
.module-top-area{
  @include page-visual($break-visual-height, $break-visual-padding-top, $module-visual-img);
}

.simulation-wrap {
  margin:49px auto 16px;
  width:$content-width;
  > iframe {
    width:$content-width;
    height:880px;
  }
}

.simulation-need-login {
  width:$content-width;
  height:880px;
  padding-top:320px;
  background:url('#{$img-path}/module/capture_simulation.gif') no-repeat 0 0;
  text-align:center;
  color:$normal-color;
  >h2 {
    margin-bottom:14px;
    font-size:$font-size-h1;
  }
  >p {
    margin-bottom:43px;
    font-size:$font-size-large;
  }
}

// pressure drop
.pressure-left {
  float:left;
  color:$brand-normaltext;
}
.card-pressure {
  @include cards-h ($pressure-width, $pressure-padding, $pressure-padding);
  @include box-shadow(0 2px 0 rgba(0,0,0,.02));

  &.card-pressure-right {
    float:right;
    width:$pressure-width-right;
    color:$brand-normaltext;

    .input-base {
      padding:4px 13px 5px;
    }

    .pd-input-value {
      padding-right:40px;
    }
  }
}
.pressure-result {
  @include cards-color ($pressure-width, $pd-result-padding-vertical, $pd-result-padding-horizontal, $pd-background-color, $pd-border-color, $pd-font-color);
  margin-top:10px;
  color:$normal-color;
}

.pd-value {
  display:inline-block;
  text-align:center;
  > .pd-inout-text {
    font-size:($font-size-base - 1);
  }
}

.pd-inout-text {
  margin-bottom:7px;
  text-transform:uppercase;
}

.pd-output {
  font-size:$font-size-h3;
  font-family:$font-sans-semibold;
}

.number-psi {
  float:left;
  padding:0 10px 0 23px;
  background:url('#{$img-path}/module/icon-psi.png') no-repeat left center;
  font-size:$font-size-large;
  font-family:$font-sans-semibold;
}

.number-temperature {
  float:left;
  margin-top:1px;
  padding:2px 6px 3px 23px;
  background:$brand-dark-orange url('#{$img-path}/module/icon-temperature.png') no-repeat 7px center;
  font-size:($font-size-base - 1);
  color:$normal-color;
  font-family:$font-sans-semibold;
}

.pd-img {
  position:relative;
  height:611px;
  margin:14px auto;
  background: url('#{$img-path}/module/bg_pressure_drop.png') no-repeat center top;
}

.pd-Lv {
  position:absolute;
  top:110px;
  left:5px;
  height:400px;
  text-align:center;
  writing-mode:vertical-lr;
  -moz-writing-mode:vertical-lr;
  -webkit-writing-mode:vertical-lr;
  -ms-writing-mode:tb-lr;
}

.pd-Lv-text {
  padding:15px 0;
  background-color:$normal-color;
  font-size:($font-size-base - 1);
}

.pd-Lv-num {
  padding-top:10px;
  font-size:($font-size-base + 1);
  font-family:$font-sans-semibold;
}

.pd-Dv {
  position:absolute;
  top:316px;
  left:41px;
  width:220px;
  height:47px;
  color:$normal-color;
  background: url('#{$img-path}/module/bg-Dv.png') no-repeat 0 0;

  > .pd-Dv-text {
    display:inline-block;
    margin-top:3px;
    padding:0;
    background-color:transparent;
  }
  > .pd-Lv-num {
    display:block;
    padding:0;
  }
}

.pd-shape {
  position:absolute;
  top:294px;
  right:-15px;
  width:60px;
  text-align:center;
  color:$brand-lightblue;

  >.pd-Lv-num, >.pd-shape-text {
    font-size:($font-size-base + 2);
  }
}

.pd-result-title {
  margin-bottom:13px;
  padding-bottom:14px;
  text-align:center;
  font-size:$font-size-base;
  text-transform:uppercase;
  border-bottom:1px solid #80e3f3;
}

.pd-result-table {
  width:100%;
  th {
    font-weight:normal;
  }
  td {
    padding:4px 0 3px 0;
    text-align:right;
    font-size:($font-size-base - 1);
    span {
      position:relative;
      top:4px;
      padding-right:20px;
      font-size:$font-size-h3;
      font-family:$font-sans-semibold;
    }
  }
}

.pd-item-title {
  clear:left;
  margin:40px 0 20px 0;
  font-size:($font-size-base + 2);
  font-family:$font-sans-semibold;
  &:first-child {
    margin-top:0;
  }
}

.pd-input-wrap {
  position:relative;
  margin-bottom:10px;
  @extend .clearfix-block;

  .pd-input-name {
    float:left;
    width:142px;
    margin-top:10px;
    font-size:($font-size-base + 1);
  }
  .pd-input-value {
    float:right;
    width:510px;
    height:38px;
    font-size:($font-size-base + 2);
  }
  .pd-input-unit {
    position:absolute;
    top:8px;
    right:15px;
  }
}

.pd-input-compound {
  float:right;
  width:510px;

  .btn-add-data {
    float:right;
    margin-top:10px;
  }
}

.pd-input-table {
  width:100%;
  th,td {
    border:1px solid $brand-pattensblue;
    text-align:center;
    font-size:($font-size-base + 1);
  }
  th {
    padding:12px 0 10px 0;
    background-color:$download-box-color;
    font-weight:normal;

    &.text-formula {
      padding:12px 20px 10px 20px;
      background-color:$pd-input-th;
      color:$normal-color;
    }
  }
  td {
    padding:4px 3px;
    vertical-align:middle;
    input[type="text"] {
      width:98%;
      height:38px;
    }
  }
}

.pd-cs-table {
  th {
    color:$normal-color;
    background-color:$brand-normaltext;
    border-color:$brand-normaltext;
    &:first-child {
      background-color:$brand-lightblue;
      border-color:$brand-lightblue;
    }
  }

  td {
    height:90px;
    padding:0;

    &:first-child {
      background-color:$download-box-color;
    }
    img {
      height:90px;
      -ms-interpolation-mode: bicubic !important;
    }
    .css-radio-label {
      margin-right:0;
    }
  }
}

.pd-col1 {
  width:109px;
}
.pd-col2, .pd-col3 {
  width:200px;
}
.pd-col4 {
  width:30px;
}
.pd-col5 {
  width:92px;
}
.pd-col6 {
  width:120px;
}
.pd-col7 {
  width:272px;
}
.pd-col8 {
  width:230px;
}

.pd-add-feed {
  table-layout: fixed;
  width:539px;
  th:last-child, td:last-child {
    border:0;
    background-color:$normal-color;
  }

  td.td-compound {
    position:relative;

    .input-base {
      padding-left:30px;
    }

    .glyphicon {
      position:absolute;
      top:16px;
      left:10px;
      color:$brand-text-gray;
      border:0;
      background-color:transparent;
    }
  }

  .btn-article-close {
    position:static;
  }
}

.select-keyword {
  width:180px;
  height:38px;
  @include text-overflow();

  img {
    height:38px;
    -ms-interpolation-mode: bicubic !important;
  }
}

.select-shape {
  position:relative;
  float:left;
  margin-left:20px;
}

.radiogroup {
  .radio-shape {
    font-size:($font-size-base - 1);
  }
}

.radio-shape {
  width:184px;
  height:158px;
  padding-top:114px;
  border:1px solid $checkbox-border;
  border-radius:2px;
  text-align:center;
  color:$brand-text-gray;

  > img {
    position:absolute;
    top:20px;
    left:54px;
    display:block;
    margin:0 auto 0 auto;
    @include opacity(.3);
  }
}

.shape-wrap {
  @extend .clearfix-block;

  input[type=radio].css-radio:checked + .radio-shape {
     border-color:$brand-lightblue;
    > img {
      @include opacity(1);
    }
  }
}

.pd-btn-wrap {
  float:right;
  width:$pressure-width-right;
  margin-top:-4px;
  padding-left:7px;
  background-color:$btn-add-data;
  color:$normal-color;
  border-right:1px solid $panel-compound-border;
  border-bottom:1px solid $panel-compound-border;
  border-left:1px solid $panel-compound-border;
  border-bottom-right-radius:4px;
  border-bottom-left-radius:4px;

  button {
    height:68px;
    margin-left:40px;
    border:0;
    background-color:transparent;
    font-family:$font-sans-semibold;

    .glyphicon {
      padding-right:3px;
      font-size:12px;
    }
    &.btn-calculate {
      float:right;
      width:288px;
      margin-left:0;
      background-color:$brand-lightblue;
      font-size:($font-size-base + 2);
    }
  }
}

.pd-cs-modal {
  padding:50px 8px 46px 50px;
}
.pd-cs-wrap {
  position:relative;
  width:712px;
  font-size:($font-size-base + 1);

  button {
    position:absolute;
    top:7px;
    right:10px;
    font-size:$font-size-large;
    color:$brand-lightblue;
    border:0;
    background-color: transparent;
  }
}
.pd-cs-input {
  width:100%;
  height:38px;
  padding:4px 36px 5px 13px;
}
.pd-cs-result {
  margin-top:25px;
  color:$brand-normaltext;

  .btn-set {
    margin-right:40px;
    text-align:right;
  }

  .pd-cd-result-text {
    margin-right:40px;
  }
}

.pd-cs-table-wrap {
  height:243px;
  margin:23px 0 40px 0;
  overflow-y:auto;
}

.pd-cs-table-area {
  width:714px;
}

.pd-noresult {
  margin:20px 40px 0 0;
  img {
    width:160px;
  }
  > p {
    margin:20px 0;
    font-size:($font-size-base + 2);
  }
}

// source code
.source-wrap {
  min-height:700px;
  margin-bottom:-50px;
  padding:49px 0;
  background-color:$section-bg;
  overflow:hidden;
}

.source-inner {
  width:$content-width;
  margin:0 auto;
}

.source-info {
  margin-bottom:18px;
  font-size:($font-size-base - 1);
  font-family:$font-sans-semibold;
  color:$brand-normaltext;

  .link-howto {
    padding:0;
    background:transparent;
    border:0;
    color:$brand-normaltext;
    &:hover,
    &:focus,
    &:active {
      color:$brand-lightblue;
    }
    > .fa {
      padding-right:8px;
      font-size:($font-size-base + 4);
      vertical-align:bottom;
    }
  }

  .tooltip-inner {
    width:422px;
    padding:15px 12px 15px 28px;
    text-align:left;
  }
}

.source-list {
  > li {
    float:left;
    width:580px;
    margin:0 0 10px 10px;

    &:nth-child(2n+1) {
      margin-left:0;
    }
  }
}

.source-card {
  position:relative;
  @include cards ($source-box-width, $source-box-height, $source-box-height, $source-box-padding-vertical, $source-box-padding-horizontal);
  @include box-shadow($break-box-shadow-color);

  > a {
    &:hover,
    &:focus,
    &:active {
      .source-title {
        color:$brand-lightblue;
      }
    }
  }

  .article-btngroup {
    position:absolute;
    left:50px;
    right:50px;
    bottom:22px;
  }
}

.source-btngroup {
  i.icon-inbox {
    font-size:17px;
    margin-top:-3px;
  }
  .btn-article-function li {
    margin-right:18px;
  }
}

.source-title {
  font-size:($font-size-large + 2);
  font-family:$font-sans-semibold;
  color:$card-title-blue;
  line-height:28px;
}

.source-text {
  margin-top:12px;
  font-size:($font-size-base + 2);
  color:$brand-normaltext;
  line-height:22px;
}


// sub
.source-inner-sub {
  width:$dsc-subContent-width;
  margin:10px auto -40px;
  padding-top:49px;
}

.source-carousel-wrap {
  margin:-49px 0 50px 0;
}

.source-carousel {
  height:350px;

  .item {
    text-align:center;
    line-height:350px;

    img {
      display:inline;
      max-height:283px;
    }
  }
}

.source-left {
  position:absolute;
  top:45%;
  left:0;
}

.source-right {
  position:absolute;
  top:45%;
  right:0;
}

.source-control > .fa {
  color:$brand-text-gray;
  font-size:32px;
  @include opacity($tooltip-opacity);

  &:hover {
    @include opacity(1);
  }
}

.carousel-indicators-source {
  bottom: -20px;
  li{
    background-color: $brand-gray;
    width:  6px;
    height: 6px;
    margin: 0;
    &.active{
      background-color: $brand-lightblue;
      width:  6px;
      height: 6px;
    }
  }
}

.source-detail {
  margin:27px 0;
  color:$brand-normaltext;
}

.source-detail-title {
  margin-bottom:15px;
  font-size:($font-size-h2 + 2);
  font-family:$font-sans-semibold;
  color:$card-title-blue;
  text-align:center;
}

.source-detail-text {
  margin-bottom:51px;
  font-style:italic;
  line-height:24px;
  font-size:($font-size-base + 1);
  text-align:center;
}

.source-detail-description {
  margin:51px 0 28px 0;
  padding-bottom:35px;
  line-height:26px;
  font-size:($font-size-base + 2);
  border-bottom:1px solid $dsc-borderColor;
}

// paid
.btn-add-file {
  float:right;
  margin-bottom:15px;
}

.list-other-file {
  clear:right;
  box-sizing:border-box;

  > li {
    margin-bottom:15px;
    height:285px;
    @include clearfix;
    @extend %box-common;
    background-color:#fff;

    > .left-info {
      float:left;
      width:1070px;
      padding:50px 40px 36px 55px;
      box-sizing:border-box;

      .dropdown {
        float:right;
        width:74px;

      }
    }

    .right-down {
     float:right;
     display:table;
     width:98px;
     height:100%;
     box-sizing:border-box;
     text-align:center;

     > .download-box {
       display:table-cell;
       vertical-align:middle;
       color:$brand-download-ic;
       font-size:$font-size-h3;
       background-color:$download-box-color;

       &:hover {
         background-color:$brand-lightblue;
         color:#fff;
       }
     }
   }

   .btn-article-function li {
     margin:0 20px 20px 0;
   }

   .user-name {
     margin-top:5px;
    > a {
       color:$valid-input-color;
     }
   }
  }
}

.wrap-tit-other {
  @extend .clearfix-block;
}

.title-other-file {
  float:left;
  width:900px;
  height:33px;
  text-overflow:ellipsis;
  white-space:nowrap;
  overflow:hidden;
  font-size:($font-size-h4 + 2);
  color:$card-title-blue;
  font-family:$font-sans-semibold;

  > img {
    padding-right:10px;
  }
}

.desc-other-file {
  margin:10px 0;
  height:68px;
  @include overflow-line(3);
  color:$brand-normaltext;
  font-size:($font-size-base + 1);
}

.upload-other-file {
  .modal-header {
    margin-bottom:22px;
  }
  .modal-dialog {
    text-align:left;
  }

  .fileinput, .input-payment {
    font-size:($font-size-base + 2);
    &::-moz-placeholder { font-size:16px;}
    &:-ms-input-placeholder { font-size:16px;} // Internet Explorer 10+
    &::-webkit-input-placeholder  { font-size:16px;} // Safari and Chrome
  }

  .fileinput, .input-title {
    height:50px;
  }

  .input-title, .input-textarea {
    width:100%;
    font-size:($font-size-base + 2);
  }

  .input-title {
    display:block;
    margin:10px 0 25px 0;
  }

  .input-textarea {
    margin-bottom:25px;
  }

  .modal-footer  {
    text-align:right;
    padding-right:0;

    > button {
      margin-left:6px;
    }
  }
}

.txt-notice-upload {
  margin-bottom:30px;
  font-size:($font-size-base - 1);
}

.txt_point {
  float:right;
  margin-top:-3px;
}
