
// Buttons
$shadow: inset 0 1px 0 rgba(255,255,255,.15), 0 1px 1px rgba(0,0,0,.075);
@mixin btn-styles($btn-color: #555)
{
  @include gradient-vertical($start-color: $btn-color, $end-color: darken($btn-color, 12%));
  @include reset-filter(); // Disable gradients for IE9 because filter bleeds through rounded corners; see https://github.com/twbs/bootstrap/issues/10620
  background-repeat: repeat-x;
  border-color: darken($btn-color, 14%);

  &:hover,
  &:focus  {
    background-color: darken($btn-color, 12%);
    background-position: 0 -15px;
  }

  &:active,
  &.active {
    background-color: darken($btn-color, 12%);
    border-color: darken($btn-color, 14%);
  }

  &.disabled,
  &:disabled,
  &[disabled] {
    background-color: darken($btn-color, 12%);
    background-image: none;
  }
}

// Common styles
.btn {
  &:active,
  &.active {
    background-image: none;
  }
}

// Dropdowns
.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus {
  @include gradient-vertical($start-color: $dropdown-link-hover-bg, $end-color: darken($dropdown-link-hover-bg, 5%));
  background-color: darken($dropdown-link-hover-bg, 5%);
}
.dropdown-menu > .active > a,
.dropdown-menu > .active > a:hover,
.dropdown-menu > .active > a:focus {
  @include gradient-vertical($start-color: $dropdown-link-active-bg, $end-color: darken($dropdown-link-active-bg, 5%));
  background-color: darken($dropdown-link-active-bg, 5%);
}

.validicon {
  float:right;
  margin: -34px 0;
  //display: block;
  width: 34px;
  color: $list-group-border;
  font-size:18px;
  @include clearfix;
}

// Mixin for generating new styles
@mixin alert-styles($color) {
  @include gradient-vertical($start-color: $color, $end-color: darken($color, 7.5%));
  border-color: darken($color, 15%);
}

/* new kka Common styles */
.text-alert {
   padding:7px 0;
   font-size:12px;
   text-align: left;
   display:inline-block;
   width:100%;
}

.text-alert-danger     {
  color:#e62f17;
}

// Progress bars
.progress { @include gradient-vertical($start-color: darken($progress-bg, 4%), $end-color: $progress-bg) }

// Mixin for generating new styles
@mixin progress-bar-styles($color) { @include gradient-vertical($start-color: $color, $end-color: darken($color, 10%)); }
// Apply the mixin to the progress bars


//
// Panels
// --------------------------------------------------

// Common styles
.panel { @include box-shadow(0 1px 2px rgba(0,0,0,.05)); }

// Mixin for generating new styles
@mixin panel-heading-styles($color) { @include gradient-vertical($start-color: $color, $end-color: darken($color, 5%));}

