// main: ../main.scss

/*
* Component: breaktime
* ----------------------
*/
.break-top-area{
  @include page-visual($break-visual-height, $break-visual-padding-top, $break-visual-img);
}

.break-main-content{
  @include clearfix();
  width: $content-width;
  margin: 0 auto;
  margin-top: -40px;
  > .tab-set1{
    line-height: ($font-size-base + 5);
  }
}

.break-dropdown{
  padding-top: 73px;
  padding-bottom: 17px;
  .dropdown-menu{
    left: -60px;
  }
  .dsc-dropmenu-Large{
    margin-left: 0;
  }
  .chem-arrow-menu{
    left: 0;
  }
}

.rec-wrap{
  margin-top: 115px;
}
.break-card-collapse{
  @include cards-h($content-width, 0, 0);
  min-height: $cards-height-sm ;
  position:relative;
  margin-bottom:10px;
  .break-img-box{
    @extend .card-img-box;
    float: right;
    width: 333px;
    height: 283px;
    margin-left: 32px;
    //background-color: $gray-base;
    line-height: 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .tec-date{
    margin-left: 55px;
    margin-top: 42px;
    &.rec-category{
      font-weight: bold;
      color: $brand-text-gray;
    }
  }
  .break-content{
    padding: 0 0 40px 0;
    margin: 15px 55px 45px 55px;
    .break-title{
      @extend .card-title, .text-wrap-word;
      font-size: $break-content-fontsize;
      font-weight: bold;
      color: $font-tabpanel-titlecolor;
      margin-bottom: 12px;
    }
    .break-text{
      @extend .text-wrap-word;
      font-size: 16px;
      padding-right:333px;
      color:$brand-normaltext;
    }
    > a {
      &:hover,
      &:focus,
      &:active  {
        .break-title {
          color:$brand-lightblue;
        }
      }
    }
  }
  .see-more{
    display: block;
    position:absolute;
    left: 54px;
    bottom: 24px;
    color:$brand-btn-ligtgray;
    &:hover {
      color:$brand-normaltext;
    }
  }
}
.break-card{
  @include cards($content-width, 224px, 224px, 0, 0);
  position:relative;
  @include box-shadow($break-box-shadow-color);
  padding-bottom: 35px;
  margin-bottom: 9px;
  color: $brand-normaltext;
  .break-img-box{
    @extend .card-img-box;
    position: relative;
    float: right;
    width: 394px;
    height: 222px;
    margin-left: 42px;
    //background-color: $gray-base;
    line-height: 0;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .tec-date{
    margin-left: 55px;
    margin-top: 35px;
    &.rec-category{
      font-weight: bold;
      color: $brand-text-gray;
    }
  }
  .break-content{
    padding: 0;
    margin: 14px 55px;
    .break-title{
      @extend .card-title;
      font-size: $break-content-fontsize;
      font-weight: bold;
      color: $font-tabpanel-titlecolor;
      margin-bottom: 6px;
      @include text-overflow();
    }
    .break-text{
      @extend .text-wrap-word;
      font-size: 16px;
      color:$brand-normaltext;
      @include text-overflow-multi(46px, 2);
    }
    .see-more{
      position:absolute;
      left: 54px;
      bottom: 10px;
      color:$brand-text-gray;
      &:hover {
        color:$brand-normaltext;
      }
    }
    > a {
      &:hover,
      &:focus,
      &:active {
        .break-title {
          color:$brand-lightblue;
        }
      }
    }
  }
}

.title-recreation {
  @include clearfix();
  padding:68px 0 17px 0;
}
