// main: ../main.scss

/*
* Component: error page and etc
* ----------------------
*/
// 404
.site-msg-body {
  height:auto;
}
.site-msg {
  width:$site-msg-width;
  margin:7% auto 0 auto;
  padding-bottom:7%;
  background:url('#{$img-path}/error/img-error.png') no-repeat center right;
  color:$brand-normaltext;
  @extend .clearfix-block;

  > h1 {
    margin:0 0 54px -2px;
    font-size:$site-msg-h1;
    font-family:$font-montserrat-semibold;
  }
}

.need-signin {
  width:700px;
  background:none;
  > h1 {
    font-size:80px;
  }
}

.site-msg-description {
  margin-bottom:49px;
  line-height:52px;
  font-size:$site-msg-description;
}

.error-msg {
  margin-bottom:47px;
  font-size:$error-msg-font-size;
  > span {
    font-family:$font-sans-semibold;
  }
}

.other-link-title {
  margin-bottom:10px;
  font-size:$out-link-font-size;
  font-family:$font-sans-semibold;
}

.other-link-list {
  font-size:$out-link-font-size;
  > li {
    margin-bottom:7px;
    > a {
      color:$brand-normaltext;
      &:hover {
        color:$brand-lightblue;
      }
    }
  }
}
