// main: ../main.scss

.signup-form-group {
  max-width: 550px;
  padding: 0 40px;
  margin: 40px auto 0;
  text-align: center;
  .form-group{
    margin-bottom:12px;
  }
}

//   
.line-through{
  overflow: hidden;
  text-align: center;
  &::before,
  &::after{
    background-color: $hr-signin;
    content: "";
    display: inline-block;
    height: 1px;
    position: relative;
    vertical-align: middle;
    width: 50%;
  }
  &::before{
    right: 0.5em;
    margin-left: -50%;
  }
  &::after{
    left: 0.5em;
    margin-right: -50%;
  }
}
.social-or-tit,.social-links-tit{
  color: $brand-normaltext;
}
.social-auth-links2 {
  margin: 2px 0 0 0;
  padding:20px 0;
  color: $brand-normaltext;
  .btn-social {
    width: 54px;
    height: 50px;
    margin-left:8px;
    text-align: center;
  }
}
.sign-en-icon{
  width: 18px;
  height: 18px;
  margin-top: -36px;
  position: absolute;
  right: 10px;
  font-size: 18px;
  color: $btn-showhide-bg;
  font-weight: bold;
}

.social-auth-links {
  margin: 37px 0 0 0;
  padding:10px 0;
  border-bottom:1px solid $btn-brand-gray-light-h;
  height:155px;
  //display:block;
  .hrSignup {
    padding: 0;
    border: none;
    color: $brand-normaltext;
    text-align: center;
    border-top: 1px solid $btn-brand-gray-light-h;
    &:after {
      content: "Sign up with";
      display: inline-block;
      position: relative;
      overflow: visible;
      top: -0.8em;
      padding: 0 0.5em;
      background: $section-bg;
    }
  }

  .btn-social {
    position: relative;
    padding-left: 44px;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width:48%;

    >:first-child {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 30px;
      line-height: 32px;
      font-size: 1.6em;
      text-align: center;
      margin:2px;
    }
  }

  .btn-facebook,
  .btn-google,
  .btn-Weibo,
  .btn-Wechat {
    color: $normal-color;
    &:hover,
    &:focus {
      opacity: 0.8;
      filter: alpha(opacity=80); /* For IE8 and earlier */
    }
  }

  .btn-facebook {
    background-color: $btn-facebook-bg;
    float:left;
    margin-bottom:10px;
    >:first-child {
      border-right: 1px solid $btn-facebook-border;
    }
  }
  .btn-google {
    background-color: $btn-google-bg;
    float:right;
    margin-bottom:10px;
    >:first-child {
      border-right: 1px solid $btn-google-border;
    }
  }
  .btn-Weibo {
    background-color: $btn-Weibo-bg;
    float:left;
    >:first-child {
      border-right: 1px solid $btn-Weibo-border;
    }
  }
  .btn-Wechat {
    background-color: $btn-Wechat-bg;
    float:right;
    >:first-child {
      border-right: 1px solid $btn-Wechat-bg;
    }
  }
}

.socia-text-link,
.signin-text{
  font-size: ($font-size-base + 2);
  color:$brand-normaltext;
}

.signin-text{
  padding:0;
}

.socia-text-link{
  padding:35px 0 0 0;
}

.signup-heading {
  font-size: $font-size-h1;
  text-transform: uppercase;
  color:$brand-headertext;
  margin-bottom: 10px;
}

.change-password-heading{
  font-family: $font-sans-semibold;
  font-size:($font-size-h2 + 2);
  color:$brand-normaltext;
  margin-bottom:42px;
}

.text-discription-large {
  font-size: ($font-size-base + 10);
  letter-spacing: 0.25px;
  color:$brand-headertext;
}

//new
.signup-question-group {
    max-width: 795px;
    padding: 0 0;
    margin:20px auto 0;
    text-align: center;

  .checkbox:nth-of-type(5n+0) {
    margin-right:0;
  }
  .checkbox{
    width:125px;
    padding:0;
    margin-top:50px;
    margin-bottom:0;
    margin-right:36px;
    position: relative;
    display:inline-block;
  }
  @include clearfix();
  .btn-area{
    button.col-md-offset-3{
      margin-left:151px;
    }
    button.col-md-7{
      float:left;
      width:460px;
    }
  }
}

.ckbox {
  display:none;
}
.ckb-label {
  display: inline-block;
  vertical-align: middle;
  margin:0 0 5px 0;
  cursor: pointer;
  width:$checkb-custom-bgsizew ;
  height:$checkb-custom-bgsizeh;
  background-size: $checkb-custom-bgsizew ($checkb-custom-bgsizeh - 58);//
  background-repeat: no-repeat;
}

.answer-text {
  width:100%;
  padding:5px;
  //  white-space: pre-line;
  position:absolute;
  top:($checkb-custom-bgsizeh - 58);
  left:0;
  bottom:0;
  font-family: $font-family-base;
  font-size:($font-size-base + 2);
  color:$brand-normaltext;
  font-weight: 600;
  line-height: ($font-size-base + 6);
  word-wrap: break-word;
  word-break:normal;
  white-space: pre-wrap; /* CSS3*/
}

// other     
.insert-text{
  position:absolute;
  top:($checkb-custom-bgsizeh - 29);
  bottom:0;
  width:$checkb-custom-bgsizew;
  word-wrap: break-word;
  line-height: 17px;
}

.btn-area {
  @include clearfix();
  margin-top:60px;
}

.or-tit-margin{
  margin-bottom: 38px;
}
//sign up --modal form group
.signin-form-group{
  width:$modal-inner-formwidth;
  margin:0 auto;
  border:0 solid red;
  .form-group{
    margin-bottom: 5px;
  }
  .margin-bottom-none{
    position: relative;
    .toggle-btn{
      label{
        &:before,
        &:after{
          border:0;
        }
      }
    }
  }
  .btn-showhide{
    position: absolute;
    right: 35px;
    top: 10px;
  }
  .checkbox + .checkbox {
    margin-left: 30px;
  }
  .btn-brand-blue{
    @include button-size(($padding-large-vertical + 4), $padding-large-horizontal, ($font-size-large - 2), $line-height-large, $border-radius-base);
    height: inherit;
  }
  hr{
    border-top: 1px solid $hr-signin;
    margin: 40px 0 25px 0;
  }
}

.fotgot-text{
  line-height: $line-height-computed;
  margin-top: 20px;
}

// signin Modal new style, parent box setting
.signin-container{
  .modal-content{
    padding:5px 20px 54px 20px;  //padding:70px 78px 54px 78px;
    .signin-notice-box{
      background-color:$modal-content-bg;
      padding:0 0 30px 0;
      .write-notice{
        padding:15px 17px;
        margin-bottom:20px;
      }
      .notice-first-text{
        margin-bottom:7px;
        font-size:$font-size-base;
      }
    }
    .modal-content-padding{
      padding: 0 58px;
    }
    .close-btn-reset{
      .close{
        margin-top:-50px;
        margin-right:-20px;
      }
    }
  }

  .box-yellow-box{
    background-color: $notice-yellowbox-bg-color;
    border:1px solid $notice-yellowbox-border-color;
    border-radius:$border-radius-base;
    color:$notice-yellowbox-text-color;
  }
}
