// main: ../main.scss
/*
* Component: helper
* ----------------------
*/

.clearfix-block {
  &:before,
  &:after {
    content: " "; // 1
    display: block; // 2
  }
  &:after {
    clear: both;
  }
}

%box-common {
  border: solid 1px $article-border-color;
  border-radius: $border-radius-base;
  background: $article-box-bgcolor;
}

%box-bottom {
  border: solid 1px $article-border-color;
  border-top:0;
  border-radius: 0 0 $border-radius-base $border-radius-base;
  background: $article-box-bgcolor;
}

@mixin overflow-line($text-rows) {
  display: -webkit-box;
  -webkit-line-clamp: $text-rows;
  overflow:hidden;
  text-overflow:ellipsis;
  -webkit-box-orient: vertical;
}
