// main: ../../main.scss
// cards

@mixin cards ($width, $min-height, $height, $padding-vertical, $padding-horizontal) {
  width: $width;
  min-height: $min-height;
  height: $height;
  padding : $padding-vertical $padding-horizontal;
  overflow: hidden;
  @extend %box-common;
}
@mixin cards-h ($width, $padding-vertical, $padding-horizontal) {
  width: $width;
  padding : $padding-vertical $padding-horizontal;
  overflow: hidden;
  @extend %box-common;
}

@mixin cards-color ($width, $padding-vertical, $padding-horizontal, $background-color, $border-color, $font-color) {
  width: $width;
  padding : $padding-vertical $padding-horizontal;
  background-color:$background-color;
  color:$font-color;
  overflow: hidden;
  @extend %box-common;
  border-color:$border-color;
}
